<template>
  <navigation-wrapper :title="$t('home.menu.setting')">
    <div class="l-navigation__content has-padding">
      <ul>
        <li class="c-info-item">
          <div class="c-info-item__label">{{ $t('setting.voice') }}</div>
          <div class="c-info-item__switch o-switch t-adpat-switch" :class="{ 'is-on': voice }" @click="toggleVoice"></div>
        </li>
        <li class="c-info-item">
          <div class="c-info-item__label">{{ $t('setting.notice') }}</div>
          <div class="c-info-item__switch o-switch t-adpat-switch" :class="{ 'is-on': notice }" @click="toggleNotice"></div>
        </li>
        <router-link tag="li" class="c-info-item" :to="{ name: 'modify-language' }">
          <div class="c-info-item__label">{{ $t('setting.language') }}</div>
          <div class="c-info-item__value u-more">{{ langVal }}</div>
        </router-link>
      </ul>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  name: 'setting',
  data () {
    return {
      voice: false,
      lang: null
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    langVal () {
      return this.lang ? this.lang.text : ''
    },
    notice () {
      return this.user.isReceiveNotify === 1
    },
    noticeUrl () {
      return this.isStaff ? 'staff/notify/receive' : 'parent/notify/receive'
    }
  },
  methods: {
    ...mapActions(['updateUserInfo']),
    toggleVoice () {
      this.voice = !this.voice
    },
    toggleNotice () {
      if (!this.$noticeHttpPost) {
        this.$noticeHttpPost = this.$http.createPost(this.noticeUrl, {
          opts: {
            lock: true
          },
          success: (res, data) => {
            this.updateUserInfo({
              isReceiveNotify: data.isReceiveNotify
            })
          }
        })
      }
      this.$noticeHttpPost.request({
        token: this.user.token,
        isReceiveNotify: this.notice ? 0 : 1
      })
    }
  },
  created () {
    let locale = this.$lang.key
    this.lang = this.$langs.find(lang => lang.key === locale)
  },
  beforeDestroy () {
    if (this.$noticeHttpPost) {
      this.$noticeHttpPost.cancel()
    }
  }
}
</script>
