import Vue from 'vue'
import Vuex from 'vuex'
import * as Types from './mutation-types'
import http from '@/utils/http.js'
import { LoginMode, StaffLevel } from '@/enum/common.js'

Vue.use(Vuex)

let userInfo = window.localStorage.getItem('smart_campus__user')
if (userInfo) {
  userInfo = JSON.parse(userInfo)
}

const SET_RUNTIME_MODE = 'SET_RUNTIME_MODE'

const CLEAR = 'CLEAR'

const UPDATE_USER = 'UPDATE_USER'

const SAVE_USER = 'SAVE_USER'

const UPDATE_RELATED_FIGURES = 'UPDATE_RELATED_FIGURES'

const UPDATE_RELATED_FIGURE = 'UPDATE_RELATED_FIGURE'

const TRANSFORM_NAME = 'TRANSFORM_NAME'

let Cache = {}
function getPost (url, props) {
  if (!Cache[url]) {
    Cache[url] = http.createPost(url, props)
  }
  return Cache[url]
}

function cancelPost () {
  for (let key in Cache) {
    Cache[key].cancel()
  }
  Cache = {}
}

const state = {
  runtimmeMode: 'normal',
  user: userInfo,
  figures: []
}

const getters = {
  isLogined (state) {
    return !!state.user
  },
  mode (state, getters) {
    if (getters.isLogined) {
      return state.user.mode
    }
    return LoginMode.UNKNOWN
  },
  isParent (state, getters) {
    return getters.mode === LoginMode.PARENT
  },
  isStaff (state, getters) {
    return getters.mode === LoginMode.STAFF
  },
  isAdmin (state, getters) {
    if (getters.isStaff) {
      switch (state.user.roles) {
        case StaffLevel.HEADMASTER:
        case StaffLevel.HEADMASTER_MIDDLE:
        case StaffLevel.HEADMASTER_LOWER:
        case StaffLevel.DIRECTOR:
          return true
        default:
          return false
      }
    }
    return false
  },
  isHeadTeacher (state, getters) {
    if (getters.isStaff) {
      return state.user.roles === StaffLevel.HEADTEACHER
    }
    return false
  },
  getFigure: (state, getters) => id => {
    switch (getters.mode) {
      case LoginMode.PARENT:
      case LoginMode.STAFF:
        return state.figures.find(figure => figure.id === id)
      default:
        return null
    }
  },
  phoneNumber (state, getters) {
    if (getters.isLogined && state.user.areaCode && state.user.mobilePhone) {
      if (state.user.areaCode === '852') {
        return state.user.mobilePhone
      }
      return state.user.areaCode + state.user.mobilePhone
    }
    return ''
  }
}

function copy (target, source) {
  if (target && source) {
    for (let key in source) {
      Vue.set(target, key, source[key])
    }
  }
}

const mutations = {
  [Types.LOGIN] (state, payload) {
    state.user = payload
  },
  [Types.LOGOUT] (state) {
    state.user = null
    window.localStorage.removeItem('smart_campus__user')
  },
  [CLEAR] (state) {
    state.figures = []
    state.runtimmeMode = 'normal'
    cancelPost()
  },
  [UPDATE_RELATED_FIGURES] (state, payload) {
    state.figures = payload
  },
  [SET_RUNTIME_MODE] (state, payload) {
    state.runtimmeMode = payload
  },
  [UPDATE_USER] (state, payload) {
    copy(state.user, payload)
  },
  [SAVE_USER] (state) {
    if (state.user) {
      window.localStorage.setItem('smart_campus__user', JSON.stringify(state.user))
    }
  },
  [UPDATE_RELATED_FIGURE] (state, payload) {
    copy(state.figures.find(figure => figure.id === payload.id), payload.info)
  },
  [TRANSFORM_NAME] (state) {
    let name
    switch (state.user.relationship) {
      case 1:
        name = `Mr.${ state.user.lastName }`
        break
      case 2:
        name = `Ms.${ state.user.lastName }`
        break
      default:
        name = state.user.firstName || state.user.userName
        break
    }
    Vue.set(state.user, 'name', name)
  }
}

const actions = {
  [Types.LOGIN] ({ commit, dispatch }, payload) {
    commit(CLEAR)
    commit(Types.LOGIN, payload)
    commit(TRANSFORM_NAME, payload)
    dispatch(UPDATE_RELATED_FIGURES)
  },
  [Types.LOGOUT] ({ commit }) {
    commit(CLEAR)
    commit(Types.LOGOUT)
  },
  [UPDATE_USER] ({ commit }, payload) {
    commit(UPDATE_USER, payload)
    commit(TRANSFORM_NAME)
    commit(SAVE_USER)
  },
  [UPDATE_RELATED_FIGURES] ({ state, getters, commit }) {
    if (getters.isLogined) {
      switch (state.user.mode) {
        case 'parent':
          getPost('parent/children', {
            data: {
              token: state.user.token
            },
            opts: {
              retry: true
            },
            success: res => commit(UPDATE_RELATED_FIGURES, res.data.map(figure => {
              figure.name = `${ figure.firstName } ${ figure.lastName }`
              return figure
            }))
          }).request()
          break
        case 'staff':
          commit(UPDATE_RELATED_FIGURES, [state.user])
          break
        default:
          break
      }
    }
  },
  setRuntimeMode ({ commit },  payload) {
    commit(SET_RUNTIME_MODE, payload)
  },
  updateUserInfo ({ state, getters, commit }, payload) {
    if (getters.isLogined) {
      commit(UPDATE_USER, payload)
      if (state.user.hasPasswordSetted === 1) {
        commit(SAVE_USER)
      }
    }
  },
  updateFigureInfo ({ commit }, payload) {
    commit(UPDATE_RELATED_FIGURE, payload)
  },
  saveUser ({ commit }) {
    if (getters.isLogined) {
      if (state.user.hasPasswordSetted === 0) {
        commit(UPDATE_USER, {
          hasPasswordSetted: 1
        })
      }
      commit(SAVE_USER)
    }
  }
}

let Store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  modules: { }
})

export default Store

export function initialize () {
  return new Promise(resolve => {
    if (Store.getters.isLogined) {
      let url
      switch (Store.state.user.mode) {
        case 'parent':
          url = 'parent/info'
          break
        case 'staff':
          url = 'staff/info'
          break
        default:
          resolve()
          return
      }
      http.createPost(url, {
        opts: {
          ignore: true,
          prevent: true
        },
        success: res => {
          Store.dispatch(UPDATE_USER, res.data)
          Store.dispatch(UPDATE_RELATED_FIGURES)
        },
        fail: () => {
          Store.dispatch(Types.LOGOUT)
        },
        complet: () => {
          resolve()
        }
      }).request({
        token: Store.state.user.token
      })
    } else {
      resolve()
    }
  })
}
