<template>
  <div class="c-form-field__button" :class="{ 'is-active': isShowPicker }" @click="showPicker">
    {{ selectedAreaCode }}
    <picker v-if="isShowPicker" :values="regins" :index="pickerIndex" :title="$t('login.picker')" @change="pick" @cancel="cancel" @confirm="confirm"></picker>
  </div>
</template>

<script>
import picker from '@/components/Picker.vue'

let fetched = []

export default {
  name: 'area-code',
  props: ['areaCode'],
  components: {
    picker
  },
  data () {
    return {
      isShowPicker: false,
      pickerIndex: 0,
      index: 0,
      areas: fetched
    }
  },
  computed: {
    selectedAreaCode () {
      return this.areas.length > 0 ? `+${ this.areas[this.index].areaCode }` : ''
    },
    regins ()  {
      return this.areas.map(val => `${ val.areaName }(+${ val.areaCode })`)
    }
  },
  methods: {
    showPicker () {
      if (this.areas.length > 0) {
        if (!this.isShowPicker) {
          this.isShowPicker = true
        }
      } else {
        this.$httpPost.request()
      }
    },
    pick (index) {
      this.pickerIndex = index
    },
    cancel () {
      this.pickerIndex = this.index
      this.isShowPicker = false
    },
    confirm () {
      this.index = this.pickerIndex
      this.notify()
      this.isShowPicker = false
    },
    notify () {
      this.$emit('change', this.areas[this.index].areaCode)
    },
    checkDefaultArea (areaCode) {
      if (areaCode) {
        let index = this.areas.findIndex(area => area.areaCode === areaCode)
        if (index >= 0) {
          this.index = index
        } else {
          this.index = 0
        }
      } else {
        this.index = 0
      }
      this.pickerIndex = this.index
      this.notify()
    },
    updateAreaCode (areas) {
      if (areas.length > 0) {
        this.areas = areas
        this.checkDefaultArea()
      }
    }
  },
  watch: {
    areaCode () {
      if (this.areas.length > 0) {
        this.checkDefaultArea(this.areaCode)
      }
    }
  },
  created () {
    this.$httpPost = this.$http.createPost('area/loadAll', {
      success: res => {
        fetched = res.data
        if (this.areas.length === 0) {
          this.updateAreaCode(res.data)
        }
      }
    })
    this.$httpPost.request()
    if (this.areas.length > 0) {
      this.checkDefaultArea(this.areaCode)
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
