<template>
  <div class="c-pop-alert">
    <div class="c-pop-alert__wrapper">
      <div class="c-pop-alert__title">{{ title }}</div>
      <div class="c-pop-alert__tip">{{ tip }}</div>
      <div class="c-pop-alert__button" @click="confirm">{{ button }}</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'pop-alert',
  props: ['title', 'tip', 'button'],
  methods: {
    confirm () {
      this.$emit('confirm')
    }
  }
}
</script>

<style lang="scss">
.c-pop-alert {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(#333, 0.8);
  z-index: 999;
  &__wrapper {
    position: absolute;
    top: 150px;
    left: 50%;
    width: 76%;
    border-radius: 6px;
    background-color: #fff;
    transform: translate(-50%, 0);
  }
  &__title {
    padding: 30px 0 15px;
    line-height: 1;
    color: #333;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
  }
  &__tip {
    position: relative;
    padding: 0 15px 15px;
    line-height: 1.5;
    color: #7f7f7f;
    font-size: 14px;
    @include border-bottom-1px(#7f7f7f);
  }
  &__button {
    line-height: 50px;
    color: #65718a;
    font-size: 18px;
    text-align: center;
  }
}
</style>
