let lockId = 1

const STATE = {
  RETRY: 0,
  CYCLE: 1,
  WAIT: 100,
  REQUEST: 101,
  COMPELETED: 102,
  SLEEP: 103,
  DESTROY: 999
}

class SingleHttpPost {

  constructor (url, props = {}) {
    this.url = url
    this.state = STATE.WAIT
    this.cancelSource = null
    this.timer = {}
    let { data = null, opts = {}, success, fail, complet } = props
    this.data = data
    this.opts = Object.assign({ cancel: true, retry: false, delay: 3000, sleep: false, lock: false, prevent: false, cycle: 0 }, opts)
    if (this.opts.cycle > 0) {
      this.opts.retry = opts.retry === false ? false : true
    }
    this.success = success
    this.fail = fail
    this.complet = complet
  }

  isWait () {
    return this.state <= STATE.WAIT
  }

  isRequesting () {
    return this.state === STATE.REQUEST
  }

  isSleep () {
    return this.state === STATE.SLEEP
  }

  _request () {
    this.state = STATE.REQUEST
    if (this.opts.lock) {
      this.opts.lockId = lockId++
    }
    let opts = Object.assign({}, this.opts)
    if (this.opts.cancel) {
      this.cancelSource = http.cancelSource()
      opts.cancelToken = this.cancelSource.token
    }
    http.post(this.url, this.data, opts).then(res => {
      if (this.state === STATE.DESTROY) {
        return
      }
      if (this.opts.sleep) {
        this.state = STATE.SLEEP
      } else if (this.opts.cycle > 0) {
        this.state = STATE.CYCLE
        this.timer = setTimeout(() => this._request(), this.opts.cycle)
      }  {
        this.state = STATE.WAIT
      }
      this.success && this.success(res, this.data)
    }, error => {
      if (this.state === STATE.DESTROY) {
        return
      }
      if (this.opts.retry && error.code < 0) {
        this.state = STATE.RETRY
        this.timer = setTimeout(() => this._request(), this.opts.delay)
      } else {
        this.state = STATE.WAIT
      }
      this.state = STATE.WAIT
      if ((!this.fail || !this.fail(error)) && !this.opts.prevent) {
        EventBus.$emit(Event.TOAST, {
          code: error.code,
          message: error.message
        })
      }
    }).then(() => {
      if (this.state === STATE.DESTROY) {
        return
      }
      this.cancelSource = null
      this.complet && this.complet()
    })
  }

  _clearTimer () {
    clearTimeout(this.timer)
  }

  request (data) {
    if (this.isWait()) {
      if (data) {
        this.data = data
      }
      this._clearTimer()
      this._request()
    }
    return this
  }

  awaken () {
    if (this.isSleep()) {
      this.state = STATE.WAIT
    }
    return this
  }

  reset () {
    this._clearTimer()
    this.state = STATE.WAIT
  }

  destroy (reason = 'destroy') {
    this.state = STATE.DESTROY
    if (this.isRequesting()) {
      if (this.opts.lock) {
        EventBus.$emit(Event.REQUEST_FINISHED, this.opts.lockId)
      }
      if (this.opts.cancel) {
        this.cancelSource.cancel(reason)
        this.cancelSource = null
      }
    }
    this._clearTimer()
  }
}

class PostRequest {

  constructor (url, props = {}) {
    this.url = url
    this.props = props
    this.httpPost = null
  }

  _getHttpPost () {
    if (!this.httpPost) {
      this.httpPost = new SingleHttpPost(this.url, this.props)
    }
    return this.httpPost
  }

  _destoryHttpPost (reason) {
    if (this.isRequesting()) {
      this.httpPost.destroy(reason)
      this.httpPost = null
    } else if (this.httpPost) {
      this.httpPost.reset()
    }
  }

  isWait () {
    return this.httpPost === null || this.httpPost.isWait()
  }

  isRequesting () {
    return this.httpPost !== null && this.httpPost.isRequesting()
  }

  isSleep () {
    return this.httpPost !== null && this.httpPost.isSleep()
  }

  request (data) {
    this._getHttpPost().request(data)
    return this
  }

  force (data, opts) {
    this._destoryHttpPost('force')
    this.setOpts(opts)
    this.request(data)
    return this
  }

  setUrl (url) {
    this._destoryHttpPost('url changed')
    this.url = url
    if (this.httpPost) {
      this.httpPost.url = url
    }
    return this
  }

  setOpts (opts) {
    if (this.props.opts) {
      Object.assign(this.props.opts, opts)
    } else {
      this.props.opts = opts
    }
    if (this.httpPost) {
      Object.assign(this.httpPost.opts, opts)
    }
    return this
  }

  awaken () {
    if (this.isSleep()) {
      this.httpPost.awaken()
    }
    return this
  }

  cancel () {
    this._destoryHttpPost()
    return this
  }

}

function analysisError ({ code, message }) {
  log(`server error ${ code }: ${ message }`)
  if (code === RetCode.ERROR_TOKEN_INVALID) {
    if (Store.getters.isLogined) {
      let mode = Store.getters.mode
      Store.dispatch(Types.LOGOUT)
      EventBus.$emit(Event.TOKEN_INVALID, mode)
    }
    return true
  }
  return false
}

const http = {
  /**
   * dataqs.stringify
   * opts
   *   - origin [Boolean] qs.stringifyfalse
   *   - lock [Boolean] loadingfalse
   *   - lockId [Number] locktrue
   *   - ignore [Boolean] 
   */
  post (url, data = {}, opts = {}) {
    if (opts.lock) {
      opts.lockId = opts.lockId || lockId++
      EventBus.$emit(Event.REQUESTING, opts.lockId)
    }
    return new Promise((resolve, reject) => {
      axios.post(url, opts.origin ? data : qs.stringify(data), opts).then(res => {
        opts.lock && EventBus.$emit(Event.REQUEST_FINISHED, opts.lockId)
        let code = res.data.code
        if (code === RetCode.SUCCESS) {
          resolve(res.data)
        } else {
          if (opts.ignore || !analysisError(res.data)) {
            reject(res.data)
          }
        }
      }, error => {
        opts.lock && EventBus.$emit(Event.REQUEST_FINISHED, opts.lockId)
        let code
        let message
        if (error.response) {
          // The request was made and the server responded with a status code
          // that falls out of the range of 2xx
          code = RetCode.ERROR_RESPONSE
          message = `Server error ${ error.response.status }`
        } else if (error.request) {
          // The request was made but no response was received
          // `error.request` is an instance of XMLHttpRequest in the browser and an instance of http.ClientRequest in node.js
          code = RetCode.ERROR_REQUEST
          message = error.message
        } else {
          // Something happened in setting up the request that triggered an Error
          code = RetCode.ERROR_UNEXPECTED
          message = error.message || 'unknown'
        }
        log(`${ code }: ${ message }`)
        reject({
          code,
          message
        })
      })
    })
  },
  cancelSource () {
    return axios.CancelToken.source()
  },
  /**
   *  props
   *    - success [Function] resdatarespostdatapost
   *    - fail [Function] error{ code: xxx, message: xxx }true
   *    - complet [Function] post
   *    - opts [Object | null]
   *      - cancel [Boolean] false
   *      - retry [Boolean] false
   *      - delay [Number] retryms3000
   *      - sleep [Boolean] awakenfalse
   *      - lock [Boolean] loadingfalse
   *      - cycle [Number] 0ms0retry
   *      - origin [Boolean] qs.stringifyfalse
   *      - ignore [Boolean] false
   *      - prevent [Boolean] false
   *      code >= 0
   *    - data [Object | null] post
   */
  createPost (url, props = {}) {
    return new PostRequest(url, props)
  },
  uploadFile (url, file, opts = {}) {
    opts.headers = {
      'Content-Type': 'multipart/form-data'
    }
    opts.origin = true
    let formData = new FormData()
    formData.append('file', file)
    return this.post(url, formData, opts)
  },
  createLockId () {
    return lockId++
  }
}

export default http
