<template>
  <safe-area-layout class="l-operation p-bind-success">
    <div slot="topbar" class="l-operation__title">{{ $t('bind.title') }}</div>
    <div class="l-operation__header">{{ $t('bind.success') }}</div>
    <div class="l-operation__content u-align-c">
      <div class="p-bind-success__img">
        <div class="p-bind-success__avatar" :class="{ 'c-solid-avatar': sex === 0, 'c-solid-avatar--boy': sex === 1, 'c-solid-avatar--girl': sex === 2, 'has-avatar': !!figure && !!figure.headUrl }">
          <div v-if="!!figure && !!figure.headUrl" class="c-solid-avatar__icon">
            <i :style="{ backgroundImage: `url(${ figure.headUrl })` }"></i>
          </div>
        </div>
        <div class="p-bind-success__link"></div>
      </div>
    </div>
    <div slot="footer" class="l-operation__footer">
      <div class="l-operation__button--full o-button t-adapt-button" @click="next">{{ $t('common.confirm') }}</div>
    </div>
  </safe-area-layout>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'bind-success',
  data () {
    return {}
  },
  computed: {
    ...mapState(['figures', 'runtimmeMode']),
    ...mapGetters(['getFigure']),
    id () {
      return Number(this.$route.params.id)
    },
    figure () {
      return this.getFigure(this.id)
    },
    sex () {
      if (this.figure) {
        if (this.figure.gender === 'Male') {
          return 1
        }
        if (this.figure.gender === 'Female') {
          return 2
        }
        return this.figure.relationship || 0
      }
      return 0
    }
  },
  methods: {
    next () {
      switch (this.runtimmeMode) {
        case 'boot':
          if (this.figures.length === 1) {
            this.$router.replace({ name: 'home' })
          } else {
            this.$router.replace({ name: 'bind', params: { type: 'again' } })
          }
          break
        default:
          this.$router.back()
          break
      }
    }
  }
}
</script>

<style lang="scss">
.p-bind-success {
  &__img {
    position: relative;
    display: inline-block;
    padding-right: 110px;
    @include bg-img-ratio('../../assets/bracelet', 118, 427);
  }
  &__avatar {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -30px;
  }
  &__link {
    position: absolute;
    top: 50%;
    right: 54px;
    margin-top: -15px;
    display: inline-block;
    @include bg-img-ratio('../../assets/bind-success', 124, 60);
  }
}
</style>
