<template>
  <navigation-wrapper class="p-motion" :title="$t('motion.title')">
    <div class="l-navigation__content has-padding">
      <div class="p-motion__header">
        <div class="p-motion-today u-clearfix">
          <div class="p-motion-today__tip">
            {{ $t('motion.today') }}
            <div class="p-motion-today__time">{{ today }}</div>
          </div>
          <div class="p-motion-today__value">{{ todaySteps }}<span class="p-motion-today__unit">{{ $t('motion.unit') }}</span></div>
        </div>
      </div>
      <div class="p-motion__tab">
        <div class="o-tab" :class="{ 'is-active': type === 1 }" @click="changeType(1)">{{ $t('motion.weekly') }}</div>
        <div class="o-tab" :class="{ 'is-active': type === 2 }" @click="changeType(2)">{{ $t('motion.monthly') }}</div>
      </div>
      <div class="p-motion__chart">
        <schart :mode="1" :steps="steps"></schart>
      </div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import schart from '@/components/StepChart.vue'

export default {
  name: 'motion',
  components: {
    schart
  },
  data () {
    return {
      date: new Date(),
      todaySteps: 0,
      type: 1,
      steps: null
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    today () {
      return this.$t('format.date', {
        week: this.$d(this.date, 'week'),
        month: this.$d(this.date, 'month'),
        day: this.date.getDate(),
        year: this.date.getFullYear()
      })
    },
    stepUrl () {
      return this.isStaff ? 'staff/sport' : 'parent/child/sport'
    },
    url () {
      return this.isStaff ? 'staff/stepsstat' : 'parent/child/stepstate'
    },
    id () {
      return Number(this.$route.params.id)
    },
    postData () {
      return this.isStaff ? {
        token: this.user.token,
        type: this.type
      } : {
        token: this.user.token,
        idStudent: this.id,
        type: this.type
      }
    }
  },
  methods: {
    back () {
      this.$router.back()
    },
    fetchTodaySteps () {
      if (!this.$stepHttpPost) {
        this.$stepHttpPost = this.$http.createPost(this.stepUrl, {
          success: res => this.todaySteps = res.data.steps
        })
      }
      this.$stepHttpPost.request(this.isStaff ? {
        token: this.user.token
      } : {
        token: this.user.token,
        idStudent: this.id
      })
    },
    fetch () {
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          success: res => this.steps = res.data.map(val => {
            return {
              time: val.sportDate,
              steps: val.steps
            }
          })
        })
      }
      this.$httpPost.request(this.isStaff ? {
        token: this.user.token,
        type: this.type
      } : {
        token: this.user.token,
        idStudent: this.id,
        type: this.type
      })
    },
    changeType (type) {
      if (this.type !== type) {
        this.type = type
        this.steps = null
        this.fetch()
      }
    }
  },
  beforeRouteUpdate (to, from, next) {
    this.date = new Date()
    this.todaySteps = 0
    this.steps = null
    this.fetchTodaySteps()
    this.fetch()
    next()
  },
  created () {
    this.fetchTodaySteps()
    this.fetch()
  },
  beforeDestroy () {
    if (this.$stepHttpPost) {
      this.$stepHttpPost.cancel()
    }
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss" scoped>
.p-motion {
  &__header {
    position: relative;
    padding: 20px 10px 26px;
    margin-bottom: 20px;
    @include border-bottom-1px(rgba(#000, 0.1));
  }
  &__tab {
    text-align: center;
    margin-bottom: 30px;
    .o-tab + .o-tab {
      margin-left: 110px;
    }
  }
}
.p-motion-today {
  position: relative;
  text-align: right;
  &__tip {
    position: absolute;
    top: 0;
    left: 0;
    color: #404040;
    font-size: 24px;
    font-weight: bold;
    line-height: 1;
    z-index: 0;
  }
  &__time {
    position: absolute;
    top: 36px;
    left: 0;
    padding: 4px 6px 2px;
    color: rgba(#000, 0.3);
    background-color: #f6f7fb;
    font-size: 10px;
    line-height: 1;
    white-space: nowrap;
  }
  &__value {
    float: right;
    color: #52c47a;
    font-size: 46px;
    font-weight: bold;
    line-height: 1;
  }
  &__unit {
    color: rgba(#000, 0.2);
    font-size: 10px;
    font-weight: normal;
    line-height: 1;
  }
}
</style>
