<template>
  <div class="c-percent-circle">
    <canvas ref="canvas" />
    <div class="c-percent-circle__info">
      <div class="c-percent-circle__total"><span class="c-percent-circle__arrived">{{ result.arrived }}</span>/{{ result.total }}</div>
      <div class="c-percent-circle__tip" :class="{ 'is-empty': !tip }">{{ tip }}</div>
      <div class="c-percent-circle__description" :class="{ 'is-empty': !description }">{{ description }}</div>
    </div>
  </div>
</template>

<script>
import percentCircle from '@/utils/canvas-percent-circle.js'

export default {
  name: 'percent-circle',
  props: {
    result: {
      type: Object,
      default () {
        return {
          arrived: 0,
          total: 0,
          percent: 0
        }
      }
    },
    tip: {
      type: String,
      default: ''
    },
    description: {
      type: String,
      default: ''
    }
  },
  methods: {
    refresh () {
      if (this.$canvas) {
        this.$canvas.targetPercent = this.result.percent
      }
    }
  },
  watch: {
    result () {
      this.$nextTick(() => {
        this.refresh()
      })
    }
  },
  mounted () {
    this.$canvas = new percentCircle(this.$refs.canvas, {
      lineWidth: 14,   //
      r: 180,
      startPoint: Math.PI * 0.7, //  Math.PI*0.7;
      targetPercent: this.result.percent,  //0-1
      point: {             //
        show: false,       //
        color: '#ffffff'     //
      }
    })
    this.$canvas.execAnimation()
  },
  beforeDestroy () {
    if (this.$canvas) {
      this.$canvas.stopAnimation()
    }
  }
}
</script>

<style lang="scss">
.c-percent-circle {
  position: relative;
  text-align: center;
  &__info {
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
    text-align: center;
  }
  &__total {
    vertical-align: bottom;
    color: rgba(#333, 0.3);
    font-size: 16px;
    white-space: normal;
  }
  &__arrived {
    color: #4978f3;
    font-size: 38px;
  }
  &__tip {
    padding: 12px 0 8px;
    color: rgba(#333, 0.3);
    font-size: 12px;
    line-height: 1;
    white-space: normal;
    &.is-empty {
      height: 12px;
    }
  }
  &__description {
    display: inline-block;
    padding: 4px 8px;
    color: rgba(#000, 0.6);
    font-size: 10px;
    line-height: 1;
    border-radius: 18px;
    background-color: #ebeff2;
    white-space: normal;
    &.is-empty {
      height: 10px;
      background-color: transparent;
    }
  }
}
</style>
