.c-navigation-bar {
  position: relative;
  box-sizing: border-box;
  padding-top: 11px;
  color: #404040;
  font-size: 18px;
  font-weight: bold;
  line-height: 1;
  text-align: center;
  &__icon_wrapper {
    position: absolute;
    top: 0;
    left: 5px;
    padding: 10px;
  }
  &__icon {
    display: inline-block;
    @include bg-img-ratio('../assets/back', 20, 36);
  }
  &__button {
    position: absolute;
    top: 4px;
    right: 10px;
    padding: 10px;
    font-size: 14px;
    line-height: 1;
    font-weight: normal;
  }
