<template>
  <safe-area-layout class="c-side-menu">
    <div slot="topbar" class="c-side-menu__header">
      <div class="c-avatar" :class="{ 'has-avatar': !!user.headUrl, 'c-avatar--boy': user.gender === 'Male', 'c-avatar--girl': user.gender === 'Female' }">
        <div v-if="!!user.headUrl" class="c-avatar__icon">
          <i :style="{ backgroundImage: `url(${ user.headUrl })` }"></i>
        </div>
      </div>
      <div class="c-side-menu__name">{{ user.name }}</div>
      <div class="c-side-menu__info">
        <div class="c-side-menu__phone"><i class="c-side-menu__icon--phone"></i>{{ phoneNumber }}</div>
        <div class="c-side-menu__email"><i class="c-side-menu__icon--email"></i>{{ user.email }}</div>
      </div>
    </div>
    <div class="c-side-menu__wrapper">
      <ul>
        <router-link tag="li" class="c-side-menu__link" :to="{ name: 'bracelet' }"><i class="c-side-menu__icon--bracelet"></i>{{ $t('home.menu.bracelet') }}</router-link>
        <router-link tag="li" class="c-side-menu__link" :to="{ name: 'account' }"><i class="c-side-menu__icon--account"></i>{{ $t('home.menu.account') }}</router-link>
        <router-link tag="li" v-if="isParent" class="c-side-menu__link" :to="{ name: 'children' }"><i class="c-side-menu__icon--kids"></i>{{ $t('home.menu.kids') }}</router-link>
        <router-link tag="li" class="c-side-menu__link" :to="{ name: 'pre-modify-pwd' }"><i class="c-side-menu__icon--password"></i>{{ $t('home.menu.password') }}</router-link>
        <router-link tag="li" class="c-side-menu__link" :to="{ name: 'setting' }"><i class="c-side-menu__icon--setting"></i>{{ $t('home.menu.setting') }}</router-link>
        <router-link tag="li" class="c-side-menu__link" :to="{ name: 'faq' }"><i class="c-side-menu__icon--faq"></i>{{ $t('home.menu.faq') }}</router-link>
        <router-link tag="li" class="c-side-menu__link" :to="{ name: 'about' }"><i class="c-side-menu__icon--about"></i>{{ $t('home.menu.about') }}</router-link>
      </ul>
    </div>
    <div slot="footer" class="c-side-menu__footer">
      <div class="o-button o-button--unwish" @click="logout">{{ $t('home.menu.logout') }}</div>
    </div>
  </safe-area-layout>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import * as Types from '@/store/mutation-types.js'

export default {
  name: 'side-menu',
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isParent', 'phoneNumber'])
  },
  methods: {
    logout () {
      this.$store.dispatch(Types.LOGOUT)
      this.$router.replace({ name: 'login' })
    }
  }
}
</script>

<style lang="scss">
.c-side-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  width: 295px;
  background-color: #323940;
  z-index: 0;
  transition: transform 0.2s;
  transform: translateX(-30px);
  text-align: center;
  &__header {
    padding-top: 36px;
    // min-height: 100%;
    // box-sizing: border-box;
    // padding: 36px 0 85px;
  }
  &__name {
    margin-bottom: 20px;
    color: #fff;
    font-size: 18px;
    line-height: 1;
  }
  &__info {
    text-align: left;
    padding: 0 20px 0 30px;
    margin-bottom: 10px;
  }
  &__phone, &__email {
    position: relative;
    display: inline-block;
    padding: 4px 0 4px 20px;
    line-height: 12px;
    color: rgba(#fff, 0.4);
    font-size: 10px;
    word-break: break-all;
  }
  &__phone {
    margin-right: 20px;
  }
  &__link {
    padding: 18px 0 18px 70px;
    background-position: 40px 50%;
    background-repeat: no-repeat;
    color: #fff;
    font-size: 14px;
    line-height: 1;
    text-align: left;
    &:active {
      background-color: rgba(#fff, 0.1);
    }
  }
  &__icon--phone {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -6px;
    @include bg-img-ratio('../../assets/icon_tel', 24, 24);
  }
  &__icon--email {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -5px;
    @include bg-img-ratio('../../assets/icon_email', 24, 20);
  }
  &__icon--bracelet {
    position: absolute;
    left: 36px;
    @include bg-img-ratio('../../assets/icon_bracelet', 34, 35);
  }
  &__icon--account {
    position: absolute;
    left: 36px;
    @include bg-img-ratio('../../assets/icon_account_info', 32, 31);
  }
  &__icon--kids {
    position: absolute;
    left: 36px;
    @include bg-img-ratio('../../assets/icon_children', 40, 35);
  }
  &__icon--password {
    position: absolute;
    left: 36px;
    @include bg-img-ratio('../../assets/icon_password', 32, 36);
  }
  &__icon--setting {
    position: absolute;
    left: 36px;
    @include bg-img-ratio('../../assets/icon_setting', 34, 34);
  }
  &__icon--faq {
    position: absolute;
    left: 36px;
    @include bg-img-ratio('../../assets/icon_faq', 34, 30);
  }
  &__icon--about {
    position: absolute;
    left: 36px;
    @include bg-img-ratio('../../assets/icon_about', 34, 35);
  }
  &__footer {
    padding: 16px 40px;
  }
}
</style>
