<template>
  <navigation-wrapper :title="$t('attendance.title')">
    <weekly-calendar class="l-navigation__placeholder" @change="change" :default-date="date" @toggle="toggle"></weekly-calendar>
    <router-view class="l-navigation__content" :date="date" />
    <div v-if="showCalendar" class="c-attendance-calendar" @click="showCalendar = false">
      <div @click.stop>
        <calendar :weeks="weeks" :default-date="selectedDate" @change="change"></calendar>
      </div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { formatToYYYYMMDD } from '@/utils/format.js'
import weeklyCalendar from '@/components/CalendarWeekly.vue'
import calendar from '@/components/Calendar.vue'

export default {
  name: 'attendance-wrapper',
  components: {
    weeklyCalendar,
    calendar
  },
  data () {
    return {
      date: null,
      showCalendar: false,
      selectedDate: null
    }
  },
  computed: {
    weeks () {
      return this.$i18n.locale === 'zh-hk' ? ['', '', '', '', '', '', ''] : ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
    }
  },
  beforeRouteUpdate (to, from, next) {
    if (to.name === from.name) {
      this.date = to.params.date || formatToYYYYMMDD(new Date())
      this.showCalendar = false
      next()
    } else if (this.date !== to.params.date) {
      this.$router.replace({ name: to.name, params: Object.assign(to.params, { date: this.date }) })
    } else {
      next()
    }
  },
  methods: {
    change (date) {
      if (this.date !== date) {
        this.date = date
        this.$router.replace({ params: { date } })
      }
      this.showCalendar = false
    },
    toggle (date) {
      this.selectedDate = date
      this.showCalendar = true
    }
  },
  created () {
    this.date = this.$route.params.date || formatToYYYYMMDD(new Date())
  }
}
</script>

<style lang="scss">
.c-attendance-calendar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(#000, 0.4);
}
</style>
