<template>
  <navigation-wrapper :title="$t('home.menu.kids')">
    <div class="l-navigation__content has-padding">
      <ul>
        <li v-for="figure in figures" :key="figure.id" class="c-children-item" @click="choose(figure)">
          <div class="c-children-item__avatar" :class="{ 'c-avatar--boy': figure.relationship === 1, 'c-avatar--girl': figure.relationship === 2,  'has-avatar': !!figure.headUrl }">
            <div v-if="!!figure.headUrl" class="c-avatar__icon">
              <i :style="{ backgroundImage: `url(${ figure.headUrl })` }"></i>
            </div>
          </div>
          <div class="c-children-item__name u-more">{{ figure.name }}</div>
        </li>
      </ul>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'children',
  computed: {
    ...mapState(['figures'])
  },
  methods: {
    choose (figure) {
      this.$router.push({ name: 'children-info', params: { id: figure.id } })
    }
  }
}
</script>

<style lang="scss">
.c-children-item {
  position: relative;
  padding: 35px 15px 35px 80px;
  @include border-bottom-1px(#e6e6e6);
  &__avatar {
    position: absolute;
    top: 16px;
    left: 0;
  }
  &__name {
    color: #404040;
    font-size: 16px;
    line-height: 1;
    padding: 7px 0;
    white-space: nowrap;
    overflow: hidden;
  }
}
</style>
