<template>
  <canvas class="c-step-chart" ref="el" @click="click"></canvas>
</template>

<script>
function WcChart (config) {
  this.init(config)
}

WcChart.prototype = {
  /**
   * margin of chart
   */
  margin: 20,
  /**
   * Initial width
   * @type {number}
   */
  width: 0,
  /**
   * Initial height
   * @type {number}
   */
  height: 0,
  /**
   * Is data has loaded
   * @type {false}
   */
  isDataLoad: false,
  /**
   * Initial of chart data
   * @type {Array}
   */
  data: null,
  /**
   * data format, it will be init by init function
   * @type {Array}
   */
  points: [],
  /**
   * font family
   * @type {String}
   */
  fontFamily: 'sans-serif,arial',
  /**
   * Initial end day
   * @type {Date}
   */
  day: null,
  /**
   * Initial of chart y-axis limit
   * @type {number}
   */
  yAxis: 16000,
  /**
   * Initial of chart y-axis increase unit
   * @type {number}
   */
  yAxisIc: 4000,
  /**
   * Background quad angle radius
   * @type {number}
   */
  quadRadius: 30,
  /**
   * head left text
   * @type {String}
   */
  headText: 'Steps',
  /**
   * chart font size
   * @type {Number}
   */
  chartFontSize: 30,
  /**
   * 10 thousand y-axis height
   * @type {Number}
   */
  chartMeanLine: 0,
  /**
   * rectangles
   * - {x: 0, y: 0, width: 0, height: 0}
   * @type {Object}
   */
  rectHead: {x: 0, y: 0, width: 0, height: 0},
  rectChart: {x: 0, y: 0, width: 0, height: 0},
  rectChartCenter: {x: 0, y: 0, width: 0, height: 0},
  /**
   * This is the only required option. It should be from 0.0 to 1.0
   * @type {number}
   */
  value: 1,
  /**
   * Fill of the arc. You may set it to:
   *   - solid color:
   *     - { color: '#3aeabb' }
   *     - { color: 'rgba(255, 255, 255, .3)' }
   *   - linear gradient (left to right):
   *     - { gradient: ['#3aeabb', '#fdd250'], gradientAngle: Math.PI / 4 }
   *     - { gradient: ['red', 'green', 'blue'], gradientDirection: [x0, y0, x1, y1] }
   *   - image:
   *     - { image: 'http://i.imgur.com/pT0i89v.png' }
   *     - { image: imageObject }
   *     - { color: 'lime', image: 'http://i.imgur.com/pT0i89v.png' } - color displayed until the image is loaded
   */
  fill: {
    gradient: [['#21b881', 0.1], ['#0e8fa2', 0.9]],
    gradientAngle: Math.PI * -45 / 180
  },
  /**
   * Animation config (see jQuery animations: http://api.jquery.com/animate/)
   */
  animation: {
    duration: 400,
    easing: 'wcChart'
  },
  /**
   * Default animation starts at 0.0 and ends at specified `value`. Let's call this direct animation.
   * If you want to make reversed animation then you should set `animationStartValue` to 1.0.
   * Also you may specify any other value from 0.0 to 1.0
   * @type {number}
   */
  animationStartValue: 0.0,
  /**
   * @type {number}
   */
  animationPoint: null,
  /**
   * Constructor of wechat chart
   */
  constructor: WcChart,
  /**
   * Container element. Should be passed into constructor config
   * @type {jQuery}
   */
  el: null,
  /**
   * Canvas element.
   * @type {HTMLCanvasElement}
   */
  canvas: null,
  /**
   * 2D-context of the canvas
   * @protected
   * @type {CanvasRenderingContext2D}
   */
  ctx: null,
  /**
   * Fill of the background
   * @type {string|CanvasGradient|CanvasPattern}
   */
  fillPattern: null,
  /**
   * Last rendered frame value
   * @type {number}
   */
  lastFrameValue: 0.0,
  /**
   * Animation timer
   * @type {number}
   */
  timer: null,
  /**
   * Mode
   * @type {number}
   */
  mode: 0,
  /**
   * Init/re-init the widget
   * @param {object} config Config
   */
  init: function (config) {
    Object.assign(this, config)
    clearInterval(this.timer)
    this.initWidget()
    this.initRect()
    this.initFill()
    if (config.data) {
      this.setData(config.data)
    } else {
      this.draw()
    }
  },
  setData: function (data) {
    this.isDataLoad = true
    clearInterval(this.timer)
    this.data = data
    this.initData()
    this.draw()
  },
  /**
   * Init widget
   * @protected
   */
  initWidget: function () {
    let el = this.el
    let canvas = this.canvas = this.canvas || this.el
    canvas.width = this.width = this.width === 0 ? el.offsetWidth : this.width
    canvas.height = this.height = this.height === 0 ? el.offsetHeight : this.height
    this.ctx = canvas.getContext('2d')
  },
  /**
   * Init rectangles
   */
  initRect: function () {
    let margin = this.margin
    let titleHeight = 88

    this.rectHead = { x: margin, y: 0, width: this.width - 2 * margin, height: titleHeight }

    this.rectChart = {
      x: margin,
      y: titleHeight,
      width: this.width - 2 * margin,
      height: this.height - titleHeight
    }

    this.rectChartCenter = {
      x: this.rectChart.x,
      y: this.rectChart.y + this.chartFontSize * 2.5,
      width: this.rectChart.width - this.chartFontSize * 0.8,
      height: this.rectChart.height - this.chartFontSize * 5,
    }
  },
  initPointsBySteps: function (data) {
    /* get limit and last data */
    let maxData = Math.max.apply(Math, data)
    let interval = this.rectChartCenter.width / (data.length - 1)
    let itrDay
    if (this.day === null) {
      itrDay = new Date()
    } else {
      itrDay = new Date(this.day.getTime())
    }
    let yMaxUint = maxData <= this.yAxis ? this.yAxis : Math.ceil((maxData - this.yAxis) / this.yAxisIc) * this.yAxisIc + this.yAxis

    /* init begin date and prepare for iteration */
    itrDay.setDate(itrDay.getDate() - data.length + 1)

    /* find out y-axis point of 10 thousand unit line */
    this.chartMeanLine = this.rectChartCenter.y + this.rectChartCenter.height - 10000 * this.rectChartCenter.height / yMaxUint

    /* transfer data into points */
    this.points = []
    for (let i = 0, j = data.length; i < j; i++) {
      this.points[i] = {
        x: this.rectChartCenter.x + interval * i,
        y: this.rectChartCenter.y + this.rectChartCenter.height - data[i] * this.rectChartCenter.height / yMaxUint,
        value: data[i],
        text: i === 0 ? (itrDay.getMonth() + 1) + '/' + itrDay.getDate() : itrDay.getDate(),
        radius: i === j - 1 ? 8 : 6,
        textAlign: i === 0 ? 'left' : 'center',
        rect: { x: this.rectChartCenter.x + interval * (i - 0.5), y: this.rectChart.y, width: interval, height: this.rectChart.height }
      }
      itrDay.setDate(itrDay.getDate() + 1)
    }
    this.animationPoint = data.length - 1
  },
  initPointsByObj: function (data) {
    /* get limit and last data */
    let maxData = Math.max.apply(Math, data.map(function (val) {
      return val.steps
    }))
    let interval = this.rectChartCenter.width / (data.length - 1)
    let yMaxUint = maxData <= this.yAxis ? this.yAxis : Math.ceil((maxData - this.yAxis) / this.yAxisIc) * this.yAxisIc + this.yAxis

    /* find out y-axis point of 10 thousand unit line */
    this.chartMeanLine = this.rectChartCenter.y + this.rectChartCenter.height - 10000 * this.rectChartCenter.height / yMaxUint

    let length = data.length
    let useWeek = length > 7
    let step = 6
    let count = (step - length % step) % step
    let time
    let text
    /* transfer data into points */
    this.points = []
    for (let i = 0; i < length; i++) {
      time = new Date(data[length - 1 - i].time)
      if (i === 0) {
        text = (time.getMonth() + 1) + '/' + time.getDate()
      } else if (i === length - 1 || !useWeek || count === step - 1) {
        text = time.getDate()
      } else {
        text = ''
      }
      count = (count + 1) % step
      this.points.push({
        x: this.rectChartCenter.x + interval * i,
        y: this.rectChartCenter.y + this.rectChartCenter.height - data[i].steps * this.rectChartCenter.height / yMaxUint,
        value: data[i].steps,
        text: text,
        radius: i === length - 1 ? 8 : 6,
        textAlign: i === 0 ? 'left' : 'center',
        rect: { x: this.rectChartCenter.x + interval * (i - 0.5), y: this.rectChart.y, width: interval, height: this.rectChart.height }
      })
    }
    this.animationPoint = data.length - 1
  },
  /**
   * init all data
   */
  initData: function () {
    if (!this.data || this.isDataLoad === false) {
      this.initPointsBySteps([])
      return
    }
    if (this.mode === 0) {
      this.initPointsBySteps(this.data)
    } else if (this.mode === 1) {
      this.initPointsByObj(this.data)
    } else {
      this.points = []
    }
  },
  /**
   * Init fill pattern
   * It could do this async (on image load)
   */
  initFill: function () {
    let fill = this.fill
    let ctx = this.ctx
    let width = this.width
    let height = this.height

    if (!fill) {
      throw Error('The fill is not specified!')
    }

    if (fill.color) {
      this.fillPattern = fill.color
    }

    if (fill.gradient) {
      let gr = fill.gradient

      if (gr.length === 1) {
        this.fillPattern = gr[0]
      } else if (gr.length > 1) {
        let ga = fill.gradientAngle || 0 // gradient direction angle 0 by default
        let gd = fill.gradientDirection || [
          width / 2 * (1 - Math.cos(ga)), // x0
          height / 2 * (1 + Math.sin(ga)), // y0
          width / 2 * (1 + Math.cos(ga)), // x1
          height / 2 * (1 - Math.sin(ga))  // y1
        ]

        let lg = ctx.createLinearGradient.apply(ctx, gd)

        for (let i = 0; i < gr.length; i++) {
          let color = gr[i]
          let pos = i / (gr.length - 1)

          if (Array.isArray(color)) {
            pos = color[1]
            color = color[0]
          }

          lg.addColorStop(pos, color)
        }

        this.fillPattern = lg
      }
    }
  },
  draw: function () {
    if (this.animation && this.animationPoint !== null) {
      this.drawAnimated()
    } else {
      this.drawFrame(this.value)
    }
  },
  /**
   * draw a frame
   * @param {number} v Frame value
   */
  drawFrame: function (v) {
    this.lastFrameValue = v
    //this.ctx.clearRect(0, 0, this.width, this.height)

    this.ctx.fillStyle = 'white'
    this.ctx.strokeStyle ='white'

    this.drawBack()
    this.drawHead()
    this.drawChart()
    this.drawStep(v)
  },

  /**
   * draw background
   */
  drawBack: function () {
    let ctx = this.ctx
    ctx.save()
    ctx.fillStyle = this.fillPattern
    ctx.globalAlpha = 1
    this.roundRect(0, 0, this.width, this.height, this.quadRadius, true, false)
    ctx.restore()
  },

  /**
   * draw head
   */
  drawHead: function () {
    let fontSize = 48
    let ctx = this.ctx

    /* draw text of head */
    ctx.save()
    ctx.globalAlpha = 0.8
    ctx.font = fontSize + 'px ' + this.fontFamily
    ctx.fillStyle = 'white'
    ctx.fillText(this.headText, this.rectHead.x, this.rectHead.y + fontSize + this.margin, this.rectHead.width)
    // current steps
    // ctx.textAlign = 'right'
    // ctx.fillText(this.points.length > 0 ? this.points[this.points.length - 1].value.toString() : '', this.rectHead.x + this.rectHead.width, this.rectHead.y + fontSize + this.margin)

    /* draw a split line */
    ctx.beginPath()
    ctx.globalAlpha = 0.8
    ctx.moveTo(this.rectHead.x, this.rectHead.y + this.rectHead.height)
    ctx.lineTo(this.rectHead.x + this.rectHead.width, this.rectHead.y + this.rectHead.height)
    ctx.stroke()

    ctx.restore()
  },

  /* draw chart */
  drawChart: function () {
    /* out if data unload */
    if (this.isDataLoad === false || this.points.length === 0) {
      return
    }

    let ctx = this.ctx
    ctx.save()
    ctx.fillStyle = 'white'

    /* draw 10 thousand line of x-axis */
    ctx.globalAlpha = 0.6
    ctx.font = (this.chartFontSize - 8) + 'px ' + this.fontFamily
    ctx.fillText('1W', this.rectChartCenter.x + this.rectChartCenter.width + 4, this.chartMeanLine + this.chartFontSize / 2.8)
    ctx.beginPath()
    ctx.lineWidth = 1
    this.dashedLineTo(this.rectChartCenter.x, this.chartMeanLine, this.rectChartCenter.x + this.rectChartCenter.width, this.chartMeanLine, [7, 4])
    ctx.stroke()

    /* draw x-axis text and point */
    ctx.font = this.chartFontSize + 'px ' + this.fontFamily
    for (let i = 0; i < this.points.length; i++) {
      /* draw text */
      if (this.points[i].text) {
        ctx.textAlign = this.points[i].textAlign
        ctx.globalAlpha = /*i === this.points.length - 1 ? 1 : */i === this.animationPoint ? 1 : 0.5
        ctx.fillText(this.points[i].text, this.points[i].x, this.rectChartCenter.y + this.rectChartCenter.height + 1.5 * this.chartFontSize)
      }
    }

    /* draw points and lines */
    for (let i = 0; i < this.points.length; i++) {
      /* draw point */
      ctx.globalAlpha = 1
      ctx.beginPath()
      // 
      // ctx.fillStyle = 'black'
      ctx.arc(this.points[i].x, this.points[i].y, this.points[i].radius, 0, 2 * Math.PI)
      ctx.fill()

      /* draw line */
      if (i !== 0) {
        ctx.save()
        ctx.beginPath()
        ctx.lineWidth = 3
        // strokeStyle
        ctx.moveTo(this.points[i - 1].x, this.points[i - 1].y)
        if (this.points[i - 1].value !== 0) {
          ctx.lineTo(this.points[i].x, this.points[i].y)
        } else {
          ctx.globalAlpha = 0.5
          this.dashedLineTo(this.points[i-1].x, this.points[i-1].y, this.points[i].x, this.points[i].y, 7)
        }
        ctx.stroke()
        ctx.restore()
      }
    }

    /* draw a shadow */
    ctx.beginPath()

    ctx.moveTo(this.points[0].x, this.points[0].y)
    for (let i = 1; i < this.points.length; i++) {
      ctx.lineTo(this.points[i].x, this.points[i].y)
    }
    ctx.lineTo(this.rectChartCenter.x + this.rectChartCenter.width, this.rectChartCenter.y + this.rectChartCenter.height)
    ctx.lineTo(this.rectChartCenter.x, this.rectChartCenter.y + this.rectChartCenter.height)
    ctx.lineTo(this.points[0].x, this.points[0].y)
    // 
    ctx.fillStyle = 'white'
    ctx.globalAlpha = 0.1
    ctx.fill()

    ctx.restore()
  },

  /* draw animation step */
  drawStep: function (v) {
    if (this.animationPoint === null || this.animationPoint < 0
      || this.animationPoint >= this.points.length
      || this.isDataLoad === false) {
      return
    }

    let ctx = this.ctx
    let point = this.points[this.animationPoint]
    let lastY = this.rectChartCenter.y - this.chartFontSize * 1.3
    let beginY = point.y
    let textY = (1 - v) * Math.abs(beginY - lastY) + lastY
    let beginAlpha = 0.4
    let endAlpha = 0.8
    let alpha = v * (endAlpha - beginAlpha) + beginAlpha

    /* draw step number */
    ctx.save()
    ctx.font = 'bold ' + this.chartFontSize + 'px ' + this.fontFamily
    ctx.textAlign = this.animationPoint === this.points.length - 1 ? 'right' : point.textAlign
    ctx.fillStyle = 'white'
    ctx.globalAlpha = alpha
    ctx.fillText(point.value.toString(), point.x, textY)
    ctx.restore()
  },
  easing: function (x, t, b, c, d) {
    return c * Math.sqrt(1 - (t = t / d - 1)*t) + b
  },
  /**
   * Draw with animate
   */
  drawAnimated: function () {
    if (this.timer) {
      clearInterval(this.timer)
    }
    let now = Date.now()
    this.timer = setInterval(() => {
      let percent = (Date.now() - now) / this.animation.duration
      this.drawFrame(this.easing(percent, this.animation.duration * percent, 0, 1, this.animation.duration))
      if (percent >= 1) {
        clearInterval(this.timer)
      }
    }, 0)
  },
  /**
   * click function of this element
   * @param e event of click
   */
  clickFuc: function (e) {
    // let dataName = 'motion'
    // let rect = this.el.getBoundingClientRect()
    // let point = {x: (e.pageX - rect.left) * this.scaleX, y: (e.pageY - rect.top) * this.scaleY }
    let point = {x: e.pageX * this.scaleX, y: e.pageY  * this.scaleY }
    if (this.isPointInRect(this.rectChart, point)) {
      if (this.isDataLoad) {
        /* click in chart */
        let points = this.points
        for (let i = 0; i < points.length; i++) {
          if (this.isPointInRect(points[i].rect, point)) {
            this.animationPoint = i
            if (this) {
              this.draw()
            }
            break
          }
        }
      }
    }
  },
  /**
   * check point is in rect, return true when point in rect
   * @param rect Object {x, y, width, height}
   * @param point Object {x, y}
   */
  isPointInRect: function (rect, point) {
    return point.x >= rect.x && point.x <= rect.x + rect.width && point.y >= rect.y && point.y <= rect.y + rect.height
  },
  roundRect: function (x, y, width, height, radius, fill, stroke) {
    stroke = typeof stroke === 'undefined' ? true : stroke
    stroke = typeof stroke === 'undefined' ? true : stroke
    radius = typeof radius === 'undefined' ? 5 : radius

    this.ctx.beginPath()
    this.ctx.moveTo(x + radius, y)
    this.ctx.lineTo(x + width - radius, y)
    this.ctx.quadraticCurveTo(x + width, y, x + width, y + radius)
    this.ctx.lineTo(x + width, y + height - radius)
    this.ctx.quadraticCurveTo(x + width, y + height, x + width - radius, y + height)
    this.ctx.lineTo(x + radius, y + height)
    this.ctx.quadraticCurveTo(x, y + height, x, y + height - radius)
    this.ctx.lineTo(x, y + radius)
    this.ctx.quadraticCurveTo(x, y, x + radius, y)
    this.ctx.closePath()

    if (stroke) {
      this.ctx.stroke()
    }

    if (fill) {
      this.ctx.fill()
    }
  },
  dashedLineTo: function (fromX, fromY, toX, toY, pattern) {
    let interval = 0, padding = 0
    if (typeof pattern === 'undefined') {
      padding = 5
      interval = 5
    } else {
      if (pattern.constructor === Array) {
        padding = pattern[0]
        interval = pattern[1]
      } else {
        padding = interval = pattern
      }
    }

    /* calculate the delta x and delta y */
    let dx = (toX - fromX)
    let dy = (toY - fromY)
    let distance = Math.floor(Math.sqrt(dx * dx + dy * dy))
    let unitLength = padding + interval
    let dashLineInterval = (unitLength <= 0) ? distance : Math.floor(distance / unitLength)
    let deltaPaddingY = (dy / distance) * padding
    let deltaPaddingX = (dx / distance) * padding
    let unitLengthX = (dx / distance) * unitLength
    let unitLengthY = (dy / distance) * unitLength

    /* draw dash line */
    let x = fromX, y = fromY
    for (let dl = 0; dl < dashLineInterval; dl++) {
      this.ctx.moveTo(x, y)
      this.ctx.lineTo(x + deltaPaddingX, y + deltaPaddingY)
      x += unitLengthX
      y += unitLengthY
    }
  }
}

export default {
  name: 'step-chart',
  props: ['steps', 'mode'],
  data () {
    return {
      wcChart: null
    }
  },
  methods: {
    click (e) {
      if (this.wcChart) {
        this.wcChart.clickFuc(e)
      }
    },
    refresh () {
      this.wcChart.setData(this.steps)
    }
  },
  watch: {
    steps () {
      this.refresh()
    }
  },
  mounted () {
    let viewWidth = window.innerWidth || document.documentElement.clientWidth
    let viewHeight = window.innerHeight || document.documentElement.clientHeight
    let width = 750
    let height = 375
    this.wcChart = new WcChart({
      width: width,
      height: height,
      scaleX: width / viewWidth,
      scaleY: height / viewHeight,
      el: this.$refs.el,
      mode: this.mode || 0
    })
  },
  beforeDestroy () {
    if (this.wcChart) {
      clearInterval(this.wcChart.timer)
    }
  }
}
</script>

<style lang='scss'>
.c-step-chart {
  width: 100%;
  height: 50%;
}
</style>
