import Vue from 'vue'
import VueI18n from 'vue-i18n'
import en from './lang/en.js'
import zh_hk from './lang/zh-hk.js'
import http from '@/utils/http.js'

Vue.use(VueI18n)

const dateTimeFormats = {
  en: {
    week: {
      weekday: 'short'
    },
    month: {
      month: 'short'
    }
  },
  'zh-hk': {
    week: {
      weekday: 'short'
    },
    month: {
      month: 'narrow'
    }
  }
}

export const langs = [
  {
    key: 'en',
    text: 'English',
    type: 1,
    status: 0,
    code: {}
  },
  {
    key: 'zh-hk',
    text: '',
    type: 2,
    status: 0,
    code: {}
  }
]

let defaultKey = window.localStorage.getItem('locale') || 'en'

let defaultLang = langs.find(lang => lang.key === defaultKey)
if (!defaultLang) {
  defaultLang = langs[0]
  defaultKey = defaultLang.key
}

const i18n = new VueI18n({
  locale: defaultKey,
  dateTimeFormats,
  messages: {
    en, // 
    'zh-hk': zh_hk // 
  }
})

export default i18n

export const lang = {
  key: defaultKey,
  code: defaultLang.code
}

export function fetchErroMsg () {
  let lang = langs.find(lang => lang.key === i18n.locale)
  if (lang && lang.status === 0) {
    lang.status = 1
    http.createPost('common/appmessage', {
      opts: {
        retry: true,
        prevent: true
      },
      success: req => {
        lang.status = 2
        req.data.forEach(info => {
          lang.code[info.code] = info.message
        })
      },
      fail: error => {
        if (error.code >= 0) {
          lang.status = 0
        }
      }
    }).request({
      env: lang.type
    })
  }
}

export function changeLang (langKey) {
  if (langKey !== i18n.locale) {
    let langLocale = langs.find(lang => lang.key === langKey)
    if (langLocale) {
      i18n.locale = langKey
      lang.key = langKey
      lang.code = langLocale.code
      window.localStorage.setItem('locale', langKey)
      fetchErroMsg()
    }
  }
}

export function resetLangs () {
  langs.forEach(lang => {
    if (lang.status === 2) {
      lang.status = 0
      lang.code.length = 0
    }
  })
  fetchErroMsg()
}
