<template>
  <div class="c-calendar">
    <div class="c-calendar__header">
      <div class="c-calendar__today" @click="toToday">{{ $t('calendar.today' )}}</div>
      <div class="c-calendar__current">{{ date }}</div>
    </div>
    <div class="c-calendar__row">
      <div v-for="(week, index) in weeks" :key="index" class="c-calendar__item">{{ week }}</div>
    </div>
    <div class="c-calendar__slide swiper-container" ref="swiper">
      <div class="swiper-wrapper">
        <div v-for="(block, index) in blocks" :key="index" class="c-calendar__block swiper-slide">
          <div v-for="(row, index) in block.rows" :key="index" class="c-calendar__row">
            <div v-for="(item, index) in row" :key="index" :id="item.date" class="c-calendar__item" @click="choose(item.date)">
              <div class="c-calendar__label" :class="{ 'is-active': selected === item.date, 'is-disabled': item.disabled }">{{ item.label }}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper'

export default {
  name: 'calendar',
  props: {
    weeks: {
      type: Array,
      default () {
        return ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
      }
    },
    defaultDate: {
      type: String,
      default: ''
    },
    range: {
      type: Boolean,
      default: true
    },
    rangeStart: {
      type: String,
      default: ''
    },
    rangeEnd: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      minDate: null,
      maxDate: null,
      index: 1,
      date: '',
      blocks: [],
      selected: null
    }
  },
  methods: {
    fillZero (number) {
      if (number < 10) {
        return `0${ number }`
      }
      return number
    },
    formatToYYYYMM (date) {
      return `${ date.getFullYear() }-${ this.fillZero(date.getMonth() + 1) }`
    },
    formatToYYYYMMDD (date) {
      return `${ date.getFullYear() }-${ this.fillZero(date.getMonth() + 1) }-${ this.fillZero(date.getDate()) }`
    },
    isValid (date) {
      if (!this.range) {
        return true
      }
      if ((!this.minDate || date >= this.minDate) && date < this.maxDate) {
        return true
      }
      return false
    },
    createItem (date, disabled) {
      return {
        label: date.getDate(),
        date: this.formatToYYYYMMDD(date),
        disabled: disabled === true || !this.isValid(date)
      }
    },
    createBlock (date) {
      let year = date.getFullYear()
      let month = date.getMonth()
      //
      let firstDayOfMonth = new Date(year, month, 1).getDay()
      let lastDay = new Date(year, month + 1, 0)
      //
      let lastDayOfMonth = lastDay.getDay()
      //
      let lastDateOfMonth = lastDay.getDate()
      let rows = [[]]
      let line = 0
      // 
      if (firstDayOfMonth !== 0) {
        for (let i = firstDayOfMonth; i > 0; i--) {
          rows[line].push(this.createItem(new Date(year, month, 1 - i), true))
        }
      }
      let temp
      for (let i = 1; i <= lastDateOfMonth; i++) {
        temp = new Date(year, month, i)
        rows[line].push(this.createItem(temp))
        if (temp.getDay() === 6) {
          line += 1
          if (i < lastDateOfMonth) {
            rows[line] = []
          }
        }
      }
      // 
      if (lastDayOfMonth !== 6) {
        for (let i = 0, j = 6 - lastDayOfMonth; i < j; i++) {
          rows[line].push(this.createItem(new Date(year, month + 1, 1 + i), true))
        }
      }
      return {
        label: this.$t('format.year-month', {
          year: year,
          month: this.$d(date, 'month')
        }),
        date: this.formatToYYYYMM(date),
        rows
      }
    },
    addPreBlock () {
      if (!this.minDate || this.startDate > this.minDate) {
        this.startDate.setMonth(this.startDate.getMonth() - 1)
        this.blocks.unshift(this.createBlock(this.startDate))
        return true
      }
      return false
    },
    addNextBlock () {
      if (!this.maxDate || this.endDate < this.maxDate) {
        this.blocks.push(this.createBlock(this.endDate))
        this.endDate.setMonth(this.endDate.getMonth() + 1)
        return true
      }
      return false
    },
    refresh () {
      if (this.$swiper) {
        this.$nextTick(() => {
          this.$swiper.updateSlides()
          this.$swiper.slideTo(this.index, 0, false)
        })
      }
    },
    notify () {
      this.$emit('change', this.selected)
    },
    choose (date) {
      if (this.isValid(new Date(date))) {
        this.selected = date
        this.notify()
      }
    },
    offsetDay (date, offset) {
      return new Date(date.getFullYear(), date.getMonth(), date.getDate() + offset)
    },
    toToday () {
      if (this.$swiper) {
        let today = this.today || new Date()
        if (today < this.minDate || today >= this.maxDate) {
          this.init(this.formatToYYYYMMDD(today))
          this.refresh()
        } else {
          this.choose(this.formatToYYYYMMDD(today))
          let date = this.formatToYYYYMM(today)
          this.index = this.blocks.findIndex(block => block.date === date)
          this.$swiper.slideTo(this.index, 0, false)
        }
      }
    },
    init (defaultDate) {
      let hasChanged = !defaultDate
      if (this.range) {
        if (this.rangeStart) {
          this.minDate = new Date(this.rangeStart)
        } else {
          this.minDate = null
        }
        if (this.rangeEnd) {
          this.maxDate = new Date(this.rangeEnd)
        } else {
          this.maxDate = this.offsetDay(new Date(), 1)
        }
        if (this.minDate >= this.maxDate) {
          this.minDate = null
        }
        let selectedDay = defaultDate ? new Date(defaultDate) : new Date()
        if (selectedDay >= this.maxDate) {
          selectedDay = this.offsetDay(this.maxDate, -1)
          hasChanged = true
        } else if (this.minDate && selectedDay < this.minDate) {
          selectedDay = this.minDate
          hasChanged = true
        }
        this.selected = this.formatToYYYYMMDD(selectedDay)
        this.today = new Date()
      } else {
        this.minDate = null
        this.maxDate = null
        this.selected = defaultDate || this.formatToYYYYMMDD(new Date())
      }
      let date = new Date(this.selected)
      let year = date.getFullYear()
      let month = date.getMonth()
      this.startDate = new Date(year, month, 1)
      this.endDate = new Date(year, month + 1, 1)
      this.blocks = [this.createBlock(this.startDate)]
      this.index = 0
      if (this.addPreBlock()) {
        this.index = 1
      }
      this.addNextBlock()
      if (hasChanged) {
        this.notify()
      }
    }
  },
  watch: {
    defaultDate () {
      this.init(this.defaultDate)
      this.refresh()
    }
  },
  created () {
    this.init(this.defaultDate)
  },
  mounted () {
    this.$swiper = new Swiper(this.$refs.swiper, {
      preventInteractionOnTransition: true,
      longSwipesRatio: 0.3,
      initialSlide: this.index,
      on: {
        slideChangeTransitionEnd: () => {
          this.$nextTick(() => {
            this.date = this.blocks[this.$swiper.activeIndex].label
            if (this.$swiper.activeIndex === 0) {
              if (this.addPreBlock()) {
                this.refresh()
              }
            } else if (this.$swiper.activeIndex === this.blocks.length - 1) {
              if (this.addNextBlock()) {
                this.index += 1
                this.refresh()
              }
            } else {
              this.index = this.$swiper.activeIndex
            }
          })
        }
      }
    })
  },
  beforeDestroy () {
    if (this.$swiper) {
      this.$swiper.destroy()
    }
  }
}
</script>

<style lang="scss">
.c-calendar {
  position: relative;
  background-color: #fff;
  z-index: 99;
  &__header {
    text-align: center;
    height: 60px;
    color: #404040;
    font-size: 14px;
    line-height: 1;
    font-weight: bold;
  }
  &__today {
    position: absolute;
    top: 0;
    left: 0;
    padding: 23px;
  }
  &__current {
    display: inline-block;
    position: relative;
    width: 120px;
    padding: 23px 0;
    &::before, &::after {
      content: '';
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
    }
    &::before {
      left: 0;
      @include bg-img-ratio('./pre', 8, 15);
    }
    &::after {
      right: 0;
      @include bg-img-ratio('./next', 8, 15);
    }
  }
  &__slide {
    flex: auto;
  }
  &__block {
    width: 100%;
  }
  &__row {
    display: flex;
    padding: 0 12.5px;
  }
  &__item {
    flex: 1 0 auto;
    width: 50px;
    text-align: center;
    vertical-align: middle;
    padding: 12px 0;
    color: #333;
    font-size: 12px;
  }
  &__label {
    display: inline-block;
    width: 25px;
    height: 25px;
    line-height: 25px;
    border-radius: 50%;
    &.is-active {
      color: #fff;
      background: linear-gradient(90deg, #4ca3f9, #4976f2);
    }
    &.is-disabled {
      opacity: 0.5;
    }
  }
}
</style>
