<template>
  <div class="c-form-field__button t-adapt-color" :class="{ 'is-active': count > 0 }" @click="send">{{ msg }}</div>
</template>

<script>
export default {
  name: 'sms',
  props: {
    mobile: {
      type: String
    },
    areaCode: {
      type: String
    },
    email: {
      type: String
    },
    mode: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      count: 0,
    }
  },
  computed: {
    msg () {
      return this.count > 0 ? this.$t('sms.countdown', { count: this.count }) : this.$t('sms.send')
    },
    url () {
      switch (this.mode) {
        case 0:
          return 'common/smscode/send'
        case 1:
          return 'common/email/send'
        default:
          return null
      }
    }
  },
  methods: {
    cacnel () {
      if (this.timer !== -1) {
        this.count = 0
        clearInterval(this.timer)
        this.timer = -1
      }
      if (this.$httpPost) {
        this.$httpPost.cancel()
      }
    },
    clock () {
      this.count = 60
      this.timer = setInterval(() => {
        this.count -= 1
        if (this.count === 0) {
          clearInterval(this.timer)
          this.timer = -1
          this.$httpPost.awaken()
        }
      }, 1000)
    },
    sendToMobile () {
      if (!this.mobile || !this.areaCode) {
        return
      }
      if (this.$httpPost.isWait()) {
        this.clock()
        this.$httpPost.request({
          mobile: this.mobile,
          areaCode: this.areaCode
        })
      }
    },
    sendToEmali () {
      if (!this.email) {
        return
      }
      if (this.$httpPost.isWait()) {
        this.clock()
        this.$httpPost.request({
          email: this.email
        })
      }
    },
    send () {
      if (!this.$httpPost && this.url) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            sleep: true
          },
          fail: () => {
            if (this.count > 0) {
              this.count = 0
              clearInterval(this.timer)
              this.timer = -1
            }
          }
        })
      }
      switch (this.mode) {
        case 0:
          this.sendToMobile()
          break
        case 1:
          this.sendToEmali()
          break
        default:
          break
      }
    }
  },
  watch: {
    mode () {
      if (this.$httpPost) {
        this.$httpPost.setUrl(this.url)
        this.cacnel()
      }
    }
  },
  beforeDestroy () {
    this.cacnel()
  }
}
</script>

