<template>
  <div class="c-attendance-details">
    <percent-circle class="c-attendance-details__percent" :result="result" :tip="$t('attendance.arrived')" :description="description">
    </percent-circle>
    <div class="c-attendance-details__list">
      <div v-for="(detail, index) in array" :key="index" class="c-attendance-detail" @click="viewSignInInformation(detail)" :class="{ 'is-nonarrival': detail.status === 1, 'is-leave': detail.status === 4, 'is-recorded': detail.status === 2 && detail.isManual !== 1, 'is-manual': detail.status === 2 && detail.isManual === 1 }">
        <div class="c-attendance-detail__avatar">
          <div class="c-solid-avatar" :class="{ 'has-avatar': !!detail.headUrl, 'c-solid-avatar--boy': detail.gender === 'Male', 'c-solid-avatar--girl': detail.gender === 'Female' }">
            <div v-if="!!detail.headUrl" class="c-solid-avatar__icon">
              <i :style="{ backgroundImage: `url(${ detail.headUrl })` }"></i>
            </div>
          </div>
        </div>
        <div class="c-attendance-detail__name">{{ `${ detail.firstName } ${ detail.lastName }` }}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { formatToYYYYMMDD } from '@/utils/format.js'
import percentCircle from '@/components/PercentCircle.vue'

export default {
  name: 'attendance-details',
  props: ['date'],
  components: {
    percentCircle
  },
  data () {
    return {
      result: {
        arrived: 0,
        total: 0,
        percent: 0
      },
      description: '',
      array: []
    }
  },
computed: {
  ...mapState(['user']),
    ...mapGetters(['isAdmin', 'isHeadTeacher']),
    mode () {
      return this.$route.params.mode
    },
    isTeacher () {
      return this.mode === 'teacher'
    },
    isStudent () {
      return this.mode === 'student'
    },
    id () {
      return Number(this.$route.params.id)
    },
    isValid () {
      return this.isAdmin || this.isStudent && this.isHeadTeacher
    },
    url () {
      if (this.isValid) {
        if (this.isStudent) {
          return this.isAdmin ? 'admin/class/studentattendance' : 'teacher/class/studentattendance'
        } else if (this.isTeacher) {
          return 'admin/schoollevel/staff/attendancedetail'
        }
      }
      return ''
    }
  },
  methods: {
    analysisStudent (data) {
      this.result = {
        arrived: data.arrivedStudentClassTotal,
        total: data.studentClassTotal,
        percent: data.studentClassTotal > 0 ? data.arrivedStudentClassTotal / data.studentClassTotal : 0
      }
      let { gradeLevel, classId } = data
      this.description = this.$t('format.classInfo', {
        gradeLevel: gradeLevel,
        classId: classId
      })
      this.array = data.studentAttendanceInfo
    },
    analysisTeacher (data) {
      this.result = {
        arrived: data.arrivedStaffSchoolLevelTotal,
        total: data.staffSchoolLevelTotal,
        percent: data.staffSchoolLevelTotal > 0 ? data.arrivedStaffSchoolLevelTotal / data.staffSchoolLevelTotal : 0
      }
      this.description = data.schoolLevelName
      this.array = data.staffAttendanceInfoList
    },
    analysis (data) {
      if (this.isStudent) {
        this.analysisStudent(data)
      } else {
        this.analysisTeacher(data)
      }
    },
    viewSignInInformation (detail) {
      this.$router.push({ name: 'sign-in-information', params: { mode: this.mode, id: this.isStudent ? detail.idStudent : detail.idStaff, info: encodeURI(this.description) } })
    },
    fetch () {
      if (this.isValid) {
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              cycle: 60000
            },
            success: res => this.analysis(res.data)
          })
        }
        this.$httpPost.force(this.isStudent ? {
          token: this.user.token,
          idClass: this.id,
          queryDate: this.date
        } : {
          token: this.user.token,
          idSchoolLevel: this.id,
          queryDate: this.date
        }, {
          cycle: formatToYYYYMMDD(new Date()) === this.date ? 60000 : 0
        })
      }
    },
    reset () {
      this.result = {
        arrived: 0,
        total: 0,
        percent: 0
      }
      this.students = []
      this.fetch()
    }
  },
  watch: {
    date () {
      this.reset()
    }
  },
  created () {
    this.fetch()
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.c-attendance-details {
  display: flex;
  flex-direction: column;
  font-size: 0;
  &__percent {
    flex: none;
    padding-top: 24px;
  }
  &__list {
    flex: auto;
    overflow-x: hidden;
  }
}
%detail-status {
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
}
.c-attendance-detail {
  position: relative;
  display: inline-block;
  padding: 14px 5px;
  color: #000;
  text-align: center;
  &__avatar {
    position: relative;
    display: inline-block;
    line-height: 0;
  }
  &__name {
    width: 80px;
    padding: 8px 0;
    font-size: 12px;
    line-height: 1;
    white-space: nowrap;
    text-align: center;
    overflow: hidden;
  }
  &.is-nonarrival {
    opacity: 0.6;
  }
  &.is-manual {
    .c-attendance-detail__avatar::after {
      @extend %detail-status;
      @include bg-img-ratio('../../assets/attendance_manual', 39, 39);
    }
  }
  &.is-leave {
    .c-solid-avatar, .c-attendance-detail__name {
      opacity: 0.6;
    }
    .c-attendance-detail__avatar::after {
      @extend %detail-status;
      @include bg-img-ratio('../../assets/attendance_leave', 39, 39);
    }
  }
  &.is-recorded {
    .c-attendance-detail__avatar::after {
      @extend %detail-status;
      @include bg-img-ratio('../../assets/attendance_normal', 39, 39);
    }
  }
}
</style>
