<template>
  <navigation-wrapper :title="$t('home.menu.bracelet')">
    <div class="l-navigation__content has-padding">
      <ul>
        <li v-for="figure in figures" :key="figure.id" class="c-bracelet-item">
          <div class="c-bracelet-item__avatar c-avatar" :class="{ 'c-avatar--boy': figure.gender === 'Male' || figure.relationship === 1, 'c-avatar--girl': figure.gender === 'Female' || figure.relationship === 2, 'has-avatar': !!figure.headUrl }">
            <div v-if="!!figure.headUrl" class="c-avatar__icon">
              <i :style="{ backgroundImage: `url(${ figure.headUrl })` }"></i>
            </div>
          </div>
          <div class="c-bracelet-item__button o-button" :class="{ 't-adapt-button': figure.braceletOfBinding === 0, 'o-button--prominent': figure.braceletOfBinding === 1 }" @click="toggle(figure)">{{ figure.braceletOfBinding === 0 ? $t('bracelet.bind') : $t('bracelet.unbind') }}</div>
          <div class="c-bracelet-item__name">{{ figure.name }}</div>
        </li>
      </ul>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'bracelet',
  computed: {
    ...mapState(['figures']),
    ...mapGetters(['isStaff'])
  },
  methods: {
    toggle (figure) {
      if (figure.braceletOfBinding === 0) {
        this.$router.push({ name: 'scan', params: { id: figure.id } })
      } else {
        this.$router.push({ name: 'unbind', params: { id: figure.id } })
      }
    }
  }
}
</script>

<style lang="scss">
.c-bracelet-item {
  position: relative;
  padding: 35px 0 35px 80px;
  @include border-bottom-1px(#e6e6e6);
  &__avatar {
    position: absolute;
    top: 16px;
    left: 0;
    &.c-avatar {
      top: 12px;
    }
  }
  &__name {
    padding: 7px 0;
    margin-right: 90px;
    color: #404040;
    font-size: 16px;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
  }
  &__button {
    float: right;
    width: 80px;
    padding: 7px 0;
  }
}
</style>
