<template>
  <navigation-wrapper :title="$t('busMap.title')">
    <!-- <google-map class="l-navigation__content" :center="center" :zoom="7">
      <google-marker v-for="(m, index) in markers" :key="index" :position="m.position" :clickable="true" :draggable="true" @click="center=m.position"></google-marker>
    </google-map> -->
    <div class="l-navigation__content c-bus-map" ref="map"></div>
    <div class="c-bus">
      <div class="c-bus-info c-bus__info">
        <div class="c-bus-info__logo c-bus-logo">
          <div class="c-bus-logo__icon"></div>
        </div>
        <div class="c-bus-info__route">{{ routeNo }}</div>
        <div v-if="plateNo" class="c-bus-info__license">{{ plateNo }}</div>
      </div>
      <div class="c-bus__time">
        <div class="c-bus-time">
          <div class="c-bus-time__label">{{ $t('busMap.boarding') }}</div>
          <div class="c-bus-time__value">{{ boarding }}</div>
        </div>
        <div class="c-bus-time">
          <div class="c-bus-time__label">{{ $t('busMap.unloading') }}</div>
          <div class="c-bus-time__value">{{ unloading }}</div>
        </div>
      </div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
// import { Map as googleMap, Marker as googleMarker  } from 'vue2-google-maps'
// import markerwithlabel from 'markerwithlabel'

let hasLoaded = false
let googleMapScript = null
let success = null

function asyncLoadGoogleMap (cb) {
  if (!hasLoaded && !googleMapScript) {
    googleMapScript = document.createElement('script')
    googleMapScript.type = 'text/javascript'
    googleMapScript.src = 'http://ditu.google.cn/maps/api/js?&key=AIzaSyDjPJDlzhSHNACgzsLbaBZHjb2N9vwGt3s'
    // googleMapScript.src = 'https://maps.googleapis.com/maps/api/js?&key=AIzaSyDjPJDlzhSHNACgzsLbaBZHjb2N9vwGt3s'
    document.body.appendChild(googleMapScript)
    googleMapScript.onload = () => {
      console.log('load google-map success')
      hasLoaded = true
      googleMapScript = null
      success && success()
    }
    googleMapScript.onerror = () => {
      console.log('load google-map error')
      hasLoaded = false
      document.body.removeChild(googleMapScript)
      googleMapScript = null
    }
  }
  if (hasLoaded) {
    cb()
  } else {
    success = cb
  }
}

function cancelLoadGoogleMap () {
  success = null
  if (googleMapScript && googleMapScript.abort) {
    googleMapScript.abort()
  }
}


export default {
  name: 'bus-map',
  components: {
    // googleMap,
    // googleMarker
  },
  data () {
    return {
      map: null,
      loaded: false,
      googleMapLoaded: false,
      icon: './bus@2x.png',
      routeNo: '',
      plateNo: '',
      boarding: '',
      unloading: '',
      gaps: []
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
  },
  methods: {
    analysis (data) {
      this.loaded = true
      this.routeNo = `No.${ data.routeNo }`
      this.plateNo = data.plateNo
      this.boarding = data.boardingTimeStr
      this.unloading = data.unloadingTimeStr
      this.gaps = data.tripGps.map(val => {
        return {
          lat: Number(val.latitude),
          lng: Number(val.longitude)
        }
      })
      this.draw()
    },
    draw () {
      if (!this.$refs.map || !this.loaded || this.gaps.length === 0) {
        return
      }
      if (!this.googleMapLoaded) {
        asyncLoadGoogleMap(() => {
          this.googleMapLoaded = true
          this.draw()
        })
        return
      }
      // let MarkerWithLabel = markerwithlabel(google.maps)
      // //this.markers
      // [{
      //   position: { lng: 120.4, lat: 30.2 },
      //   orderId:'123456',
      //   name:'',
      //   show:false,
      // }, {
      //   position: {lng: 120.5, lat: 30.2},
      //   orderId:'654321',
      //   name:'',
      //   show:false,
      // }].map(item=>{
      //   let marker = new MarkerWithLabel({
      //     position: item.position,
      //     icon: './bus@2x.png', //
      //     draggable: false, //
      //     map: map,   //
      //     labelContent: item.name,    //label
      //     labelAnchor: new google.maps.Point(22, 0),    //label
      //     labelClass: 'labels', // the CSS class for the label
      //     labelStyle: { background:'#fff',padding:'5px' }
      //   })
      //   //
      //   let iw = new google.maps.InfoWindow({
      //     content: `<div>
      //               <p> ${item.orderId}</p>
      //               <p>${item.name}</p>
      //             </div>`})
      // })
      let latlngbounds = new google.maps.LatLngBounds()
      this.gaps.forEach(val => {
        latlngbounds.extend(val)
        return val
      })
      let center = latlngbounds.getCenter()
      if (!this.$map) {
        this.$map = new google.maps.Map(this.$refs.map, {
          // zoom: 15,
          center: center,
          mapTypeId: google.maps.MapTypeId.ROADMAP,
          disableDefaultUI: true,
          // draggable: false
        })
        this.$marker = new google.maps.Marker({
          position: this.gaps[this.gaps.length - 1],
          icon: './bus@2x.png',
          map: this.$map
        })
        this.$polyline = new google.maps.Polyline({
          path: this.gaps,
          geodesic: true,
          strokeColor: '#ff0000',
          strokeOpacity: 1.0,
          strokeWeight: 2,
          map: this.$map
        })
      } else {
        this.$map.setCenter(center)
        this.$marker.setPosition(this.gaps[this.gaps.length - 1])
        this.$polyline.setPath(this.gaps)
      }
      this.$map.fitBounds(latlngbounds)
      // points.map(val => {
      //   new google.maps.Marker({
      //     position: val,
      //     // icon: './bus@2x.png',
      //     map: this.map,
      //     style: { 'color': 'red' }
      //   })
      // })
      // new google.maps.Marker({
      //   position: points[points.length - 1],
      //   icon: './bus@2x.png',
      //   map: this.map
      // })
      // new google.maps.Polyline({
      //   path: points,
      //   geodesic: true,
      //   strokeColor: '#ff0000',
      //   strokeOpacity: 1.0,
      //   strokeWeight: 2,
      //   map: this.map
      // })
      // new google.maps.Polyline({
      //   path: points.slice(1),
      //   geodesic: true,
      //   strokeColor: '#0000ff',
      //   strokeOpacity: 1.0,
      //   strokeWeight: 2,
      //   map: this.map
      // })
    }
  },
  created () {
    this.$httpPost = this.$http.createPost(this.isStaff ? 'staff/bustrip' : 'parent/child/bustrip', {
      opts: {
        cycle: 60000
      },
      success: res => this.analysis(res.data)
    })
    this.$httpPost.request({
      token: this.user.token,
      idBus: this.$route.params.busId,
      checkDetailId: this.$route.params.id
    })
  },
  mounted () {
    this.draw()
  },
  beforeDestroy () {
    cancelLoadGoogleMap()
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.c-bus-map {
  position: relative;
  background-color: #f2f2f2;
}
.c-bus {
  display: flex;
  position: absolute;
  bottom: 15px;
  left: 15px;
  right: 15px;
  padding: 24px 14px 18px 16px;
  border-radius: 6px;
  background-color: #fff;
  &__info {
    flex: none;
    margin-right: 20px;
  }
  &__time {
    flex: 1;
  }
}
.c-bus-logo {
  display: inline-block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #fff;
  &__icon {
    display: inline-block;
    position: relative;
    top: 5px;
    left: 5px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-position: 50% 50%;
    background-size: 36px 31px;
    background-color: #e8e3e3;
    background-repeat: no-repeat;
    @include bg-img-ratio-normal('../../assets/bus2');
  }
}
.c-bus-info {
  position: relative;
  padding-top: 20px;
  min-width: 50px;
  &__logo {
    position: absolute;
    top: -38px;
    left: 50%;
    margin-left: -30px;
  }
  &__route {
    padding: 2px 0;
    color: rgba(#333, 0.6);
    font-size: 14px;
    text-align: center;
  }
  &__license {
    display: inline-block;
    padding: 5px;
    border-radius: 4px;
    color: rgba(#333, 0.6);
    font-size: 12px;
    background-color: #ffd240;
    text-align: center;
  }
}
.c-bus-time {
  display: flex;
  color: #333;
  font-size: 16px;
  font-weight: bold;
  &__time {
    flex: none;
  }
  &__value {
    flex: 1;
    text-align: right;
  }
  & + & {
    margin-top: 25px;
  }
}
</style>
