<template>
  <div class="p-home" @touchstart="touchstart" @touchmove="touchmove" @touchend="touchend" :class="{ 'is-touch': side.moving }">
    <side-menu class="p-home__side" :style="sideStyle"></side-menu>
    <safe-area-layout class="p-home__main t-adapt-bg" :class="{ 'is-touch': side.moving }" :style="mainStyle">
      <template slot="topbar">
        <div class="p-home__shortcut">
          <div class="p-home__icon_wrapper" @click="toggleSide">
            <i class="p-home__menu"></i>
          </div>
          <!-- <div class="p-home__icon_wrapper u-float-r" @click="scan">
            <i class="p-home__scan"></i>
          </div> -->
        </div>
      </template>
      <div class="p-home__content">
        <div class="p-home__header swiper-container" id="person-swiper">
          <div class="swiper-wrapper">
            <character-intro v-for="figure in figures" :key="figure.id" class="p-home__person swiper-slide" :person="figure" :connected="connected" :connecting="connecting"></character-intro>
          </div>
          <div class="swiper-pagination" id="person-pagination"></div>
        </div>
        <div v-if="toast" class="p-home__toast p-home-toast" :class="`p-home-toast--${ toast }`" @click="clickToast">
          {{ $t(`home.toast.${ toast }`) }}
          <div class="p-home-toast__icon" @click.stop="clickToastIcon"></div>
        </div>
        <div class="p-home__section">
          <div class="p-home-block">
            <div class="p-home-block__header" @click="toHistory">
              <span class="p-home-block__title">{{ $t('home.timeline.title') }}</span>
              <span class="p-home-block__tip">{{ today }}</span>
              <i v-if="this.records.length > 0" class="p-home-block__arrow"></i>
            </div>
            <div class="p-home-block__content">
              <div class="c-timeline p-home-timeline">
                <div v-for="(point, index) in timeline" :key="index" class="c-timeline__point">
                  <div class="c-timeline__header"></div>
                  <div class="c-timeline__tail"></div>
                  <div class="c-timeline__content" :class="`p-home-timeline__level${ index }`">
                    <div class="c-timeline__time">{{ point.time }}</div>
                    <div class="c-timeline__info">
                      <div class="c-timeline__row is-nowrap">{{ $t(`record.${ point.type }`) }}</div>
                      <div v-if="point.type === 3 || point.type === 5" class="c-timeline__row is-nowrap">
                        <div class="p-home-timeline__bus t-adapt-color" @click="toBusMap(point)">{{ $t('record.bus') }}</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <component v-if="canShowAttendance" class="p-home__section" :is="attendanceComponent"></component>
        <div class="p-home__section">
          <div class="p-home-block">
            <div tag="div" class="p-home-block__header" @click="toMotion">
              <span class="p-home-block__title">{{ $t('home.motion.title') }}</span>
              <i class="p-home-block__arrow"></i>
            </div>
            <div class="p-home-block__content">
              <div class="p-home-steps u-clearfix">
                <i class="p-home-steps__icon"></i>
                <div class="p-home-steps__tip">{{ $t('home.motion.tip') }}</div>
                <div class="p-home-steps__value">{{ steps }}<span class="p-home-steps__unit">{{ $t('home.motion.unit') }}</span></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <template slot="inde">
        <div v-show="mainX > 0" class="p-home__mask" :style="maskStyle" @click="showSide && toggleSide()" @touchmove.stop.prevent="touchmove"></div>
      </template>
    </safe-area-layout>
  </div>
</template>

<script>
import { mapState, mapActions, mapGetters } from 'vuex'
import Swiper from 'swiper'
import sideMenu from '@/views/side-menu/SideMenu.vue'
import characterIntro from '@/views/components/CharacterIntroHome.vue'
import homeAttendanceAdmin from '@/views/components/HomeAttendanceAdmin.vue'
import homeAttendanceTeacher from '@/views/components/HomeAttendanceTeacher.vue'
import * as SystemEvent from '@/code/system-event.js'

export default {
  name: 'home',
  components: {
    sideMenu,
    characterIntro,
    homeAttendanceAdmin,
    homeAttendanceTeacher
  },
  props: [],
  data () {
    return {
      showSide: false,
      records: [],
      index: 0,
      toast: '',
      date: new Date(),
      steps: 0,
      need: {},
      connected: null,
      connecting: 'null',
      timer: -1,
      clockTimer: -1,
      side: {
        date: null,
        lastTouch: 0,
        touch: false,
        moving: false
      },
      sideX: -50,
      mainX: 0
    }
  },
  computed: {
    ...mapState(['user', 'figures']),
    ...mapGetters(['isStaff', 'isAdmin', 'isHeadTeacher', 'getFigure']),
    today () {
      return this.$t('format.date', {
        week: this.$d(this.date, 'week'),
        month: this.$d(this.date, 'month'),
        day: this.date.getDate(),
        year: this.date.getFullYear()
      })
    },
    timeline () {
      let length = this.records.length
      let array
      switch (true) {
        case length > 3:
          array = this.records.slice(0, 3).map(this.transformRecord)
          break
        case length === 3:
          array = this.records.map(this.transformRecord)
          break
        default:
          array = this.records.map(this.transformRecord)
          for (let i = length; i < 3; i++) {
            array.push(this.records[i] || {
              type: 0,
              time: '--:--:--'
            })
          }
          break
      }
      return array
    },
    figure () {
      return this.figures.length > 0 ? this.figures[this.index] : null
    },
    id () {
      return this.figure ? this.figure.id : null
    },
    canShowAttendance () {
      return this.isAdmin || this.isHeadTeacher
    },
    attendanceComponent () {
      return this.isAdmin ? 'home-attendance-admin' : 'home-attendance-teacher'
    },
    sideStyle () {
      return {
        transform: `translateX(${ this.sideX }px)`
      }
    },
    mainStyle () {
      return {
        transform: `translateX(${ this.mainX }px)`
      }
    },
    maskStyle () {
      return {
        opacity: this.mainX / 295 * 0.4
      }
    }
  },
  methods: {
    ...mapActions(['setRuntimeMode']),
    transformRecord (record) {
      let dateTime = record.checkTimeStr.split(' ')
      return {
        type: record.type,
        date: dateTime[0],
        time: dateTime[1],
        busId: record.busId,
        detailid: record.checkDetailId
      }
    },
    checkBluetooth () {
      if (!this.toast || this.toast === 'bluetooth') {
        this.toast = this.$native.bluetooth ? null : 'bluetooth'
      }
    },
    checkFlag () {
      if (this.figure) {
        if (!this.need[this.id]) {
          this.need[this.id] = {
            bracelet: true,
            bus: true
          }
        }
        let flag = this.need[this.id]
        if (flag.bracelet && this.figure.braceletOfBinding === 0) {
          this.toast = 'bracelet'
          return
        }
        if (!this.isStaff && flag.bus && !this.figure.busstudentId) {
          this.toast = 'bus'
          return
        }
      }
      if (!this.$native.bluetooth) {
        this.toast = 'bluetooth'
        return
      }
      this.toast = null
    },
    connectBracelet (braceletMac) {
      if (this.connected) {
        this.$native.disConnectBracelet()
        this.connected = null
      }
      this.connecting = braceletMac
      this.$native.connectBracelet(braceletMac).then(result => {
        if (result) {
          this.connected = braceletMac
        } else {
          this.connected = null
        }
        this.connecting = null
      })
    },
    checkBracelet (soon) {
      clearTimeout(this.timer)
      if (this.figure && this.figure.braceletOfBinding === 1 && this.connecting !== 'null' && this.connected !== this.figure.braceletMac) {
        let braceletMac = this.figure.braceletMac
        if (soon) {
          this.connectBracelet(braceletMac)
        } else {
          this.timer = setTimeout(() => {
            this.connectBracelet(braceletMac)
          }, 2000)
        }
      }
    },
    refetch (clear) {
      this.checkFlag()
      if (!this.figure) {
        return
      }
      if (clear) {
        this.records = []
        this.steps = 0
        if (this.$recordsHttpPost) {
          this.$recordsHttpPost.cancel()
        }
        if (this.$stepHttpPost) {
          this.$stepHttpPost.cancel()
        }
      }
      this.checkBracelet()
      if (!this.$recordsHttpPost) {
        this.$recordsHttpPost = this.$http.createPost(this.isStaff ? 'staff/signinrecord' : 'parent/child/signinrecord', {
          opts: {
            cycle: 60000
          },
          success: res => this.records = res.data
        })
      }
      this.$recordsHttpPost.request(this.isStaff ? {
        token: this.figure.token,
        page: 1,
        pageSize: 3
      } : {
        token: this.user.token,
        idStudent: this.figure.id,
        page: 1,
        pageSize: 3
      })
      if (!this.$stepHttpPost) {
        this.$stepHttpPost = this.$http.createPost(this.isStaff ? 'staff/sport' : 'parent/child/sport', {
          opts: {
            retry: true
          },
          success: res => this.steps = res.data.steps
        })
      }
      this.$stepHttpPost.request(this.isStaff ? {
        token: this.figure.token
      } : {
        token: this.user.token,
        idStudent: this.figure.id
      })
    },
    updatePerson () {
      if (this.figures.length > 1 && !this.$personSwiper) {
        this.$personSwiper = new Swiper('#person-swiper', {
          // direction: 'vertical', // 
          loop: false, // 
          // 
          pagination: {
            el: '#person-pagination',
            longSwipesRatio: 0.3
          },
          on: {
            slideChangeTransitionStart: () => {
              this.$router.replace({ name: 'home', params: { index: this.$personSwiper.activeIndex } })
            }
          }
        })
        let index = Math.min(this.index, this.figures.length - 1)
        if (index > 0) {
          this.$personSwiper.slideTo(index, 0, false)
        }
      }
      this.refetch()
    },
    scan () {
      this.$native.scan()
    },
    clickToast () {
      switch (this.toast) {
        case 'bracelet':
          this.$router.push({ name: 'scan', params: { id: this.id } })
          break
        case 'bus':
          this.$router.push({ name: 'modify-bus', params: { id: this.id } })
          break
        case 'bluetooth':
          this.$router.push({ name: 'bluetooth' })
          break
        default:
          break
      }
    },
    clickToastIcon () {
      switch (this.toast) {
        case 'bracelet':
        case 'bus':
          this.need[this.id][this.toast] = false
          this.checkFlag()
          break
        case 'bluetooth':
          this.clickToast()
          break
        default:
          break
      }
    },
    toHistory () {
      if (this.figure) {
        this.$router.push({ name: 'history', params: { id: this.id } })
      }
    },
    toMotion () {
      if (this.figure) {
        this.$router.push({ name: 'motion', params: { id: this.id } })
      }
    },
    toBind () {
      this.$router.push({ name: 'scan', params: { id: this.id } })
    },
    toBusMap (point) {
      this.$router.push({ name: 'bus-map', params: { busId: point.busId, id: point.detailid } })
    },
    clock () {
      this.date = new Date()
    },
    ready () {
      if (this.$native.isReady) {
        this.$native.isBraceletConnected().then(result => {
          if (result.type) {
            this.connected = result.mac
          } else {
            this.connected = null
          }
          this.connecting = null
          this.checkBracelet(true)
        })
      }
    },
    checkStyle () {
      this.mainX = this.showSide ? 295 : 0
      this.sideX = this.showSide ? 0 : -50
    },
    toggleSide () {
      this.showSide = !this.showSide
      this.checkStyle()
    },
    clearTouchInfo () {
      this.side.touch = false
      this.side.moving = false
      this.side.lastTouch = null
      this.side.date = null
    },
    touchstart (e) {
      this.side.touch = true
      this.side.lastTouch = e.touches[0]
      this.side.date = Date.now()
    },
    touchmove (e) {
      if (!this.side.touch) {
        return
      }
      let touch = e.touches[0]
      let offsetX = touch.pageX - this.side.lastTouch.pageX
      if (!this.side.moving) {
        let offsetY = touch.pageY - this.side.lastTouch.pageY
        if (Math.abs(offsetY) > Math.abs(offsetX) || this.showSide && offsetX > 0 || !this.showSide && offsetX < 0) {
          this.clearTouchInfo()
          return
        }
      }
      e.preventDefault()
      this.side.moving = true
      this.side.lastTouch = touch
      this.mainX = Math.max(0, Math.min(295, this.mainX + offsetX))
      this.sideX = -50 + this.mainX / 6
    },
    touchend () {
      if (this.side.touch && this.side.moving) {
        let isFlick = Date.now() - this.side.date < 200
        this.clearTouchInfo()
        if (isFlick) {
          this.toggleSide()
        } else if (this.showSide && this.mainX < 200) {
          this.toggleSide()
        } else if (!this.showSide && this.mainX > 100) {
          this.toggleSide()
        } else {
          this.checkStyle()
        }
      }
    },
    braceletChanged (data) {
      if (data && data.macAddress) {
        this.connected = data.macAddress
      }
    }
  },
  watch: {
    figures () {
      this.$nextTick(() => {
        this.updatePerson()
      })
    }
  },
  beforeRouteUpdate (to, from, next) {
    let index = to.params.index || 0
    if (index !== this.index) {
      this.index = Math.min(index, this.figures.length - 1)
      this.refetch(true)
      if (this.$personSwiper && this.$personSwiper.realIndex !== index) {
        this.$personSwiper.slideTo(index, 0, false)
      }
    }
    next()
  },
  created () {
    this.ready()
    this.setRuntimeMode('normal')
    this.index = this.$route.params.index || 0
    this.$eventBus.$on(SystemEvent.BLUETOOTH_CHANGED, this.checkBluetooth)
    this.$eventBus.$on(SystemEvent.NATIVE_READY, this.ready)
    this.$eventBus.$on(SystemEvent.BRACELET_CONNECTED, this.braceletChanged)
    this.clockTimer = setInterval(() => {
      this.clock()
    }, 60000)
  },
  mounted () {
    this.updatePerson()
  },
  beforeDestroy () {
    this.$eventBus.$off(SystemEvent.BLUETOOTH_CHANGED, this.checkBluetooth)
    this.$eventBus.$off(SystemEvent.NATIVE_READY, this.ready)
    this.$eventBus.$off(SystemEvent.BRACELET_CONNECTED, this.braceletChanged)
    clearTimeout(this.timer)
    clearInterval(this.clockTimer)
    if (this.$personSwiper) {
      this.$personSwiper.destroy()
    }
    if (this.$recordsHttpPost) {
      this.$recordsHttpPost.cancel()
    }
    if (this.$stepHttpPost) {
      this.$stepHttpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-home {
  position: relative;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  &__side {
    transition: transform 0.2s;
  }
  &__main {
    position: relative;
    min-height: 100%;
    width: 100%;
    transition: transform 0.2s;
    background-color: #f2f2f2;
    background-size: 100% 262.5px;
    background-repeat: no-repeat;
    overflow: hidden;
  }
  &__shortcut {
    box-sizing: border-box;
    padding: 0 5px;
  }
  &__icon_wrapper {
    display: inline-block;
    padding: 10px;
  }
  &__menu {
    display: inline-block;
    @include bg-img-ratio('../../assets/icon_menu', 42, 32);
  }
  &__scan {
    display: inline-block;
    @include bg-img-ratio('../../assets/icon_scan', 42, 42);
  }
  &__header {
    padding-top: 10px;
    height: 140px;
    margin-bottom: 14px;
    .swiper-container {
      overflow: visible;
    }
    .swiper-wrapper {
      white-space: nowrap;
    }
    .swiper-pagination {
      bottom: 0;
    }
  }
  &__person {
    box-sizing: border-box;
    width: 100%;
    padding: 0 15px;
  }
  &__toast {
    margin: 0 15px 4px;
  }
  &__section {
    padding: 0 15px 12px;
  }
  .p-home-timeline {
    padding: 20px 0 26px;
    &__bus {
      display: inline-block;
      box-sizing: border-box;
      padding: 6px 8px;
      font-size: 12px;
      line-height: 1;
      white-space: nowrap;
      overflow: hidden;
      border-radius: 12px;
    }
    &__level1 {
      color: rgba(#333, 0.7);
    }
    &__level2 {
      color: rgba(#333, 0.3);
    }
  }
  &__mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #333;
    z-index: 99;
    transition: opacity 0.2s;
    &.is-touch {
      transition: none;
    }
  }
  .p-home-steps {
    padding: 15px 0 30px;
  }
  &.is-touch {
    .p-home__side, .p-home__main, .p-home__mask {
      transition: none;
    }
  }
}
.p-home-toast {
  position: relative;
  box-sizing: border-box;
  padding: 16px 50px 16px 45px;
  border-radius: 6px;
  color: #333;
  font-size: 12px;
  line-height: 1;
  white-space: nowrap;
  overflow: hidden;
  background-color: #fffaeb;
  &__icon {
    position: absolute;
    top: 0;
    right: 0;
    width: 43px;
    height: 100%;
    &::after {
      content: '';
      position: absolute;
      top: 15px;
      right: 15px;
      font-weight: bold;
      line-height: 1;
      @include bg-img-ratio('../../assets/cancel', 24, 24);
    }
  }
  &::after {
    content: '';
    position: absolute;
    top: 10px;
    right: 43px;
    height: 24px;
    width: 1px;
    background-color: #c1c1c1;
    text-align: center;
  }
  &--bracelet::before {
    content: '';
    position: absolute;
    top: 12px;
    left: 16px;
    @include bg-img-ratio('../../assets/no_bracelet', 39, 37);
  }
  &--bus::before {
    content: '';
    position: absolute;
    top: 12px;
    left: 16px;
    @include bg-img-ratio('../../assets/unbus', 41, 36);
  }
  &--bluetooth {
    &::before {
      content: '';
      position: absolute;
      top: 12px;
      left: 16px;
      @include bg-img-ratio('../../assets/no_bluetooth', 39, 37);
    }
    .p-home-toast__icon::after {
      content: '';
      position: absolute;
      top: 16px;
      right: 15px;
      @include bg-img-ratio('../../assets/more', 14, 24);
    }
  }
}
.p-home-block {
  background-color: #fff;
  padding: 0 15px;
  border-radius: 6px;
  &__header {
    position: relative;
    height: 50px;
    line-height: 48px;
    overflow: hidden;
    @include border-bottom-1px(#e6e6e6);
  }
  &__title {
    display: inline-block;
    color: #333333;
    font-size: 16px;
    font-weight: bold;
    line-height: 1;
  }
  &__tip {
    display: inline-block;
    background-color: #f6f8fb;
    padding: 4px 10px;
    margin-left: 10px;
    color: #a7a8aa;
    font-size: 12px;
    line-height: 1;
  }
  &__supplement {
    display: inline-block;
    margin-left: 2px;
    color: rgba(#333, 0.3);
    font-size: 12px;
    line-height: 1;
  }
  &__content {
    position: relative;
  }
  &__arrow {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -6px;
    @include bg-img-ratio('../../assets/more', 14, 24);
  }
}
.p-home-steps {
  &__icon {
    display: inline-block;
    @include bg-img-ratio('../../assets/step', 72, 72);
  }
  &__tip {
    display: inline-block;
    vertical-align: top;
    padding-left: 10px;
    color: rgba(#000, 0.4);
    font-size: 12px;
    line-height: 36px;
  }
  &__value {
    float: right;
    color: #000;
    font-size: 28px;
    line-height: 36px;
  }
  &__unit {
    padding-left: 4px;
    color: rgba(#000, 0.3);
    font-size: 10px;
  }
}
</style>
