<template>
  <div class="p-modify-mobile-confirm is-form">
    <div class="p-modify-mobile-confirm__current">{{ $t('mobile.current') }}</div>
    <div class="p-modify-mobile-confirm__mobile">{{ mobile }}&nbsp;</div>
    <div class="p-modify-mobile-confirm__warning">{{ $t('mobile.warning') }}</div>
    <div class="p-modify-mobile-confirm__tip">{{ $t('mobile.tip') }}</div>
    <router-link tag="div" class="o-button t-adapt-button" :to="{ name: 'pre-modify-mobile' }" replace>{{ $t('mobile.change') }}</router-link>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'

export default {
  name: 'modify-mobile-confirm',
  computed: {
    ...mapState(['user']),
    ...mapGetters(['phoneNumber']),
    mobile () {
      let mobile = this.phoneNumber
      if (mobile) {
        let length = mobile.length
        let start
        let middle
        let end
        if (length > 5) {
          start = mobile.substring(0, 2)
          middle = mobile.substring(2, length - 3).replace(/\d/g, '*')
          end = mobile.substring(length - 3)
          return start + middle + end
        } else if (length > 3) {
          start = mobile.substring(0, 1)
          middle = mobile.substring(1, length - 1).replace(/\d/g, '*')
          end = mobile.substring(length - 1)
          return start + middle + end
        } else {
          return mobile.replace(/\d/, '*')
        }
      }
      return ''
    }
  }
}
</script>

<style lang="scss">
.p-modify-mobile-confirm {
  &__current {
    color: #333;
    font-size: 16px;
    margin-bottom: 18px;
  }
  &__mobile {
    color: #333;
    font-size: 32px;
    margin-bottom: 16px;
  }
  &__warning {
    color: #fe6652;
    font-size: 14px;
    margin-bottom: 10px;
  }
  &__tip {
    color: #adadad;
    font-size: 12px;
    margin-bottom: 50px;
  }
}
</style>
