<template>
  <navigation-wrapper class="p-sign-in-history" :title="$t('history.title')">
    <div class="l-navigation__placeholder p-sign-in-history__target">{{ name }}</div>
    <div class="l-navigation__content u-overflow-hidden" ref="wrapper">
      <div>
        <ul ref="listWrapper">
          <li v-for="(recordSet, index) in records" :key="index">
            <div v-once class='p-sign-in-history__time'>{{ recordSet.label }}</div>
            <div class='p-sign-in-history__timeline'>
              <div v-for="(point, index) in recordSet.records" :key="index" class="c-timeline__point">
                <div class="c-timeline__header"></div>
                <div class="c-timeline__tail"></div>
                <div class="c-timeline__content">
                  <div class="c-timeline__time">{{ point.time }}</div>
                  <div class="c-timeline__info">
                    <div class="c-timeline__row">{{ $t(`record.${ point.type }`) }}</div>
                    <div v-if="point.type === 3 || point.type === 5" class="c-timeline__row">
                      <div class="p-sign-in-history__bus t-adapt-color" @click="toBusMap(point)">{{ $t('record.bus') }}</div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <div v-if="canPullUp" class="p-sign-in-history__pullup">{{ pullUpTip }}</div>
      </div>
      <loading :fetching="loading" :isError="isError" :retry="fetch"></loading>
      <div v-if="empty" class='p-sign-in-history__time'>{{ $t('common.empty') }}</div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { getRect } from '@/utils/dom.js'
import loading from '@/components/Loading.vue'
import BScroll from 'better-scroll'

export default {
  name: 'sign-in-history',
  components: {
    loading
  },
  data () {
    return {
      records: [],
      httpPost: null,
      page: 1,
      pageSize: 20,
      isPullUpLoad: false,
      error: false,
      hasMore: true,
      hasFetched: false
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff', 'getFigure']),
    url () {
      return this.isStaff ? 'staff/signinrecord' : 'parent/child/signinrecord'
    },
    id () {
      return Number(this.$route.params.id)
    },
    figure () {
      return this.getFigure(this.id)
    },
    name () {
      return this.figure ? this.figure.name : ''
    },
    fetching () {
      return this.httpPost && this.httpPost.isRequesting()
    },
    loading () {
      return this.fetching && !this.hasFetched
    },
    isError () {
      return this.error && !this.hasFetched
    },
    has () {
      return this.records.length > 0
    },
    empty () {
      return this.hasFetched && !this.has
    },
    canPullUp () {
      return this.hasFetched && this.has
    },
    pullUpTip () {
      return this.isPullUpLoad ? this.$t('pullUp.loading') : this.hasMore ? this.$t('pullUp.more') : this.$t('pullUp.finished')
    }
  },
  methods: {
    initScroll () {
      if (!this.$refs.wrapper || !this.has || this.$scroll) {
        return
      }
      this.$refs.listWrapper.style.minHeight = `${ getRect(this.$refs.wrapper).height + 1 }px`
      this.$scroll = new BScroll(this.$refs.wrapper, {
        scrollY: true,
        click: true,
        probeType: 2,
        pullUpLoad: {
          threshold: 10
        }
      })
      this.$scroll.on('pullingUp', () => {
        if (this.hasMore && !this.fetching) {
          this.isPullUpLoad = true
          this.fetch()
        } else {
          this.$scroll.finishPullUp()
        }
      })
    },
    pullUpFnished () {
      if (this.isPullUpLoad) {
        this.isPullUpLoad = false
        this.$scroll.finishPullUp()
      }
    },
    forceUpdate () {
      this.$nextTick(() => {
        if (this.$scroll) {
          this.pullUpFnished()
          this.$scroll.refresh()
        } else {
          this.initScroll()
        }
      })
    },
    checkDayStr (date, defaults) {
      let today = new Date()
      if (this.isSameDay(today, date)) {
        return this.$t('history.today')
      } else {
        today.setDate(today.getDate() - 1)
        if (this.isSameDay(today, date)) {
          return this.$t('history.yesterday')
        }
      }
      return defaults
    },
    isSameDay (date, date2) {
      return date.getFullYear() === date2.getFullYear() && date.getMonth() === date2.getMonth() && date.getDate() === date2.getDate()
    },
    add (records) {
      this.hasFetched = true
      if (records.length < this.pageSize) {
        this.hasMore = false
      }
      if (records.length === 0) {
        return
      }
      let info = this.records[this.records.length - 1]
      let record
      let dateTime
      let date
      for (let i = 0, j = records.length; i < j; i++) {
        record = records[i]
        dateTime = record.checkTimeStr.split(' ')
        date = new Date(record.checkTime)
        if (!info || !this.isSameDay(info.date, date)) {
          info = {
            label: this.checkDayStr(date, dateTime[0]),
            date: date,
            records: []
          }
          this.records.push(info)
        }
        info.records.push({
          type: record.type,
          date: dateTime[0],
          time: dateTime[1],
          busId: record.busId,
          detailid: record.checkDetailId
        })
      }
      this.page += 1
    },
    fetch () {
      this.httpPost.request(this.isStaff ? {
        token: this.user.token,
        page: this.page,
        pageSize: this.pageSize
      } : {
        token: this.user.token,
        idStudent: this.id,
        page: this.page,
        pageSize: this.pageSize
      })
    },
    toBusMap (point) {
      this.$router.push({ name: 'bus-map', params: { busId: point.busId, id: point.detailid } })
    }
  },
  watch: {
    fetching (nVal, oVal) {
      if (oVal && !nVal) {
        setTimeout(() => {
          this.forceUpdate()
        }, 20)
      }
    }
  },
  created () {
    this.httpPost = this.$http.createPost(this.url, {
      success: (res) => {
        this.error = false
        this.add(res.data)
      },
      fail: () => {
        this.error = true
      }
    })
    this.fetch()
  },
  mounted () {
    this.initScroll()
  },
  beforeDestroy () {
    if (this.httpPost) {
      this.httpPost.cancel()
    }
    if (this.$scroll) {
      this.$scroll.destroy()
    }
  }
}
</script>

<style lang="scss">
.p-sign-in-history {
  &__target {
    height: 30px;
    padding: 20px 15px 0;
    color: #404040;
    font-size: 16px;
    font-weight: bold;
    line-height: 1;
  }
  &__time {
    padding: 13px 15px;
    color: rgba(#333, 0.4);
    font-size: 14px;
    line-height: 1;
    background-color: #f2f2f2;
  }
  &__timeline {
    padding: 20px 15px;
  }
  &__bus {
    display: inline-block;
    box-sizing: border-box;
    padding: 6px 8px;
    color: #40ca7c;
    font-size: 12px;
    line-height: 1;
    border-radius: 12px;
    border: 1px solid #40ca7c;
  }
  &__pullup {
    padding: 15px 0;
    text-align: center;
    color: rgba(#333, 0.4);
    font-size: 10px;
    line-height: 1;
    background-color: #f2f2f2;
  }
}
</style>
