<template>
  <div class="c-home-attendance-teacher" :class="{ 'is-enough': attendances.length >= 2 }" @click="toClassAttendance">
    <div class="swiper-container" ref="swiper">
      <div class="swiper-wrapper">
        <attendance-block v-for="(attendance, index) in attendances" :key="index" class="swiper-slide c-attendance-block--single" :title="$t('attendanceType.student')" :supplement="$t('attendance.supplement')" :attendance="attendance">
          <div class="c-home-attendance-teacher__belong">{{ $t('format.classInfo', { gradeLevel: attendance.gradeLevel, classId: attendance.classId }) }}</div>
        </attendance-block>
      </div>
      <div class="swiper-pagination" ref="pagination"></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import Swiper from 'swiper'
import attendanceBlock from '@/views/components/AttendanceBlock.vue'

export default {
  name: 'attendance-block-teacher',
  components: {
    attendanceBlock
  },
  data () {
    return {
      attendances: []
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    analysis (data) {
      data.forEach(classInfo => {
        classInfo.arrived = classInfo.arrivedStudentClassTotal
        classInfo.total = classInfo.studentClassTotal
      })
      this.attendances = data
      this.$nextTick(() => {
        this.refreshSwiper()
      })
    },
    refreshSwiper () {
      let length = this.attendances.length
      if (this.$swiper) {
        if (this.$swiper.slides.length !== length) {
          if (length <= 1) {
            this.$swiper.destroy()
            this.$swiper = null
          } else {
            this.$swiper.update()
          }
        }
      } else if (length >= 2) {
        this.$swiper = new Swiper(this.$refs.swiper, {
          loop: false, // 
          spaceBetween: 5,
          // 
          pagination: {
            el: this.$refs.pagination,
            dynamicBullets: length > 5 ? true : false,
            longSwipesRatio: 0.3
          }
        })
      }
    },
    toClassAttendance () {
      if (this.attendances.length > 0) {
        let index = this.$swiper ? this.$swiper.realIndex : 0
        this.$router.push({ name: 'attendance-details', params: { mode: 'student', id: this.attendances[index].idClass } })
      }
    }
  },
  created () {
    this.$httpPost = this.$http.createPost('teacher/class/studentstat', {
      opts: {
        cycle: 60000
      },
      success: res => this.analysis(res.data)
    })
    this.$httpPost.request({
      token: this.user.token
    })
  },
  mounted () {
    this.refreshSwiper()
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
    if (this.$swiper) {
      this.$swiper.destroy()
    }
  }
}
</script>

<style lang="scss">
.c-home-attendance-teacher {
  &__belong {
    position: absolute;
    right: 24px;
    top: 8px;
    color: rgba(51, 51, 51, 0.5);
  }
  &.is-enough {
    margin-bottom: 30px;
    .swiper-pagination {
      display: block;
    }
  }
  .c-attendance-block {
    margin-right: 5px;
    &:last-child {
      margin-right: 0;
    }
  }
  .swiper-container {
    overflow: visible;
  }
  .swiper-wrapper {
    white-space: nowrap;
  }
  .swiper-pagination {
    bottom: -24px;
    display: none;
  }
}
</style>
