<template>
  <div class="c-weekly-calendar">
    <div class="c-weekly-calendar__date">{{ date }}</div>
    <div class="c-weekly-calendar__wrapper">
      <div class="c-weekly-calendar__slide swiper-container" ref="swiper">
        <div class="swiper-wrapper">
          <div v-for="(block, index) in blocks" :key="index" class="c-weekly-calendar__block swiper-slide">
            <div v-for="(item, index) in block.items" :key="index" :id="item.date" class="c-weekly-calendar__item" @click="choose(item.date)">
              <div class="c-weekly-calendar__label" :class="{ 'is-active': selected === item.date, 'is-disabled': item.disabled }">{{ item.label }}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="c-weekly-calendar__extend">
        <i class="c-weekly-calendar__icon" @click="toggle"></i>
      </div>
    </div>
  </div>
</template>

<script>
import Swiper from 'swiper'

export default {
  name: 'calendar-weekly',
  props: {
    defaultDate: {
      type: String,
      default: ''
    },
    range: {
      type: Boolean,
      default: true
    },
    rangeStart: {
      type: String,
      default: ''
    },
    rangeEnd: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      startDate: null,
      endDate: null,
      minDate: null,
      maxDate: null,
      index: 1,
      date: '',
      blocks: [],
      selected: null
    }
  },
  methods: {
    fillZero (number) {
      if (number < 10) {
        return `0${ number }`
      }
      return number
    },
    format (date) {
      return `${ date.getFullYear() }-${ this.fillZero(date.getMonth() + 1) }-${ this.fillZero(date.getDate()) }`
    },
    isValid (date) {
      if (!this.range) {
        return true
      }
      if ((!this.minDate || date >= this.minDate) && date < this.maxDate) {
        return true
      }
      return false
    },
    createBlock (date) {
      let items = []
      let temp
      for (let i = 0; i < 7; i++) {
        temp = this.offsetDay(date, i)
        items[i] = {
          label: temp.getDate(),
          date: this.format(temp),
          disabled: !this.isValid(temp)
        }
      }
      return {
        label: this.$t('format.year-month', {
          year: date.getFullYear(),
          month: this.$d(date, 'month')
        }),
        items
      }
    },
    addPreBlock () {
      if (!this.minDate || this.startDate > this.minDate) {
        this.startDate = this.offsetDay(this.startDate, -7)
        this.blocks.unshift(this.createBlock(this.startDate))
        return true
      }
      return false
    },
    addNextBlock () {
      if (!this.maxDate || this.endDate < this.maxDate) {
        this.blocks.push(this.createBlock(this.endDate))
        this.endDate = this.offsetDay(this.endDate, 7)
        return true
      }
      return false
    },
    refresh () {
      if (this.$swiper) {
        this.$nextTick(() => {
          this.$swiper.updateSlides()
          this.$swiper.slideTo(this.index, 0, false)
        })
      }
    },
    notify () {
      this.$emit('change', this.selected)
    },
    choose (date) {
      if (this.selected !== date && this.isValid(new Date(date))) {
        this.selected = date
        this.notify()
      }
    },
    toggle () {
      this.$emit('toggle', this.selected)
    },
    offsetDay (date, offset) {
      return new Date(date.getFullYear(), date.getMonth(), date.getDate() + offset)
    },
    init () {
      let hasChanged = !this.defaultDate
      if (this.range) {
        if (this.rangeStart) {
          this.minDate = new Date(this.rangeStart)
        } else {
          this.minDate = null
        }
        if (this.rangeEnd) {
          this.maxDate = new Date(this.rangeEnd)
        } else {
          this.maxDate = this.offsetDay(new Date(), 1)
        }
        if (this.minDate >= this.maxDate) {
          this.minDate = null
        }
        let selectedDay = this.defaultDate ? new Date(this.defaultDate) : new Date()
        if (selectedDay >= this.maxDate) {
          selectedDay = this.offsetDay(this.maxDate, -1)
          hasChanged = true
        } else if (this.minDate && selectedDay < this.minDate) {
          selectedDay = this.minDate
          hasChanged = true
        }
        this.selected = this.format(selectedDay)
      } else {
        this.minDate = null
        this.maxDate = null
        this.selected = this.defaultDate || this.format(new Date())
      }
      let date = new Date(this.selected)
      let week = date.getDay()
      if (week > 0) {
        this.startDate = this.offsetDay(date, -week)
      } else {
        this.startDate = date
      }
      this.endDate = this.offsetDay(this.startDate, 7)
      this.blocks = [this.createBlock(this.startDate)]
      this.index = 0
      if (this.addPreBlock()) {
        this.index = 1
      }
      this.addNextBlock()
      if (hasChanged) {
        this.notify()
      }
    }
  },
  watch: {
    defaultDate () {
      this.init()
      this.refresh()
    }
  },
  created () {
    this.init()
  },
  mounted () {
    this.$swiper = new Swiper(this.$refs.swiper, {
      preventInteractionOnTransition: true,
      longSwipesRatio: 0.3,
      initialSlide: this.index,
      on: {
        slideChangeTransitionEnd: () => {
          this.$nextTick(() => {
            this.date = this.blocks[this.$swiper.activeIndex].label
            if (this.$swiper.activeIndex === 0) {
              if (this.addPreBlock()) {
                this.refresh()
              }
            } else if (this.$swiper.activeIndex === this.blocks.length - 1) {
              if (this.addNextBlock()) {
                this.index += 1
                this.refresh()
              }
            } else {
              this.index = this.$swiper.activeIndex
            }
          })
        }
      }
    })
  },
  beforeDestroy () {
    if (this.$swiper) {
      this.$swiper.destroy()
    }
  }
}
</script>

<style lang="scss">
.c-weekly-calendar {
  position: relative;
  height: 60px;
  &__date {
    padding-left: 15px;
    color: rgba(#333, 0.3);
    font-size: 10px;
    line-height: 18px;
  }
  &__wrapper {
    display: flex;
  }
  &__slide {
    flex: auto;
  }
  &__block {
    display: flex;
    width: 100%;
    height: 40px;
  }
  &__item {
    flex: 1;
    text-align: center;
  }
  &__label {
    display: inline-block;
    width: 25px;
    height: 25px;
    color: #333;
    font-size: 12px;
    line-height: 25px;
    border-radius: 50%;
    &.is-active {
      color: #fff;
      background: linear-gradient(90deg, #4ca3f9, #4976f2);
    }
    &.is-disabled {
      opacity: 0.5;
    }
  }
  &__extend {
    flex: none;
    width: 60px;
    text-align: center;
    & > i {
      display: inline-block;
      @include bg-img-ratio('./calendar', 41, 43);
    }
  }
}
</style>
