<template>
  <safe-area-layout class="p-login">
    <template slot="bg">
      <div class="p-login__bg" :class="{ 'p-login__bg--logo': isHomeMode, 'p-login__bg--sign': !isHomeMode }"></div>
    </template>
    <div class="l-form t-white p-login__wrapper">
      <transition name="move-up" mode="out-in">
        <div v-if="showMode === 1" class="p-login__content p-login__content--login" key="login">
          <div class="p-login__1st p-login__logo"><div class="p-login__logo_icon" @click="openBackDoor"></div></div>
          <div class="p-login__2nd p-login__school">C D N I S</div>
          <div class="p-login__3rd p-login__tip">{{ $t('welcome') }}</div>
          <router-link class="p-login__4th p-login__button" :to="{ params: { mode: 'parent' } }" replace>{{ $t('login.sign') }}</router-link>
        </div>
        <div v-if="showMode === 2" class="p-login__content p-login__content--sign" key="sign">
          <div class="p-login__1st p-login__header">
            <router-link class="p-login__back" :to="{ name: 'login' }" replace></router-link>
          </div>
          <div class="p-login__1st p-login__nav">
            <router-link class="o-fixed-tab--white" :class="{ 'is-active': isParentMode }" :to="{ params: { mode: 'parent' } }" replace>{{ $t('login.parents') }}</router-link>
            <router-link class="o-fixed-tab--white" :class="{ 'is-active': isStaffMode }" :to="{ params: { mode: 'staff' } }" replace>{{ $t('login.teachers') }}</router-link>
          </div>
          <div v-if="isStaffMode">
            <form-field class="p-login__2nd" :label="$t('login.account')" :placeholder="$t('login.accountPlaceholder')" :error="accountError" v-model="account"></form-field>
            <form-field class="p-login__3rd" type="password" :label="$t('common.pwd')" :error="pwdError" v-model="password"></form-field>
          </div>
          <div v-else>
            <form-field class="p-login__2nd c-form-field--select" :label="$t('sms.phone')" :placeholder="$t('login.phonePlaceholder')" :error="accountError" v-model="mobile">
              <area-code @change="pick"></area-code>
            </form-field>
            <form-field v-if="usePwd" class="p-login__3rd" type="password" :label="$t('common.pwd')" :error="pwdError" v-model="password"></form-field>
            <form-field v-else class="p-login__3rd c-form-field--button" :label="$t('sms.vCode')" :error="pwdError" v-model="vCode">
              <sms :mobile="mobile" :areaCode="areaCode"></sms>
            </form-field>
          </div>
          <div class="p-login__4th u-clearfix">
            <router-link v-if="usePwd" class="p-login__forget u-underline" :to="{ name: 'pre-reset-pwd', params: { mode: mode } }">{{ $t('login.forget') }}</router-link>
            <div class="o-button o-button--submit" :class="{ 'is-active': isValid }" @click="sign">{{ $t('login.sign') }}</div>
          </div>
          <div v-if="isParentMode" class="p-login__4th u-align-c p-login__toggle-tip">
            <div class="u-underline" @click="toggleLoginMode">{{ toggleTip }}</div>
          </div>
        </div>
      </transition>
    </div>
  </safe-area-layout>
</template>

<script>
import { mapActions } from 'vuex'
import * as Types from '@/store/mutation-types.js'
import * as RetCode from '@/code/return-code.js'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'
import sms from '@/widgets/Sms.vue'

let LoginState = {
  HOME: 'home',
  PARENT: 'parent',
  STAFF: 'staff'
}

export default {
  name: 'login',
  components: {
    formField,
    areaCode,
    sms,
  },
  data () {
    return {
      showMode: '',
      mode: LoginState.HOME,
      requestMode: LoginState.HOME,
      signMode: window.localStorage.getItem('hasLoggedIn') === 'true' ? 0 : 1, // 0 password, 1 vcode
      areaCode: '',
      mobile: '',
      vCode: '',
      account: '',
      password: '',
      user: null,
      accountErrorInfo: null,
      pwdErrorInfo: null,
      clickCount: 0
    }
  },
  computed: {
    isParentMode () {
      return this.mode === LoginState.PARENT
    },
    isStaffMode () {
      return this.mode === LoginState.STAFF
    },
    isHomeMode () {
      return this.mode === LoginState.HOME
    },
    usePwd () {
      return this.signMode === 0 || this.mode === LoginState.STAFF
    },
    toggleTip () {
      return this.usePwd ? this.$t('login.useVCode') : this.$t('login.usePwd')
    },
    mobileVal () {
      return this.areaCode && this.mobile ? this.areaCode + this.mobile : null
    },
    isValid () {
      return this.usePwd ? (this.isParentMode ? this.mobileVal : this.account) && this.password : this.mobileVal && this.vCode
    },
    accountError () {
      return this.accountErrorInfo ? this.$lang.code[this.accountErrorInfo.code] || this.accountErrorInfo.message : ''
    },
    pwdError () {
      return this.pwdErrorInfo ? this.$lang.code[this.pwdErrorInfo.code] || this.pwdErrorInfo.message : ''
    }
  },
  methods: {
    ...mapActions(['setRuntimeMode', 'saveUser']),
    refreshMode () {
      this.$nextTick(() => {
        this.showMode = this.isHomeMode ? 1 : 2
      })
    },
    setMode (mode) {
      switch (mode) {
        case LoginState.PARENT:
          this.$httpPost && this.$httpPost.setUrl('parent/login')
          break
        case LoginState.STAFF:
          this.$httpPost && this.$httpPost.setUrl('staff/login')
          break
        default:
          mode =LoginState.HOME
          break
      }
      if (this.mode !== mode) {
        switch (this.mode) {
          case LoginState.PARENT:
            this.mobile = ''
            this.vCode = ''
            this.password = ''
            break
          case LoginState.STAFF:
            this.account = ''
            this.password = ''
            break
          default:
            break
        }
        this.stopLogin()
        this.mode = mode
      }
      this.refreshMode()
    },
    toggleLoginMode () {
      if (this.usePwd) {
        this.password = ''
      } else {
        this.vCode = ''
      }
      this.stopLogin()
      this.signMode = this.signMode === 0 ? 1 : 0
    },
    clearError () {
      this.accountErrorInfo = null
      this.pwdErrorInfo = null
    },
    stopLogin () {
      this.clearError()
      if (this.$httpPost) {
        this.$httpPost.cancel()
      }
      if (this.$infoHttpPost) {
        this.$infoHttpPost.cancel()
      }
    },
    toSetPassword (mode, token) {
      this.$router.replace({ name: 'set-pwd', params: { mode, token } })
    },
    changeRuntimmeMode (isBoot) {
      this.setRuntimeMode(isBoot ? 'boot' : 'normal')
    },
    afterSign (userInfo) {
      let user = Object.assign(this.$user, userInfo)
      let mode = this.requestMode
      user.mode = mode
      this.$store.dispatch(Types.LOGIN, user)
      if (user.hasPasswordSetted === 0) {
        this.changeRuntimmeMode('boot')
        this.toSetPassword(mode, user.token)
      } else {
        this.changeRuntimmeMode('normal')
        this.saveUser()
        this.$router.replace({ name: 'home', params: { mode } })
      }
    },
    updateInfo (token) {
      if (!this.$infoHttpPost) {
        this.$infoHttpPost = this.$http.createPost(this.isParentMode ? 'parent/info' : 'staff/info', {
          opts: {
            lock: true
          },
          success: res => {
            this.afterSign(res.data)
          }
        })
      }
      this.$infoHttpPost.request({
        token
      })
    },
    sign () {
      if (this.isValid) {
        this.clearError()
        this.requestMode = this.mode
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.isParentMode ? 'parent/login' : 'staff/login', {
            opts: {
              lock: true
            },
            success: res => {
              this.$user = res.data
              this.updateInfo(res.data.token)
            },
            fail: error => {
              switch (error.code) {
                case RetCode.ERROR_PARENT_ACCOUNT_NOT_EXIST:
                case RetCode.ERROR_ACCOUNT_NOT_EXIST:
                case RetCode.ERROR_ACCOUNT_NOT_EXIST2:
                  this.accountErrorInfo = {
                    code: error.code,
                    message: error.message
                  }
                  return true
                case RetCode.ERROR_PARENT_PWD_INCORRECT:
                case RetCode.ERROR_VCODE_UNSET:
                case RetCode.ERROR_VCODE_INCORRECT:
                case RetCode.ERROR_PWD_INCORRECT:
                  this.pwdErrorInfo = {
                    code: error.code,
                    message: error.message
                  }
                  return true
                default:
                  return false
              }
            }
          })
        }
        switch (this.mode) {
          case LoginState.PARENT:
            this.$httpPost.request({
              account: this.mobileVal,
              passCode: this.usePwd ? this.$md5(this.password) : this.vCode,
              type: this.usePwd ? 1 : 2
            })
            break
          case LoginState.STAFF:
            this.$httpPost.request({
              account: this.account,
              password: this.$md5(this.password)
            })
            break
          default:
            break
        }
      }
    },
    pick (areaCode) {
      this.areaCode = areaCode
    },
    openBackDoor () {
      this.clickCount += 1
      if (this.clickCount === 5) {
        this.$router.push({ name: 'backdoor' })
      }
    }
  },
  beforeRouteUpdate (to, from, next) {
    this.setMode(to.params.mode)
    this.clickCount = 0
    next()
  },
  created () {
    this.$nextTick(() => {
      this.setMode(this.$route.params.mode)
    })
  },
  beforeDestroy () {
    this.stopLogin()
  }
}
</script>

<style lang="scss">
@import './theme/white.scss';

.p-login {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  @include bg-img-ratio-normal('../../assets/bg');
  &__wrapper {
    position: relative;
    flex: 1;
  }
  &__content {
    padding-bottom: 70px;
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transition: background-color 0.8s ease-out;
  }
  &__bg--logo {
    background-color: rgba(#000,  0.2);
  }
  &__bg--sign {
    background-color: rgba(#000,  0.7);
  }
  &__logo {
    padding: 90px 0 15px;
    text-align: center;
  }
  &__logo_icon {
    display: inline-block;
    @include bg-img-ratio('../../assets/logo', 150, 150);
  }
  &__school {
    color: #fff;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
  }
  &__tip {
    padding: 15px 25px 150px;
    color: #fff;
    font-size: 11px;
    font-weight: bold;
    line-height: 18px;
    text-align: center;
  }
  &__button {
    position: absolute;
    bottom: 76px;
    left: 25px;
    right: 25px;
    background-color: #fff;
    box-sizing: border-box;
    padding: 17px 23px;
    border-radius: 25px;
    text-align: center;
    color: #333;
    font-size: 16px;
    line-height: 1;
  }
  &__header {
    position: relative;
    height: 68px;
  }
  &__back {
    position: absolute;
    top: 14px;
    left: -8px;
    padding: 8px;
    width: 12px;
    height: 12px;
    &::after {
      content: '';
      display: inline-block;
      position: absolute;
      top: 8px;
      left: 8px;
      @include bg-img-ratio('../../assets/cancel2', 24, 24);
    }
  }
  &__nav {
    margin-bottom: 40px;
    text-align: center;
    .o-fixed-tab--white + .o-fixed-tab--white {
      margin-left: 115px;
    }
  }
  &__forget {
    float: left;
    line-height: 50px;
    color: $t-white__c-form-field;
    font-size: 13px;
  }
  &__toggle-tip {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20px;
    color: $t-white__c-form-field;
    font-size: 12px;
  }
  .l-form {
    padding-top: 0;
  }
  .o-button--submit {
    float: right;
  }

  .move-up-enter-active {
    animation: delay 0.6s ease-out both;
    .p-login__1st {
      animation: move-up 0.2s ease-out both;
    }
    .p-login__2nd {
      animation: move-up 0.2s 0.1s ease-out both;
    }
    .p-login__3rd {
      animation: move-up 0.2s 0.2s ease-out both;
    }
    .p-login__4th {
      animation: opacity 0.2s 0.3s ease-in both;
    }
  }
  .move-up-leave-active {
    animation: delay 0.6s ease-out both;
    .p-login__1st {
      animation: move-up-leave 0.2s ease-in both;
    }
    .p-login__2nd {
      animation: move-up-leave 0.2s 0.1s ease-in both;
    }
    .p-login__3rd {
      animation: move-up-leave 0.2s 0.2s ease-in both;
    }
    .p-login__4th {
      animation: move-up-leave 0.2s 0.3s ease-in both;
    }
  }
}

@keyframes move-up {
  0% {
    opacity: 0;
    transform: translate3d(0, 20px, 0)
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
  }
}

@keyframes move-up-leave {
  100% {
    opacity: 0;
    transform: translate3d(0, -20px, 0)
  }
}

@keyframes opacity-leave {
  100% {
    opacity: 0;
  }
}
</style>
