<template>
  <div class="l-form c-modify">
    <div class="l-form__title">{{ $t('resetPwd.title') }}</div>
    <form-field class="c-form-field--select" :label="$t('sms.phone')" v-model="mobile">
      <area-code @change="pick"></area-code>
    </form-field>
    <form-field class="c-form-field--button" :label="$t('sms.vCode')" :error="vCodeError" v-model="vCode">
      <sms :mobile="mobile" :areaCode="areaCode"></sms>
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="next">{{ $t('common.next') }}</div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import * as RetCode from '@/code/return-code.js'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'
import sms from '@/widgets/Sms.vue'

export default {
  name: 'pre-set-pwd',
  components: {
    formField,
    areaCode,
    sms
  },
  data () {
    return {
      areaCode: '',
      mobile: '',
      vCode: '',
      vCodeErrorInfo: null
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      return this.isStaff ? 'staff/mobile/checkvcode' : 'staff/mobile/checkvcode'
    },
    mobileVal () {
      return this.areaCode && this.mobile ? this.areaCode + this.mobile : null
    },
    isValid () {
      return this.mobileVal && this.vCode
    },
    vCodeError () {
      return this.vCodeErrorInfo ? this.$lang.code[this.vCodeErrorInfo.code] || this.vCodeErrorInfo.message : ''
    }
  },
  methods: {
    next () {
      if (this.isValid) {
        this.vCodeErrorInfo = null
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: () => {
              this.$router.replace({ name: 'modify-pwd' })
            },
            fail: error => {
              switch (error.code) {
                case RetCode.ERROR_VCODE_UNSET:
                case RetCode.ERROR_VCODE_INCORRECT:
                  this.vCodeErrorInfo = {
                    code: error.code,
                    message: error.message
                  }
                  return true
                default:
                  return false
              }
            }
          })
        }
        this.$httpPost.request({
          token: this.user.token,
          mobile: this.mobileVal,
          vCode: this.vCode
        })
      }
    },
    pick (areaCode) {
      this.areaCode = areaCode
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
