<template>
  <div class="c-form-field" :class="{ 'is-invalid': !!error, 'is-active': isFocused }">
    <div class="c-form-field__label">{{ label }}</div>
    <div class="c-form-field__input_wrapper">
      <input :type="type" class="c-form-field__input" :placeholder="placeholder" :value="value"
        @input="$emit('input', $event.target.value)" @focus="isFocused = true" @blur="blur" />
      <slot></slot>
    </div>
    <div class="c-form-field__tip">{{ text }}</div>
  </div>
</template>

<script>
export default {
  name: 'form-field',
  props: {
    type: {
      type: String,
      default: 'text'
    },
    label: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    error: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      isFocused: false
    }
  },
  computed: {
    text () {
      return this.error || this.tip
    }
  },
  methods: {
    blur () {
      this.isFocused = false
      this.$emit('blur')
    }
  }
}
</script>

<style lang="scss">
$c-form-field: #333 !default;
$c-form-field__label: rgba(#333, 0.5) !default;
$c-form-field__border: rgba(#333, 0.1) !default;
$c-form-field__placeholder: rgba(#333, 0.3) !default;
$c-form-field__tip: rgba(#333, 0.3) !default;
$c-form-field__error: #ff6775 !default;
$c-form-field__button: rgba(#333, 0.5) !default;
$c-form-field__button_border: rgba(#333, 0.1) !default;
$c-form-field__button--active: rgba(#333, 0.5) !default;
$c-form-field__button_border--active: rgba(#333, 0.1) !default;

.c-form-field {
  width: 100%;
  &__label {
    color: $c-form-field__label;
    font-size: 13px;
  }
  &__input_wrapper {
    position: relative;
    width: 100%;
    @include border-bottom-1px($c-form-field__border);
  }
  &__input {
    box-sizing: border-box;
    padding: 14px 0 10px;
    border: none;
    outline: none;
    width: 100%;
    background-color: transparent;
    color: $c-form-field;
    font-size: 24px;
    line-height: 1;
    &::-webkit-input-placeholder {
      color: $c-form-field__placeholder;
      font-size: 19px;
    }
  }
  &__input[type='password'] {
    letter-spacing: 4px;
  }
  &__tip {
    padding: 10px 0;
    height: 26px;
    color: $c-form-field__tip;
    font-size: 12px;
    line-height: 14px;
    text-align: left;
  }
  &.is-active {
    .c-form-field__input_wrapper::after {
      border-color: $c-form-field;
      border-width: 2px;
    }
    .c-form-field__label {
      color: $c-form-field;
    }
    .c-form-field__input {
      color: $c-form-field;
    }
  }
  &.is-invalid {
    .c-form-field__input_wrapper::after {
      border-color: $c-form-field__error;
      border-width: 2px;
    }
    .c-form-field__input {
      color: $c-form-field__error;
    }
    .c-form-field__tip {
      color: $c-form-field__error;
    }
  }
}
.c-form-field--select {
  position: relative;
  .c-form-field__input {
    padding-left: 86px;
  }
  .c-form-field__button {
    position: absolute;
    top: 15px;
    left: 0;
    width: 86px;
    color: $c-form-field;
    font-size: 24px;
    height: 24px;
    line-height: 1;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    &::after {
      content: '';
      position: absolute;
      top: 8px;
      right: 6px;
      width: 0;
      height: 0;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: 6px solid $c-form-field;
      border-top: none;
    }
    &.is-active::after {
      border-top: 6px solid $c-form-field;
      border-bottom: none;
    }
  }
}
.c-form-field--button {
  position: relative;
  .c-form-field__input {
    padding-right: 100px;
  }
  .c-form-field__button {
    font-family: PingFang SC;
    position: absolute;
    right: 10px;
    bottom: 12px;
    box-sizing: border-box;
    min-width: 80px;
    padding: 5px;
    line-height: 1;
    color: $c-form-field__button;
    font-size: 12px;
    border-radius: 12px;
    border: 1px solid $c-form-field__button_border;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    &.is-active {
      color: $c-form-field__button--active;
      border-color: $c-form-field__button_border--active;
    }
  }
}
.c-form-field--pwd {
  position: relative;
  &.is-valid {
    .c-form-field__input_wrapper::before {
      content: '';
      position: absolute;
      top: 18px;
      right: 8px;
      @include bg-img-ratio('./set_right', 35, 36);
    }
    .c-form-field__input {
      padding-right: 30px;
    }
  }
}
</style>
