<template>
  <navigation-wrapper>
    <div class="l-navigation__content l-form">
      <div class="u-align-c">
        <div class="p-about__logo"></div>
      </div>
      <div class="p-about__app">{{ $t('about.app') }}</div>
      <div class="p-about__version">{{ version }}</div>
      <div class="p-about__content">
        <div class="p-about__info">1. Contact who, and how if people hava problem with the App or bracelet;</div>
        <div class="p-about__info">2. Where (who and how or may be how much) to register the bracelet;</div>
        <div class="p-about__info">3. In addition to contact person should create a Appinfo@cdnis.edu.hk email address as a genneral email contact point for people to ask or comment</div>
      </div>
    </div>
  </navigation-wrapper>
</template>

<script>
import * as SystemEvent from '@/code/system-event.js'

export default {
  name: 'about',
  data () {
    return {
      version: ''
    }
  },
  methods: {
    getVersion () {
      this.$native.getNativeAPPVer().then(version => {
        this.version = `${ this.$t('about.version') } ${ version }`
      })
    }
  },
  created () {
    this.$eventBus.$on(SystemEvent.NATIVE_READY, this.getVersion)
    this.getVersion()
  },
  beforeDestroy () {
    this.$eventBus.$off(SystemEvent.NATIVE_READY, this.getVersion)
  }
}
</script>

<style lang="scss">
.p-about {
  &__logo {
    display: inline-block;
    @include bg-img-ratio('../../assets/logo', 150, 150);
  }
  &__app {
    margin-top: 14px;
    color: #333;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
  }
  &__version {
    margin-top: 15px;
    text-align: center;
  }
  &__content {
    margin-top: 65px;
    padding-bottom: 10px;
    color: #333;
    font-size: 12px;
    text-align: left;
  }
  &__info {
    color: #333;
    font-size: 12px;
    line-height: 2;
    & + & {
      margin-top: 28px;
    }
  }
}
</style>
