<template>
  <div class="c-attendance-block">
    <div class="c-attendance-block__wrapper">
      <div class="c-attendance-block__header">
        <span class="c-attendance-block__title">{{ title || '' }}</span>
        <span class="c-attendance-block__supplement">{{ supplement }}</span>
      </div>
      <div class="c-attendance-block__content">
        <div class="c-attendance-block__tip">{{ $t('attendance.arrived') }}</div>
        <div class="c-attendance-block__percent"><span class="c-attendance-block__arrived">{{ arrived }}</span>/{{ total }}</div>
        <div class="o-progress">
          <div class="o-pregress__inner" :style="{ width: `${ precent }%` }"></div>
        </div>
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'attendance-block',
  props: ['title', 'attendance', 'supplement'],
  computed: {
    arrived () {
      return this.attendance ? this.attendance.arrived : 0
    },
    total () {
      return this.attendance ? this.attendance.total : 0
    },
    precent () {
      return this.total > 0 ? this.arrived / this.total * 100 : 0
    }
  },
  methods: {
    click () {
      this.$emit('click', this.attendance)
    }
  }
}
</script>

<style lang="scss">
.c-attendance-block {
  display: inline-block;
  box-sizing: border-box;
  width: 100%;
  &__wrapper {
    background-color: #fff;
    padding: 0 15px 30px;
  }
  &__header {
    position: relative;
    height: 50px;
    line-height: 48px;
    margin-bottom: 10px;
    white-space: normal;
    overflow: hidden;
    @include border-bottom-1px(#e6e6e6);
  }
  &__title {
    display: inline-block;
    color: #333333;
    font-size: 16px;
    font-weight: bold;
    line-height: 1;
  }
  &__supplement {
    display: inline-block;
    margin-left: 2px;
    color: rgba(#333, 0.3);
    font-size: 12px;
    line-height: 1;
  }
  &__content {
    position: relative;
  }
  &__tip {
    margin-bottom: 10px;
    color: rgba(51, 51, 51, 0.3);
    font-size: 10px;
    line-height: 1;
  }
  &__percent {
    padding-bottom: 18px;
    vertical-align: bottom;
    color: rgba(#333, 0.3);
    font-size: 21px;
    line-height: 1;
  }
  &__arrived {
    color: #4978f3;
    font-size: 42px;
  }
  &--single {
    .c-attendance-block__wrapper {
      border-radius: 6px;
    }
  }
}
</style>
