import { log } from '@/utils/log.js'
import EventBus from '@/utils/event-bus.js'
import * as SystemEvent from '@/code/system-event.js'

let hasNative = false

let api = null

function callHandler (funcName, data) {
  if (hasNative) {
    let dataInfo = data ? JSON.stringify(data) : 'null'
    log(`call native ${ funcName }: ${ dataInfo }`)
    return new Promise(resolve => {
      api.callHandler(funcName, data, function (res) {
        log(`native ${ funcName }: ${ res }`)
        resolve(res)
      })
    })
  } else {
    return Promise.reject({ code: -1 })
  }
}

let readyCb = []

const JsNativeApi = {
  _bluetooth: 'waiting',
  get bluetooth () {
    if (this._bluetooth === 'waiting') {
      return true
    } else {
      return this._bluetooth === 'open'
    }
  },
  get bluetoothState () {
    return this._bluetooth
  },
  get isReady () {
    return hasNative
  },
  ready () {
    if (hasNative) {
      return Promise.resolve()
    } else {
      return new Promise(resolve => {
        readyCb.push(resolve)
      })
    }
  },
  getNativeOS () {
    let os = window.localStorage.getItem('os')
    if (os) {
      return Promise.resolve(os)
    } else {
      return callHandler('getNativeOS', null).then(os => {
        window.localStorage.setItem('os', os)
        return os
      })
    }
  },
  getNativeAPPVer () {
    return callHandler('getNativeAPPVer', null)
  },
  openSysSetting () {
    return callHandler('openSysSetting', null).then(result => {
      return result === 'true' ? true : false
    })
  },
  scan () {
    return callHandler('scanQRCode', null).then(result => {
      return JSON.parse(result)
    })
  },
  ringUp (phoneNumber) {
    if (phoneNumber) {
      return callHandler('ringUp', {
        phoneNumber
      }).then(result => {
        return result === 'true' ? true : false
      })
    } else {
      return Promise.reject({ code: 101 })
    }
  },
  isBraceletConnected () {
    return callHandler('isBraceletConnected', null).then(result => {
      return JSON.parse(result)
    })
  },
  connectBracelet (mac) {
    return callHandler('connectBracelet', {
      mac
    }).then(result => {
      return result === 'true' ? true : false
    })
  },
  disConnectBracelet () {
    return callHandler('disConnectBracelet', null).then(result => {
      return result === 'true' ? true : false
    })
  },
  unbindBracelet (mac) {
    return callHandler('unbindBracelet', {
      mac
    }).then(result => {
      return result === 'true' ? true : false
    })
  }
}

function emit (event, payload) {
  EventBus.$emit(event, payload)
}


const NativeEvent = {
  BLUETOOTH_OPEN: 102,
  BLUETOOTH_CLOSE: 103,
  BRACELET_CONNECTED: 200,
  BRACELET_DISCONNECT: 201
}

const EventMap = {
  [NativeEvent.BLUETOOTH_OPEN]: SystemEvent.BLUETOOTH_OPEN,
  [NativeEvent.BLUETOOTH_CLOSE]: SystemEvent.BLUETOOTH_CLOSE,
  [NativeEvent.BRACELET_CONNECTED]: SystemEvent.BRACELET_CONNECTED,
  [NativeEvent.BRACELET_DISCONNECT]: SystemEvent.BRACELET_DISCONNECT
}

function analysis (event, data = null) {
  let eventName = EventMap[event]
  if (eventName) {
    switch (eventName) {
      case SystemEvent.BLUETOOTH_OPEN:
      case SystemEvent.BLUETOOTH_CLOSE:
        let state = eventName === SystemEvent.BLUETOOTH_OPEN
        JsNativeApi._bluetooth = state ? 'open' : 'close'
        emit(eventName)
        emit(SystemEvent.BLUETOOTH_CHANGED, {
          open: state
        })
        break
      default:
        emit(eventName, data)
        break
    }
    emit(SystemEvent.NATIVE_EVENT, {
      type: eventName,
      data
    })
  }
}

function nativeEventCallback (data) {
  log(`nativeEventCallback: ${ data }`)
  data = JSON.parse(data)
  analysis(data.type, data.data)
}

window.setEventCallback = nativeEventCallback

function proxy (bridge) {
  if (!hasNative) {
    hasNative = true
    api = bridge

    bridge.registerHandler('setEventCallback', function (data) {
      nativeEventCallback(data)
    })

    callHandler('isBluetoothOpen', null).then(function (open) {
      let state = open === 'true' ? 'open' : 'close'
      if (JsNativeApi._bluetooth !== state) {
        analysis(state === 'open' ? NativeEvent.BLUETOOTH_OPEN : NativeEvent.BLUETOOTH_CLOSE)
      }
    })

    readyCb.forEach(cb => {
      cb()
    })

    emit(SystemEvent.NATIVE_READY)
  }
}

window.JsNativeApi = JsNativeApi

export default JsNativeApi

export function inject () {
  return new Promise(resolve => {
    window.setupWebViewJavascriptBridge = function (cb) {
      if (window.WebViewJavascriptBridge) {
        return cb(window.WebViewJavascriptBridge)
      } else {
        // android bridge
        document.addEventListener('WebViewJavascriptBridgeReady', function () {
          cb(window.WebViewJavascriptBridge)
        }, false)
      }
      if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(cb) }
      window.WVJBCallbacks = [cb]
      let WVJBIframe = document.createElement('iframe')
      WVJBIframe.style.display = 'none'
      WVJBIframe.src = 'https://__bridge_loaded__'
      document.documentElement.appendChild(WVJBIframe)
      setTimeout(function () { document.documentElement.removeChild(WVJBIframe) }, 0)
    }
    window.setupWebViewJavascriptBridge(function (bridge) {
      proxy(bridge)
    })
    resolve()
  })
}
