<template>
  <div v-if="fetching || isError" class="c-loading">
    <div v-if="fetching" class="c-loading__tip"><img class="c-loading__icon" src="./loading.gif">{{ $t('common.loading') }}</div>
    <div v-else-if="isError" class="c-loading__button" @click="retry">{{ $t('common.retry') }}</div>
  </div>
</template>

<script>
export default {
  name: 'loading',
  props: {
    fetching: {
      type: Boolean,
      default: false,
      required: true
    },
    isError: {
      type: Boolean,
      default: false,
      required: true
    },
    retry: {
      type: Function,
      required: true
    }
  }
}
</script>

<style lang="scss" scoped>
.c-loading {
  padding-top: 10px;
  text-align: center;
  &__tip {
    display: inline-block;
    font-weight: bold;
    line-height: 18px;
    margin-bottom: 10px;
  }
  &__icon {
    width: 20px;
    height: 20px;
    vertical-align: bottom;
    margin-right: 8px;
  }
  &__button {
    display: inline-block;
    padding: 4px 10px;
    border-radius: 4px;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.78);
    margin-bottom: 10px;
  }
}
</style>
