<template>
  <navigation-wrapper class="c-modify" :title="$t('account.email')">
    <div class="l-navigation__content l-form">
      <form-field :label="$t('email.label')" :error="emailErrMsg" v-model.trim="email">
      </form-field>
      <form-field :label="$t('common.confirm')" :error="confirmErr" v-model="confirmEmail">
      </form-field>
      <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import formField from '@/components/FormField.vue'
import * as RetCode from '@/code/return-code.js'
import { checkEmail } from '@/utils/validate.js'

export default {
  name: 'modify-email',
  components: {
    formField
  },
  data () {
    return {
      email: '',
      emailErr: '',
      emailErrInfo: null,
      confirmEmail: '',
      confirmErr: ''
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      return this.isStaff ? 'staff/email/modify' : 'parent/email/modify'
    },
    isValid () {
      return !!this.email && !!this.confirmEmail
    },
    emailErrMsg () {
      return this.emailErr || (this.emailErrInfo ? this.$lang.code[this.emailErrInfo.code] || this.emailErrInfo.message : '')
    }
  },
  methods: {
    ...mapActions(['updateUserInfo']),
    save () {
      if (this.isValid) {
        this.emailErrInfo = null
        if (checkEmail(this.email)) {
          this.emailErr = ''
        } else {
          this.emailErr = this.$t('email.formatErr')
          this.confirmErr = ''
          return
        }
        if (this.confirmEmail !== this.email) {
          this.confirmErr = this.$t('email.notMatch')
          return
        } else {
          this.confirmErr = ''
        }
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: (res, data) => {
              this.updateUserInfo({
                email: data.email
              })
              this.$router.back()
            },
            fail: error => {
              switch (error.code) {
                case RetCode.ERROR_EMAIL_ALREADY_USED:
                  this.emailErrInfo = {
                    code: error.code,
                    message: error.message
                  }
                  return true
                default:
                  return false
              }
            }
          })
        }
        this.$httpPost.request({
          token: this.user.token,
          email: this.email
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

