export function checkPwd (pwd) {
  if (pwd.length < 6) {
    return {
      result: false,
      reason: 'tooShort'
    }
  } else if (pwd.length > 18) {
    return {
      result: false,
      reason: 'tooLong'
    }
  } else if (!/^[a-zA-Z\d~!@#$%^&*()_+`\-={}:";'<>?,.]+$/.test(pwd)) {
    return {
      result: false,
      reason: 'illegal'
    }
  } else if (/^[a-zA-Z]+$/.test(pwd) || /^\d+$/.test(pwd) || /^[~!@#$%^&*()_+`\-={}:";'<>?,.]+$/.test(pwd)) {
    return {
      result: false,
      reason: 'simple'
    }
  } else {
    return {
      result: true
    }
  }
}

export function checkEmail (email) {
  return /^[a-z0-9A-Z]+[-|a-z0-9A-Z._]+@([a-z0-9A-Z]+(-[a-z0-9A-Z]+)?\.)+[a-z]{2,}$/.test(email)
}
