<template>
  <character-intro :person="intro">
    <div class="c-bracelet-state">
      <div class="c-bracelet-state__icon" :class="{ 'has-unbind': state === 0, 'has-unconnected': state === 1, 'has-connected': state === 2 }"></div>
      <div class="c-bracelet-state__label">{{ stateTip }}</div>
    </div>
  </character-intro>
</template>

<script>
import { mapGetters } from 'vuex'
import characterIntro from '@/views/components/CharacterIntro.vue'

export default {
  name: 'home-character-Intro',
  props: ['person', 'connected', 'connecting'],
  components: {
    characterIntro
  },
  data () {
    return {
      connectState: -1
    }
  },
  computed: {
    ...mapGetters(['isStaff']),
    state () {
      switch (true) {
        case this.person.braceletOfBinding === 0:
          return 0
        case this.connected && this.connected === this.person.braceletMac:
          return 2
        case this.connecting === 'null' || this.connecting === this.person.braceletMac:
          return -1
        default:
          return 1
      }
    },
    stateTip () {
      switch (this.state) {
        case 0:
          return this.$t('state.unbind')
        case 1:
          return this.$t('state.unconnected')
        case 2:
          return this.$t('state.connected')
        default:
          return ''
      }
    },
    level () {
      switch (this.person.roles) {
        case 1:
        case 2:
        case 3:
        case 4:
          return this.$t('staffLevel.admin')
        case 5:
          return this.$t('staffLevel.headTeacher')
        case 6:
          return this.$t('staffLevel.teacher')
        default:
          return this.$t('staffLevel.unknown')
      }
    },
    intro () {
      let name = this.person.name
      return {
        name: this.isStaff ? this.$t('home.staff.name', {
          name
        }) : name,
        headUrl: this.person.headUrl,
        info: this.isStaff ? '' : this.$t('common.teacher') + this.person.headTeacherUserName,
        tag: this.isStaff ? this.level : this.$t('format.classInfo2', {
          gradeLevel: this.person.gradeLevel,
          classId: this.person.classId
        })
      }
    }
  }
}
</script>

<style lang="scss">
.c-bracelet-state {
  position: relative;
  width: 86px;
  padding-top: 6px;
  text-align: center;
  &__icon {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 50px;
    @include bg-img-ratio('../../assets/sh_icon', 64, 100);
    &::after {
      content: '';
      position: absolute;
      right: 1px;
      bottom: 6px;
      @include bg-img-ratio('../../assets/loading', 28, 28);
    }
    &.has-unbind {
      @include bg-img-ratio('../../assets/unbound', 93, 94);
      &::after {
        display: none;
      }
    }
    &.has-connected::after {
      right: 0;
      bottom: 3px;
      @include bg-img-ratio('../../assets/connect', 35, 36);
    }
    &.has-unconnected::after {
      right: 0;
      bottom: 3px;
      @include bg-img-ratio('../../assets/unconnect', 35, 36);
    }
  }
  &__label {
    position: absolute;
    top: 72px;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    color: rgba(#fff, 0.6);
    font-size: 12px;
    white-space: nowrap;
  }
}
</style>
