<template>
  <attendance-statistics :title="$t(`attendance.${ mode }`)" :result="result" :attendances="attendances" @choose="toGradeAttendance">
  </attendance-statistics>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { formatToYYYYMMDD } from '@/utils/format.js'
import attendanceStatistics from '@/views/components/AttendanceStatistics.vue'

export default {
  name: 'attendance-school',
  props: ['date'],
  components: {
    attendanceStatistics
  },
  data () {
    return {
      result: {
        arrived: 0,
        total: 0
      },
      attendances: []
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isAdmin']),
    mode () {
      return this.$route.params.mode
    },
    isTeacher () {
      return this.mode === 'teacher'
    },
    url () {
      return this.isTeacher ? 'admin/teacher/attendance' : 'admin/student/schoollevel/attendance'
    }
  },
  methods: {
    analysis (data) {
      let result = {
        arrived: this.isTeacher ? data.arrivedStaffTotal : data.arrivedStudentTotal,
        total: this.isTeacher ? data.staffTotal : data.studentTotal
      }
      if (this.isTeacher) {
        this.attendances = data.staffStat.map(val => {
          val.label = val.schoolLevel
          val.arrived = val.arrivedStaffSchoolLevelTotal
          val.total = val.staffSchoolLevelTotal
          val.precent = val.total > 0 ? val.arrived / val.total * 100 : 0
          val.proportion = val.total > 0 && result.total > 0 ? val.total / val.result * 100 : 0
          return val
        })
      } else {
        this.attendances = data.studentStat.map(val => {
          val.label = val.schoolLevel
          val.arrived = val.arrivedStudentSchoolLevelTotal
          val.total = val.studentSchoolLevelTotal
          val.proportion = val.studentSchoolLevelTotal > 0 ? val.arrivedStudentSchoolLevelTotal / val.studentSchoolLevelTotal * 100 : 0
          val.precent = val.total > 0 ? val.arrived / val.total * 100 : 0
          val.proportion = val.total > 0 && result.total > 0 ? val.total / val.result * 100 : 0
          return val
        })
      }
      this.result = result
    },
    toGradeAttendance (attendance) {
      if (this.isTeacher) {
        this.$router.push({ name: 'attendance-details', params: { mode: 'teacher', id: attendance.idSchoolLevel, date: this.date } })
      } else {
        this.$router.push({ name: 'attendance-grade', params: { schoolLevelId: attendance.idSchoolLevel, date: this.date } })
      }
    },
    fetch () {
      if (this.isAdmin) {
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            success: res => this.analysis(res.data)
          })
        }
        this.$httpPost.force({
          token: this.user.token,
          queryDate: this.date
        }, {
          cycle: formatToYYYYMMDD(new Date()) === this.date ? 60000 : 0
        })
      }
    },
    reset () {
      this.result = {
        arrived: 0,
        total: 0
      },
      this.attendances = []
      this.fetch()
    }
  },
  watch: {
    date () {
      this.reset()
    }
  },
  created () {
    this.fetch()
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
