<template>
  <div class="c-attendance-statistics">
    <attendance-block class="c-attendance-statistics__total" :title="title" :supplement="supplement" :attendance="result">
      <div class="c-attendance-statistics__desc">{{ $t('attendance.total') }}</div>
    </attendance-block>
    <div class="c-attendance-statistics__list">
      <attendance-item v-for="(attendance, index) in attendances" :key="index" :attendance="attendance" @click.native="choose(attendance)">
      </attendance-item>
    </div>
  </div>
</template>

<script>
import attendanceBlock from '@/views/components/AttendanceBlock.vue'
import attendanceItem from '@/views/components/AttendanceItem.vue'

export default {
  name: 'attendance-statistics',
  props: {
    title: {
      type: String,
      default: ''
    },
    supplement: {
      type: String,
      default: ''
    },
    result: {
      type: Object,
      default () {
        return {
          arrived: 0,
          total: 0
        }
      }
    },
    attendances: {
      type: Array,
      default () {
        return []
      }
    }
  },
  components: {
    attendanceBlock,
    attendanceItem
  },
  methods: {
    choose (attendance) {
      this.$emit('choose', attendance)
    }
  }
}
</script>

<style lang="scss">
.c-attendance-statistics {
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #f2f2f2;
  &__total {
    flex: none;
    padding: 12px 0;
  }
  &__desc {
    position: absolute;
    right: 24px;
    top: 8px;
    color: #333;
    font-size: 16px;
  }
  &__list {
    flex: auto;
    padding-bottom: 12px;
    overflow-x: hidden;
  }
}
</style>
