<template>
  <navigation-wrapper :title="$t('setting.language')">
    <div slot="button" class="c-navigation-bar__button t-adapt-text" @click="save">{{ $t('common.save') }}</div>
    <div class="l-navigation__content has-padding">
      <ul>
        <li v-for="(lang, i) in $langs" :key="lang.key" class="c-info-item" @click="change(i)">
          <div class="c-info-item__label">{{ lang.text }}</div>
          <div class="c-info-item__radio" :class="{ 'o-radio': index === i }"></div>
        </li>
      </ul>
    </div>
  </navigation-wrapper>
</template>

<script>
import { changeLang } from '@/locale/index.js'

export default {
  name: '',
  data () {
    return {
      index: 0,
      defaultIndex: 0
    }
  },
  methods: {
    change (index) {
      this.index = index
    },
    save () {
      if (this.index !== this.defaultIndex) {
        changeLang(this.$langs[this.index].key)
      }
      this.$router.back()
    }
  },
  created () {
    let locale = this.$lang.key
    this.index = this.$langs.findIndex(lang => lang.key === locale)
    this.defaultIndex = this.index
  }
}
</script>
