<template>
  <navigation-wrapper>
    <div class="l-form c-modify" style="padding-top: 20px;">
      <div class="l-form__title"></div>
      <div class="c-form-field c-form-field--button" :class="{ 'is-active': isFocused }">
        <div class="c-form-field__label"></div>
        <div class="c-form-field__input_wrapper">
          <input type="text" class="c-form-field__input" style="font-size: 16px;" v-model="serveUrl" @focus="isFocused = true" @blur="isFocused = false" />
          <div class="c-form-field__button" @click="reset"></div>
        </div>
      </div>
      <div class="c-info-item">
        <div class="c-info-item__label"></div>
        <div class="c-info-item__switch o-switch t-adpat-switch" :class="{ 'is-on': isDebug }" @click="toggle"></div>
      </div>
      <div class="o-button o-button--submit t-adapt-button--active c-modify__submit is-active" @click="save"></div>
    </div>
  </navigation-wrapper>
</template>

<script>
import axios from 'axios'
import { setEnabled, isUsed } from '@/utils/log.js'
import { resetLangs } from '@/locale/index.js'

export default {
  name: 'backdoor',
  data () {
    return {
      isFocused: false,
      isDebug: isUsed(),
      defaultServelUrl: axios.defaults.baseURL,
      serveUrl: axios.defaults.baseURL
    }
  },
  methods: {
    save () {
      if (this.serveUrl !== this.defaultServelUrl) {
        axios.defaults.baseURL = this.serveUrl
        window.localStorage.setItem('smart_campus__base_url', this.serveUrl)
        resetLangs()
      }
      setEnabled(this.isDebug)
      window.localStorage.setItem('smart_campus__debug', this.isDebug ? 'true' : 'false')
      this.$router.back()
    },
    toggle () {
      this.isDebug = !this.isDebug
    },
    reset () {
      this.serveUrl = this.$cfg.base_url
    }
  }
}
</script>
