<template>
  <transition :duration='{ leave: 1000 }' name='fade' appear>
    <div v-show="isShow" class="c-toast">
      <p class="c-toast__content">{{ msg }}</p>
    </div>
  </transition>
</template>

<script>
import * as Event from '@/code/custom-event.js'

export default {
  name: 'toast',
  data () {
    return {
      msgInfo: null,
      isShow: false,
      timer: -1
    }
  },
  computed: {
    msg () {
      return this.msgInfo ? this.$lang.code[this.msgInfo.code] || this.msgInfo.message || this.$t('error.unknown') : ''
    }
  },
  methods: {
    show (msgInfo) {
      clearTimeout(this.timer)
      if (!this.isShow) {
        this.isShow = true
      }
      this.msgInfo = msgInfo
      this.timer = setTimeout(() => {
        this.isShow = false
      }, 5000)
    }
  },
  mounted () {
    this.$eventBus.$on(Event.TOAST, this.show)
  },
  beforeDestroy () {
    if (this.isShow) {
      clearTimeout(this.timer)
    }
  }
}
</script>

<style lang="scss">
.c-toast {
  position: fixed;
  z-index: 999;
  min-height: 90px;
  top: 300px;
  left: 50%;
  width: 250px;
  transform: translate(-50%, 0);
  text-align: center;
  transition: opacity 0.2s ease-out;
  &__content {
    padding: 10px 25px;
    color: #ffffff;
    font-size: 14px;
    border-radius: 30px;
    background-color: rgba(#000, 0.6);
  }
}

.fade-enter, .fade-leave-to {
  &.c-toast {
    opacity: 0;
  }
}
</style>
