<template>
  <navigation-wrapper class="p-attendance-history" :title="$t('attendance.history')">
    <div class="l-navigation__placeholder c-attendance-type">
      <router-link class="o-tab c-attendance-type__tab" :class="{ 'is-active': isNormal }" :to="{ params: { type: 'normal' } }" replace>{{ $t('signIn.normal') }}</router-link>
      <router-link class="o-tab c-attendance-type__tab" :class="{ 'is-active': isAbnormal }" :to="{ params: { type: 'abnormal' } }" replace>{{ $t('signIn.abnormal') }}</router-link>
    </div>
    <div class="l-navigation__content u-overflow-hidden">
      <div class="p-attendance-history__content" :class="{ 'p-attendance-history__content--current': isNormal }" ref="wrapper">
        <div>
          <ul ref="listWrapper">
            <li v-for="(attendanceSet, index) in normalAttendances" :key="index">
              <div v-once class='p-attendance-history__time'>{{ attendanceSet.label }}</div>
              <div class='p-attendance-history__list'>
                <div v-for="(item, index) in attendanceSet.attendances" :key="index" class="c-attendance-history-item">
                  <div class="c-attendance-history-item__info u-clearfix">
                    <div class="c-attendance-history-item__date">{{ item.date }}</div>
                    <div class="c-attendance-history-item__state">{{ item.isManual === 1 ? $t('signInType.2') : $t('signInType.1') }}</div>
                  </div>
                  <div class="c-attendance-history-item__time">{{ item.time }}</div>
                </div>
              </div>
            </li>
          </ul>
          <div v-if="normalCanPullUp" class="p-sign-in-history__pullup">{{ normalPullUpTip }}</div>
        </div>
        <loading :fetching="normalLoading" :isError="normalIsError" :retry="fetchNormal"></loading>
        <div v-if="normalEmpty" class='p-attendance-history__time'>{{ $t('common.empty') }}</div>
      </div>
      <div class="p-attendance-history__content" :class="{ 'p-attendance-history__content--current': isAbnormal }" ref="wrapper2">
        <div>
          <ul ref="listWrapper2">
            <li v-for="(attendanceSet, index) in abnormalAttendances" :key="index">
              <div v-once class='p-attendance-history__time'>{{ attendanceSet.label }}</div>
              <div class='p-attendance-history__list'>
                <div v-for="(item, index) in attendanceSet.attendances" :key="index" class="c-attendance-history-item">
                  <div class="c-attendance-history-item__info u-clearfix">
                    <div class="c-attendance-history-item__date">{{ item.date }}</div>
                    <div class="c-attendance-history-item__state">{{ $t('signInType.3') }}</div>
                  </div>
                  <div class="c-attendance-history-item__time">{{ item.time }}</div>
                </div>
              </div>
            </li>
          </ul>
          <div v-if="abnormalCanPullUp" class="p-sign-in-history__pullup">{{ abnormalPullUpTip }}</div>
        </div>
        <loading :fetching="abnormalLoading" :isError="abnormalIsError" :retry="fetchAbnormal"></loading>
        <div v-if="abnormalEmpty" class='p-attendance-history__time'>{{ $t('common.empty') }}</div>
      </div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState } from 'vuex'
import BScroll from 'better-scroll'
import { getRect } from '@/utils/dom.js'
import loading from '@/components/Loading.vue'

export default {
  name: 'attendance-history',
  components: {
    loading
  },
  data () {
    return {
      currentType: null,
      normalHttpPost: null,
      normalPage: 1,
      normalPageSize: 20,
      normalIsPullUpLoad: false,
      normalError: false,
      normalHasMore: true,
      normalHasFetched: false,
      normalAttendances: [],
      abnormalHttpPost: null,
      abnormalPage: 1,
      abnormalPageSize: 20,
      abnormalIsPullUpLoad: false,
      abnormalError: false,
      abnormalHasMore: true,
      abnormalHasFetched: false,
      abnormalAttendances: []
    }
  },
  computed: {
    ...mapState(['user']),
    mode () {
      return this.$route.params.mode
    },
    isTeacher () {
      return this.mode === 'teacher'
    },
    isStudent () {
      return this.mode === 'student'
    },
    url () {
      return this.isTeacher ? 'admin/staff/history/attendance' : 'teacher/student/history/attendance'
    },
    id () {
      return Number(this.$route.params.id)
    },
    normalFetching () {
      return !!this.normalHttpPost && this.normalHttpPost.isRequesting()
    },
    normalLoading () {
      return this.normalFetching && !this.normalHasFetched
    },
    normalIsError () {
      return this.normalError && !this.normalHasFetched
    },
    normalHas () {
      return this.normalAttendances.length > 0
    },
    normalEmpty () {
      return this.normalHasFetched && !this.normalHas
    },
    normalCanPullUp () {
      return this.normalHasFetched && this.normalHas
    },
    normalPullUpTip () {
      return this.normalIsPullUpLoad ? this.$t('pullUp.loading') : this.normalHasMore ? this.$t('pullUp.more') : this.$t('pullUp.finished')
    },
    abnormalFetching () {
      return !!this.abnormalHttpPost && this.abnormalHttpPost.isRequesting()
    },
    abnormalLoading () {
      return this.abnormalFetching && !this.abnormalHasFetched
    },
    abnormalIsError () {
      return this.abnormalError && !this.abnormalHasFetched
    },
    abnormalHas () {
      return this.abnormalAttendances.length > 0
    },
    abnormalEmpty () {
      return this.abnormalHasFetched && !this.abnormalHas
    },
    abnormalCanPullUp () {
      return this.abnormalHasFetched && this.abnormalHas
    },
    abnormalPullUpTip () {
      return this.abnormalIsPullUpLoad ? this.$t('pullUp.loading') : this.abnormalHasMore ? this.$t('pullUp.more') : this.$t('pullUp.finished')
    },
    isNormal () {
      return this.currentType === 'normal'
    },
    isAbnormal () {
      return this.currentType === 'abnormal'
    }
  },
  methods: {
    isSameMonth (date, date2) {
      return date.getFullYear() === date2.getFullYear() && date.getMonth() === date2.getMonth()
    },
    isSameDay (date, date2) {
      return this.isSameMonth(date, date2) && date.getDate() === date2.getDate()
    },
    checkDateStr (date) {
      let today = new Date()
      if (this.isSameDay(today, date)) {
        return this.$t('history.today')
      } else {
        today.setDate(today.getDate() - 1)
        if (this.isSameDay(today, date)) {
          return this.$t('history.yesterday')
        }
      }
      return this.$t('format.month-day', {
        month: this.$d(date, 'month'),
        day: date.getDate()
      })
    },
    addData (attendance, attendances) {
      let info = attendances[attendances.length - 1]
      let date = new Date(attendance.checkTime)
      if (!info || !this.isSameMonth(info.date, date)) {
        info = {
          label: this.$t('format.year-month', {
            year: date.getFullYear(),
            month: this.$d(date, 'month')
          }),
          date: date,
          attendances: []
        }
        attendances.push(info)
      }
      info.attendances.push({
        date: this.checkDateStr(date),
        time: attendance.checkTimeStr.split(' ')[1],
        isManual: attendance.isManual
      })
    },
    initNormalScroll () {
      if (!this.$refs.wrapper || !this.normalHas || this.$normalScroll) {
        return
      }
      this.$refs.listWrapper.style.minHeight = `${ getRect(this.$refs.wrapper).height + 1 }px`
      this.$normalScroll = new BScroll(this.$refs.wrapper, {
        scrollY: true,
        probeType: 2,
        pullUpLoad: {
          threshold: 10
        }
      })
      this.$normalScroll.on('pullingUp', () => {
        if (this.normalHasMore && !this.normalFetching) {
          this.normalIsPullUpLoad = true
          this.fetchNormal()
        } else {
          this.$normalScroll.finishPullUp()
        }
      })
    },
    normalPullUpFnished () {
      if (this.normalIsPullUpLoad) {
        this.normalIsPullUpLoad = false
        this.$normalScroll.finishPullUp()
      }
    },
    normalForceUpdate () {
      this.$nextTick(() => {
        if (this.$normalScroll) {
          this.normalPullUpFnished()
          this.$normalScroll.refresh()
        } else {
          this.initNormalScroll()
        }
      })
    },
    addNormal (attendances) {
      this.normalHasFetched = true
      if (attendances.length < this.normalPageSize) {
        this.normalHasMore = false
      }
      if (attendances.length === 0) {
        return
      }
      for (let i = 0, j = attendances.length; i < j; i++) {
        this.addData(attendances[i], this.normalAttendances)
      }
      this.normalPage += 1
    },
    fetchNormal () {
      this.normalHttpPost.request(this.isStudent ? {
        token: this.user.token,
        idStudent: this.id,
        checkType: 1,
        page: this.normalPage,
        pageSize: this.normalPageSize
      } : {
        token: this.user.token,
        idStaff: this.id,
        checkType: 1,
        page: this.normalPage,
        pageSize: this.normalPageSize
      })
    },
    initNormal () {
      if (!this.normalHttpPost) {
        this.normalHttpPost = this.$http.createPost(this.url, {
          success: (res) => {
            this.normalError = false
            this.addNormal(res.data)
          },
          fail: () => {
            this.normalError = true
          }
        })
      }
      if (!this.normalHasFetched) {
        this.fetchNormal()
      }
    },
    initAbnormalScroll () {
      if (!this.$refs.wrapper2 || !this.abnormalHas || this.$abnormalScroll) {
        return
      }
      this.$refs.listWrapper2.style.minHeight = `${ getRect(this.$refs.wrapper).height + 1 }px`
      this.$abnormalScroll = new BScroll(this.$refs.wrapper2, {
        scrollY: true,
        probeType: 2,
        pullUpLoad: {
          threshold: 10
        }
      })
      this.$abnormalScroll.on('pullingUp', () => {
        if (this.abnormalHasMore && !this.abnormalFetching) {
          this.abnormalIsPullUpLoad = true
          this.fetchAbnormal()
        } else {
          this.$abnormalScroll.finishPullUp()
        }
      })
    },
    abnormalPullUpFnished () {
      if (this.abnormalIsPullUpLoad) {
        this.abnormalIsPullUpLoad = false
        this.$abnormalScroll.finishPullUp()
      }
    },
    addAbnormal (attendances) {
      this.abnormalHasFetched = true
      if (attendances.length < this.abnormalPageSize) {
        this.abnormalHasMore = false
      }
      if (attendances.length === 0) {
        return
      }
      for (let i = 0, j = attendances.length; i < j; i++) {
        this.addData(attendances[i], this.abnormalAttendances)
      }
      this.abnormalPage += 1
    },
    fetchAbnormal () {
      this.abnormalHttpPost.request(this.isStudent ? {
        token: this.user.token,
        idStudent: this.id,
        checkType: 3,
        page: this.abnormalPage,
        pageSize: this.abnormalPageSize
      } : {
        token: this.user.token,
        idStaff: this.id,
        checkType: 3,
        page: this.abnormalPage,
        pageSize: this.abnormalPageSize
      })
    },
    abnormalForceUpdate () {
      this.$nextTick(() => {
        if (this.$abnormalScroll) {
          this.abnormalPullUpFnished()
          this.$abnormalScroll.refresh()
        } else {
          this.initAbnormalScroll()
        }
      })
    },
    initAbnormal () {
      if (!this.abnormalHttpPost) {
        this.abnormalHttpPost = this.$http.createPost(this.url, {
          success: (res) => {
            this.abnormalError = false
            this.addAbnormal(res.data)
          },
          fail: () => {
            this.abnormalError = true
          }
        })
      }
      if (!this.abnormalHasFetched) {
        this.fetchAbnormal()
      }
    },
    updateAttendances (type) {
      if (this.currentType !== type) {
        this.currentType = type
        switch (this.currentType) {
          case 'normal':
            this.initNormal()
            break
          case 'abnormal':
            this.initAbnormal()
            break
          default:
            break
        }
      }
    }
  },
  watch: {
    normalFetching (nVal, oVal) {
      if (oVal && !nVal) {
        setTimeout(() => {
          this.normalForceUpdate()
        }, 20)
      }
    },
    abnormalFetching (nVal, oVal) {
      if (oVal && !nVal) {
        setTimeout(() => {
          this.abnormalForceUpdate()
        }, 20)
      }
    }
  },
  beforeRouteUpdate (to, from, next) {
    this.updateAttendances(to.params.type)
    next()
  },
  created () {
    this.updateAttendances(this.$route.params.type)
  },
  mounted () {
    switch (this.currentType) {
      case 'normal':
        this.initNormalScroll()
        break
      case 'abnormal':
        this.initAbnormalScroll()
        break
      default:
        break
    }
  },
  beforeDestroy () {
    if (this.normalHttpPost) {
      this.normalHttpPost.cancel()
    }
    if (this.$normalScroll) {
      this.$normalScroll.destroy()
    }
    if (this.abnormalHttpPost) {
      this.abnormalHttpPost.cancel()
    }
    if (this.$abnormalScroll) {
      this.$abnormalScroll.destroy()
    }
  }
}
</script>

<style lang="scss" scoped>
.p-attendance-history {
  &__time {
    padding: 18px 15px 10px;
    color: rgba(#333, 0.4);
    font-size: 14px;
    line-height: 1;
    background-color: #f2f2f2;
  }
  &__content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    transform: translateX(100%);
    &--current {
      transform: translateX(0);
    }
  }
  &__list {
    background-color: #fff;
  }
}
.c-attendance-history-item {
  padding: 15px;
  background-color: #fff;
  &__info {
    color: #333;
    font-size: 14px;
  }
  &__date {
    float: left;
  }
  &__state {
    float: right;
  }
  &__time {
    margin-top: 6px;
    text-align: right;
    color: rgba(#333, 0.7);
    font-size: 12px;
    line-height: 1;
  }
}
.c-attendance-type {
  padding-top: 6px;
  text-align: center;
  &__tab {
    min-width: 120px;
    & + & {
      margin-left: 40px;
    }
  }
}
</style>
