<template>
  <safe-area-layout class="l-operation p-unbind">
    <div slot="topbar" class="l-operation__title">{{ $t('unbind.title') }}</div>
    <div class="l-operation__header">{{ $t('unbind.tip') }}</div>
    <div class="l-operation__content u-align-c">
      <div class="p-unbind__img">
        <div class="p-unbind__avatar" :class="{ 'c-solid-avatar': sex === 0, 'c-solid-avatar--boy': sex === 1, 'c-solid-avatar--girl': sex === 2, 'has-avatar': !!figure && !!figure.headUrl }">
          <div v-if="!!figure && !!figure.headUrl" class="c-solid-avatar__icon">
            <i :style="{ backgroundImage: `url(${ figure.headUrl })` }"></i>
          </div>
        </div>
        <div class="p-unbind__link"></div>
      </div>
    </div>
    <div slot="footer" class="l-operation__footer">
      <div class="l-operation__button o-border-button o-button--cancel" @click="cancel">{{ $t('common.cancel') }}</div>
      <div class="l-operation__button o-button o-button--prominent" @click="unbind">{{ fail ? $t('bracelet.retry') : $t('bracelet.unbind') }}</div>
    </div>
  </safe-area-layout>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  name: 'unbind',
  data () {
    return {
      fail: false
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff', 'getFigure']),
    url () {
      return this.isStaff ? 'staff/bracelet/unbind' : 'parent/bracelet/unbind'
    },
    id () {
      return Number(this.$route.params.id)
    },
    figure () {
      return this.getFigure(this.id)
    },
    sex () {
      if (this.figure) {
        if (this.figure.gender === 'Male') {
          return 1
        }
        if (this.figure.gender === 'Female') {
          return 2
        }
        return this.figure.relationship || 0
      }
      return 0
    }
  },
  methods: {
    ...mapActions(['updateFigureInfo']),
    cancel () {
      this.$router.back()
    },
    unbind () {
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: () => {
            if (this.figure) {
              this.$native.unbindBracelet(this.figure.braceletMac)
            }
            this.updateFigureInfo({
              id: this.id,
              info: {
                braceletOfBinding: 0,
                braceletMac: ''
              }
            })
            this.cancel()
          },
          fail: () => {
            this.fail = true
          }
        })
      }
      this.$httpPost.request(this.isStaff ? {
        token: this.user.token
      } : {
        token: this.user.token,
        idStudent: this.id
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-unbind {
  &__img {
    position: relative;
    display: inline-block;
    padding-right: 110px;
    @include bg-img-ratio('../../assets/bracelet', 118, 427);
  }
  &__avatar {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -30px;
  }
  &__link {
    position: absolute;
    top: 50%;
    right: 54px;
    margin-top: -15px;
    display: inline-block;
    @include bg-img-ratio('../../assets/bind-success', 124, 60);
  }
}
</style>
