<template>
  <navigation-wrapper :title="$t('home.menu.account')">
    <div class="l-navigation__content has-padding">
      <ul>
        <li class='c-account-info'>
          <div class="c-account-info__name u-more">{{ user.name }}</div>
          <div class="c-account-info__avatar c-avatar" :class="{ 'has-avatar': !!user.headUrl, 'c-avatar--boy': user.gender === 'Male', 'c-avatar--girl': user.gender === 'Female' }" @click="upload">
            <div v-if="!!user.headUrl" class="c-avatar__icon">
              <i :style="{ backgroundImage: `url(${ user.headUrl })` }"></i>
            </div>
          </div>
        </li>
        <li v-if="isStaff" class="c-info-item">
          <div class="c-info-item__label">{{ $t('account.id') }}</div>
          <div class="c-info-item__value">{{ user.legacyId }}</div>
        </li>
        <li v-if="!isStaff" class="c-info-item">
          <div class="c-info-item__label">{{ $t('account.relationship') }}</div>
          <div class="c-info-item__value">{{ relationship }}</div>
        </li>
        <router-link tag="li" class="c-info-item" :to="{ name: 'modify-mobile-confirm' }">
          <div class="c-info-item__label">{{ $t('account.phone') }}</div>
          <div class="c-info-item__value u-more">{{ user.mobilePhone }}</div>
        </router-link>
        <router-link tag="li" class="c-info-item" :to="{ name: 'modify-email' }">
          <div class="c-info-item__label">{{ $t('account.email') }}</div>
          <div class="c-info-item__value u-more">{{ user.email }}</div>
        </router-link>
      </ul>
      <input class="u-display-none" ref="fileUpload" type="file" accept="image/*" @change="handleFile" />
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  name: 'account-info',
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      return this.isStaff ? 'staff/pic/modify' : 'parent/pic/modify'
    },
    relationship () {
      switch (true) {
        case this.user.relationship === 1:
          return this.$t('relationship.father')
        case this.user.relationship === 2:
          return this.$t('relationship.mother')
        default:
          return ''
      }
    }
  },
  methods: {
    ...mapActions(['updateUserInfo']),
    upload () {
      if (this.user && this.url && this.$refs.fileUpload && !this.cancelSource) {
        this.$refs.fileUpload.click()
      }
    },
    clear () {
      this.cancelSource = null
      this.$refs.fileUpload.value = null
    },
    handleFile (e) {
      let target = e.target || e.srcElement
      let file = target.files[0]
      if (!file) {
        return
      }
      if (!this.$uploadHttpPost) {
        this.$uploadHttpPost = this.$http.createPost('common/pic/upload', {
          opts: {
            headers: {
              'Content-Type': 'multipart/form-data'
            },
            origin: true,
            lock: true
          },
          success: res => {
            this.changeAvatar(res.data.realPath)
          }
        })
      }
      let formData = new FormData()
      formData.append('file', file)
      this.$uploadHttpPost.request(formData)
    },
    changeAvatar (path) {
      this.$log(`account head url: ${ path }`)
      if (!this.$avatarHttpPost) {
        this.$avatarHttpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: (res, data) => {
            this.updateUserInfo({
              headUrl: data.picImgPath
            })
          }
        })
      }
      this.$avatarHttpPost.request({
        token: this.user.token,
        picImgPath: path
      })
    }
  },
  beforeDestroy () {
    if (this.$uploadHttpPost) {
      this.$uploadHttpPost.cancel()
    }
    if (this.$avatarHttpPost) {
      this.$avatarHttpPost.cancel()
    }
  }
}
</script>
