<template>
  <div class="l-form c-modify p-pre-forget-pwd">
    <div class="l-form__title">{{ $t('resetPwd.title') }}</div>
    <div class="p-pre-forget-pwd__nav">
      <div class="o-fixed-tab" :class="{ 'is-active': isPhoneMode }" @click="changeMode(0)">{{ $t('tab.phone') }}</div>
      <div class="o-fixed-tab" :class="{ 'is-active': isEmailMode }" @click="changeMode(1)">{{ $t('tab.email') }}</div>
    </div>
    <form-field v-if="isPhoneMode" type="number" class="c-form-field--select" :label="$t('sms.phone')" v-model="mobile">
      <area-code :areaCode="areaCode" @change="pick"></area-code>
    </form-field>
    <form-field v-else class="c-form-field" :label="$t('sms.email')" :placeholder="$t('sms.email')" v-model="email">
    </form-field>
    <form-field class="c-form-field--button" :label="$t('sms.vCode')" :error="vCodeError" v-model="vCode">
      <sms :mobile="mobile" :areaCode="areaCode" :email="email" :mode="checkMode"></sms>
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="next">{{ $t('common.next') }}</div>
  </div>
</template>

<script>
import * as RetCode from '@/code/return-code.js'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'
import sms from '@/widgets/Sms.vue'

export default {
  name: 'pre-set-pwd',
  components: {
    formField,
    areaCode,
    sms
  },
  data () {
    return {
      checkMode: 0,
      areaCode: '',
      mobile: '',
      email: '',
      vCode: '',
      vCodeErrorInfo: null
    }
  },
  computed: {
    mode () {
      return this.$route.params.mode
    },
    isStaff () {
      return this.mode === 'staff'
    },
    url () {
      // switch (this.checkMode) {
      //   case 0:
      //     return this.isStaff ? 'staff/preModify' : ''
      //   case 1:
      //     return ''
      //   default:
      //     return ''
      // }
      return ''
    },
    isPhoneMode () {
      return this.checkMode === 0
    },
    isEmailMode () {
      return this.checkMode === 1
    },
    mobileVal () {
      return this.areaCode && this.mobile ? this.areaCode + this.mobile : null
    },
    account () {
      return this.isPhoneMode ? this.mobileVal : this.email
    },
    isValid () {
      return this.url && this.account && this.vCode
    },
    vCodeError () {
      return this.vCodeErrorInfo ? this.$lang.code[this.vCodeErrorInfo.code] || this.vCodeErrorInfo.message : ''
    }
  },
  methods: {
    clearError () {
      this.vCodeErrorInfo = null
    },
    changeMode (checkMode) {
      if (this.checkMode !== checkMode) {
        this.clearError()
        this.vCode = ''
        this.checkMode = checkMode
        if (this.$httpPost) {
          this.$httpPost.setUrl(this.url)
        }
      }
    },
    pick (areaCode) {
      this.areaCode = areaCode
    },
    next () {
      if (this.url && this.isValid) {
        this.clearError()
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: req => {
              this.$router.replace({ name: 'reset-pwd', params: { mode: this.mode, token: req.data.token } })
            },
            fail: error => {
              switch (error.code) {
                case RetCode.ERROR_VCODE_UNSET:
                case RetCode.ERROR_VCODE_INCORRECT:
                  this.vCodeErrorInfo = {
                    code: error.code,
                    message: error.message
                  }
                  return true
                default:
                  return false
              }
            }
          })
        }
        this.$httpPost.request(this.isPhoneMode ? {
          mobile: this.mobileVal,
          vCode: this.vCode
        } : {
          email: this.email,
          vCode: this.vCode
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-pre-forget-pwd {
  &__nav {
    margin-bottom: 40px;
    text-align: center;
    .o-fixed-tab + .o-fixed-tab {
      margin-left: 115px;
    }
  }
}
</style>
