<template>
  <safe-area-layout class="l-operation p-bind-failure">
    <div slot="topbar" class="l-operation__title">{{ $t('bind.title') }}</div>
    <div class="l-operation__header p-bind-failure__error">
      {{ $t('bind.failure') }}
      <div class="p-bind-failure__tip">{{ failTip }}</div>
    </div>
    <div class="l-operation__content u-align-c">
      <div class="p-bind-failure__img">
        <div class="p-bind-failure__avatar" :class="{ 'c-solid-avatar': sex === 0, 'c-solid-avatar--boy': sex === 1, 'c-solid-avatar--girl': sex === 2, 'has-avatar': !!figure && !!figure.headUrl }">
          <div v-if="!!figure && !!figure.headUrl" class="c-solid-avatar__icon">
            <i :style="{ backgroundImage: `url(${ figure.headUrl })` }"></i>
          </div>
        </div>
        <div class="p-bind-failure__link"></div>
      </div>
    </div>
    <div slot="footer" class="l-operation__footer">
      <div class="l-operation__button o-border-button o-button--cancel" @click="cancel">{{ $t('common.cancel') }}</div>
      <div class="l-operation__button o-button t-adapt-button" @click="retry">{{ $t('bracelet.retry') }}</div>
    </div>
  </safe-area-layout>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  name: 'bind-failure',
  computed: {
    ...mapState(['user', 'runtimmeMode']),
    ...mapGetters(['isStaff', 'getFigure']),
    url () {
      return this.isStaff ? 'staff/bracelet/bind' : 'parent/bracelet/bind'
    },
    id () {
      return Number(this.$route.params.id)
    },
    code () {
      return this.$route.params.code ? this.$route.params.code : null
    },
    figure () {
      return this.getFigure(this.id)
    },
    sex () {
      if (this.figure) {
        if (this.figure.gender === 'Male') {
          return 1
        }
        if (this.figure.gender === 'Female') {
          return 2
        }
        return this.figure.relationship || 0
      }
      return 0
    },
    failTip () {
      let tip
      if (this.code !== null) {
        tip = this.$lang.code[this.code]
      }
      return tip || this.$t('bind.failTip')
    }
  },
  methods: {
    ...mapActions(['updateFigureInfo']),
    cancel () {
      switch (this.runtimmeMode) {
        case 'boot':
          this.$router.replace({ name: 'home' })
          break
        default:
          this.$router.back()
          break
      }
    },
    bind (serialNum, macAddress, nfcId) {
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: (res, data) => {
            this.updateFigureInfo({
              id: this.id,
              info: {
                braceletOfBinding: 1,
                braceletMac: data.macAddress
              }
            })
            this.$router.replace({ name: 'bind-success', params: { id: this.id } })
          },
          fail: error => {
            this.$router.replace({ name: 'bind-failure', params: { id: this.id, code: error.code } })
            return true
          }
        })
      }
      this.$httpPost.request(this.isStaff ? {
        token: this.user.token,
        serialNum,
        macAddress,
        nfcId
      } : {
        token: this.user.token,
        idStudent: this.id,
        serialNum,
        macAddress,
        nfcId
      })
    },
    retry () {
      this.$native.scan().then(result => {
        if (result.type === 1) {
          let data = result.message.split('#')
          if (data.length === 3) {
            this.bind(data[0], data[1], data[2])
          }
        }
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-bind-failure {
  &__error {
    color: #ff6775;
  }
  &__tip {
    padding-top: 16px;
    color: #adadad;
    font-size: 10px;
  }
  &__img {
    position: relative;
    display: inline-block;
    padding-right: 110px;
    @include bg-img-ratio('../../assets/bracelet', 118, 427);
  }
  &__avatar {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -30px;
  }
  &__link {
    position: absolute;
    top: 50%;
    right: 54px;
    margin-top: -15px;
    display: inline-block;
    @include bg-img-ratio('../../assets/bind-failure', 124, 60);
  }
}
</style>
