<template>
  <div class="c-home-attendance-admin">
    <attendance-block :title="$t('attendanceType.teacher')" class="c-attendance-block--single" :supplement="$t('attendance.supplement')" :attendance="teacherAttendance" @click.native="toTeacherAttendance"></attendance-block>
    <attendance-block :title="$t('attendanceType.student')" class="c-attendance-block--single" :supplement="$t('attendance.supplement')" :attendance="studentAttendance" @click.native="toStudentAttendance"></attendance-block>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import attendanceBlock from '@/views/components/AttendanceBlock.vue'

export default {
  name: 'home-attendance-admin',
  components: {
    attendanceBlock
  },
  data () {
    return {
      teacherAttendance: {
        arrived: 0,
        total: 0
      },
      studentAttendance: {
        arrived: 0,
        total: 0
      }
    }
  },
  computed: {
    ...mapState(['user']),
  },
  methods: {
    analysis (data) {
      this.teacherAttendance = {
        arrived: data.arrivedStaffTotal,
        total: data.staffTotal
      }
      this.studentAttendance = {
        arrived: data.arrivedStudentTotal,
        total: data.studentTotal
      }
    },
    toTeacherAttendance () {
      this.$router.push({ name: 'attendance-school', params: { mode: 'teacher' } })
    },
    toStudentAttendance () {
      this.$router.push({ name: 'attendance-school', params: { mode: 'student' } })
    }
  },
  created () {
    this.$httpPost = this.$http.createPost('admin/dashboard', {
      opts: {
        cycle: 60000
      },
      success: res => this.analysis(res.data)
    })
    this.$httpPost.request({
      token: this.user.token
    })
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.c-home-attendance-admin {
  white-space: nowrap;
  .c-attendance-block {
    width: 50%;
    padding-right: 8px;
    &__header {
      margin-bottom: 20px;
    }
    &__arrived {
      font-size: 32px;
    }
    &__percent {
      font-size: 18px;
    }
    &:last-child {
      padding-right: 0;
    }
    & + .c-attendance-block {
      padding-left: 8px;
    }
  }
}
</style>
