<template>
  <div class="l-form c-modify">
    <form-field :label="$t('setPwd.oldPwd')" :error="pwdError" v-model="oldPassword">
    </form-field>
    <form-field class="c-form-field--pwd" :class="{ 'is-valid': isPwdValid }" :label="$t('setPwd.newPwd')" :tip="$t('setPwd.tip')" :error="passwordErr" @blur="checkPwdValid" v-model="password">
    </form-field>
    <form-field class="c-form-field--pwd" :class="{ 'is-valid': isConfirmPwdValid }" :label="$t('common.confirm')" :error="confirmPasswordErr" @blur="checkConfirmPwdValid" v-model="confirmPassword">
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import formField from '@/components/FormField.vue'
import { checkPwd } from '@/utils/validate.js'
import * as RetCode from '@/code/return-code.js'

export default {
  name: 'modify-pwd',
  components: {
    formField
  },
  data () {
    return {
      oldPassword: '',
      password: '',
      confirmPassword: '',
      passwordErr: '',
      confirmPasswordErr: '',
      pwdErrorInfo: null,
      isPwdValid: false,
      isConfirmPwdValid: false,
      isMatch: false
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      return this.isStaff ? 'staff/password/modify' : 'parent/password/reset'
    },
    isValid () {
      return !!this.oldPassword && this.isPwdValid && this.isConfirmPwdValid && this.isMatch
    },
    pwdError () {
      return this.pwdErrorInfo ? this.$lang.code[this.pwdErrorInfo.code] || this.pwdErrorInfo.message : ''
    }
  },
  methods: {
    checkPwdValid () {
      let isPwdValid = checkPwd(this.password)
      this.isPwdValid = isPwdValid.result
      this.isMatch = this.confirmPassword === this.password
      if (this.isPwdValid) {
        this.passwordErr = ''
        if (this.confirmPassword && !this.isMatch) {
          this.confirmPasswordErr = this.$t('pwdErr.notMatch')
        } else {
          this.confirmPasswordErr = ''
        }
      } else {
        this.passwordErr = this.$t(`pwdErr.${ isPwdValid.reason }`)
        this.confirmPasswordErr = ''
      }
    },
    checkConfirmPwdValid () {
      let isConfirmPwdValid = checkPwd(this.confirmPassword)
      this.isConfirmPwdValid = isConfirmPwdValid.result
      this.isMatch = this.confirmPassword === this.password
      if (this.isPwdValid && !this.isMatch) {
        this.confirmPasswordErr = this.$t('pwdErr.notMatch')
      } else {
        this.confirmPasswordErr = ''
      }
    },
    save () {
      if (!this.isValid) {
        return
      }
      this.pwdErrorInfo = null
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: () => {
            this.$router.back()
          },
          fail: error => {
            switch (error.code) {
              case RetCode.ERROR_PARENT_OLD_PWD_INCORRECT:
              case RetCode.ERROR_OLD_PWD_INCORRECT:
                this.pwdErrorInfo = {
                  code: error.code,
                  message: error.message
                }
                return true
              default:
                return false
            }
          }
        })
      }
      this.$httpPost.request({
        token: this.user.token,
        oldPwd: this.$md5(this.oldPassword),
        newPwd: this.$md5(this.password)
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
