let percentCircle = function (element, option) {
  let defaultOption = {
    colorArr: ['#4ebafc', '#4a85f5'],  //
    lineWidth: 20,   //
    colorBg: '#e6eaed', // #ffffff
    r: 100,
    startPoint: Math.PI * 0.7, //  Math.PI*0.3
    targetPercent: 0,  //
    point: {             //
      show: false,       //
      color: '#ffffff'     //
    }
  }
  Object.assign(defaultOption, option)
  this.options = defaultOption
  let c = element
  this.ctx = c.getContext('2d')
  this.currentPercent = 0
  this.targetPercent = this.options.targetPercent
  this.exec = false

  //let Cwidth = 370, Cheight=260
  let cwidth = this.options.r + this.options.lineWidth
  let cheight = this.options.r + this.options.lineWidth
  c.width = cwidth * 2
  c.height = cheight * 2

  //let that = this
  c.style.width = cwidth + 'px'
  c.style.height = cheight + 'px'

  this.width = c.width
  this.height = c.height

  this.draw()
}

//
function insert (str) {
  return str.slice(0, 2) + ',' + str.slice(2, 4) + ',' + str.slice(4, 6)
}

function convert (color) {
  let color1Arr = color.substring(1, color.length)

  let newColor1 = insert(color1Arr).split(',')
  let newColor1_new = []

  newColor1.forEach(function (item) {
    newColor1_new.push(parseInt(item, 16))
  })

  return newColor1_new
}

function pickHex (color1, color2) {
  let newColor1 = convert(color1)
  let newColor2 = convert(color2)

  let newAll = []
  for (let i = 0; i < newColor1.length; i++) {
    if (newColor1[i] + newColor2[i]) {
      newAll.push(parseInt((newColor1[i]+newColor2[i])/2))
    } else {
      newAll.push(0)
    }
  }
  let newColorAll = []
  newAll.forEach(function (item) {
    if (item < 10) {
      item = '0' + item
    }
    newColorAll.push(item.toString(16))
  })

  return '#' + newColorAll.join('')
}

percentCircle.prototype.draw = function () {
  let that = this
  this.ctx.clearRect(0, 0, this.width, this.height)

  let r = this.options.r
  //let endPoint = Math.PI-this.options.startPoint
  this.ctx.lineWidth = this.options.lineWidth

  //
  this.ctx.save()
  this.ctx.beginPath()
  this.ctx.lineCap = 'round'
  this.ctx.strokeStyle = this.options.colorBg
  this.ctx.arc(this.width / 2, r + this.options.lineWidth, r, this.options.startPoint, Math.PI-this.options.startPoint, false)
  this.ctx.stroke()
  this.ctx.restore()

  if (this.currentPercent > 0) {
    //console.log(that.options.colorArr[1])
    let minColor = pickHex(this.options.colorArr[0], this.options.colorArr[1])
    //
    this.ctx.save()
    let my_gradient = this.ctx.createLinearGradient(0, this.height, 0, this.height / 2 + 30 - r)
    my_gradient.addColorStop(0, this.options.colorArr[0])
    my_gradient.addColorStop(1, minColor)
    this.ctx.strokeStyle = my_gradient
    this.ctx.lineCap = 'round'
    let leftCircle = -0.5
    if (this.currentPercent < 0.5) {
      leftCircle = (2- (this.options.startPoint / Math.PI - 0.5) *2) * this.currentPercent - (2 - this.options.startPoint / Math.PI)
    }

    this.ctx.beginPath()
    this.ctx.arc(this.width / 2, r + this.options.lineWidth, r, this.options.startPoint, leftCircle * Math.PI, false)
    this.ctx.stroke()
    this.ctx.restore()

    //
    if (this.currentPercent > 0.5) {
      //console.log(this.currentPercent)
      let my_gradient2 = this.ctx.createLinearGradient(this.width / 2, this.height, this.width / 2, this.height / 2 + 30 - r)
      my_gradient2.addColorStop(0, minColor)
      my_gradient2.addColorStop(1, this.options.colorArr[1])
      this.ctx.save()
      this.ctx.strokeStyle = my_gradient2
      this.ctx.lineCap = 'round'
      let rightCircle = (this.currentPercent - 0.5) * (2 - (this.options.startPoint / Math.PI - 0.5) * 2) - 0.5
      this.ctx.beginPath()
      this.ctx.arc(this.width / 2, r + this.options.lineWidth, r, Math.PI * -0.5, rightCircle * Math.PI, false)
      this.ctx.stroke()
      this.ctx.restore()
    }

    if (this.options.point.show) {
      let endPoint = (2 - (this.options.startPoint / Math.PI - 0.5) * 2) * this.currentPercent - (2 - this.options.startPoint / Math.PI)
      let endPointX = this.width / 2 + (this.width / 2 - this.options.lineWidth) * Math.cos(endPoint * Math.PI)
      let endPointY = this.height / 2 + (this.width / 2 - this.options.lineWidth) * Math.sin(endPoint * Math.PI)
      this.ctx.save()
      this.ctx.fillStyle = that.options.point.color
      this.ctx.beginPath()
      this.ctx.arc(endPointX, endPointY, 10, 0, Math.PI * 2, true)
      this.ctx.fill()
      this.ctx.restore()
    }
  }
}

percentCircle.prototype.execAnimation = function () {
  if (!this.exec) {
    this.exec = true
    this.timer = setInterval(() => {
      if (this.currentPercent < this.targetPercent) {
        this.currentPercent += 0.02
        if (this.currentPercent > this.targetPercent) {
          this.currentPercent = this.targetPercent
        }
        this.draw()
      } else if (this.currentPercent > this.targetPercent) {
        this.currentPercent -= 0.02
        if (this.currentPercent < this.targetPercent) {
          this.currentPercent = this.targetPercent
        }
        this.draw()
      }
    }, 1000 / 90)
  }
}

percentCircle.prototype.stopAnimation = function () {
  if (this.exec) {
    this.exec = false
    clearInterval(this.timer)
  }
}

export default percentCircle
