<template>
  <div class="p-pre-modify-mobile is-form c-modify">
    <div class="p-pre-modify-mobile__nav">
      <div class="o-fixed-tab" :class="{ 'is-active': isPhoneMode }" @click="changeMode(0)">{{ $t('tab.phone') }}</div>
      <div class="o-fixed-tab" :class="{ 'is-active': isEmailMode }" @click="changeMode(1)">{{ $t('tab.email') }}</div>
    </div>
    <form-field v-if="isPhoneMode" type="number" class="c-form-field--select" :label="$t('mobile.oldPhoneNumber')" v-model="mobile">
      <area-code :areaCode="areaCode" @change="pick"></area-code>
    </form-field>
    <form-field v-else class="c-form-field" :label="$t('mobile.oldEmail')" :placeholder="$t('mobile.oldEmail')" v-model="email">
    </form-field>
    <form-field class="c-form-field--button" :label="$t('sms.vCode')" :error="vCodeError" v-model="vCode">
      <sms :mobile="mobile" :areaCode="areaCode" :email="email" :mode="mode"></sms>
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="next">{{ $t('common.next') }}</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import * as RetCode from '@/code/return-code.js'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'
import sms from '@/widgets/Sms.vue'

export default {
  name: 'pre-modify-mobile',
  components: {
    formField,
    areaCode,
    sms
  },
  data () {
    return {
      mode: 0,
      areaCode: '',
      mobile: '',
      email: '',
      vCode: '',
      vCodeErrorInfo: null
    }
  },
  computed: {
    ...mapState(['user']),
    url () {
      switch (this.mode) {
        case 0:
          return 'staff/mobile/checkvcode'
        case 1:
          return 'staff/email/checkvcode'
        default:
          return ''
      }
    },
    isPhoneMode () {
      return this.mode === 0
    },
    isEmailMode () {
      return this.mode === 1
    },
    mobileVal () {
      return this.areaCode && this.mobile ? this.areaCode + this.mobile : null
    },
    account () {
      return this.isPhoneMode ? this.mobileVal : this.email
    },
    isValid () {
      return this.account && this.vCode
    },
    vCodeError () {
      return this.vCodeErrorInfo ? this.$lang.code[this.vCodeErrorInfo.code] || this.vCodeErrorInfo.message : ''
    }
  },
  methods: {
    clearError () {
      this.vCodeErrorInfo = null
    },
    changeMode (mode) {
      if (this.mode !== mode) {
        this.clearError()
        this.vCode = ''
        this.mode = mode
        if (this.$httpPost) {
          this.$httpPost.setUrl(this.url)
        }
      }
    },
    pick (areaCode) {
      this.areaCode = areaCode
    },
    next () {
      if (this.isValid) {
        this.clearError()
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: () => {
              this.$router.replace({ name: 'modify-mobile' })
            },
            fail: error => {
              switch (error.code) {
                case RetCode.ERROR_VCODE_UNSET:
                case RetCode.ERROR_VCODE_INCORRECT:
                  this.vCodeErrorInfo = {
                    code: error.code,
                    message: error.message
                  }
                  return true
                default:
                  return false
              }
            }
          })
        }
        this.$httpPost.request(this.isPhoneMode ? {
          token: this.user.token,
          mobile: this.mobileVal,
          vCode: this.vCode
        } : {
          token: this.user.token,
          email: this.email,
          vCode: this.vCode
        })
      }
    }
  },
  created () {
    // this.areaCode = this.user.areaCode
    // this.mobile = this.user.mobilePhone
    // this.email = this.user.email
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

<style lang="scss">
.p-pre-modify-mobile {
  &__nav {
    margin-bottom: 40px;
    text-align: center;
    .o-fixed-tab + .o-fixed-tab {
      margin-left: 115px;
    }
  }
}
</style>
