<template>
  <div class="c-picker" :class="{ 'is-still': isStill }" @click="cancel">
    <div class="c-picker__wrapper" @click.stop>
      <div class="c-picker__choose">
        <span class="c-picker__cancel" @click="cancel">{{ cancelTxt }}</span>
        <span class="c-picker__confirm" @click="confirm">{{ confirmTxt }}</span>
        <div class="c-picker__title" @click.stop>{{ title }}</div>
      </div>
      <div class="c-picker__wheel_wrapper" :style="getWrapperStyle" ref="wrapper">
        <ul class="c-picker__wheel" :style="getWheelStyle" ref="wheel">
          <slot>
            <li class="c-picker__item" v-for="(item, cursor) in scrollValues" :key="cursor" :class="{ 'is-active': selectedIndex === cursor }">
              {{ item.value }}
            </li>
          </slot>
        </ul>
        <div class="c-picker__divider" :style="getDividerStyle"></div>
        <div class="c-picker__cover" :style="getCoverStyle"></div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import VueI18n from '@/locale/index.js'

export default {
  name: 'picker',
  props: {
    values: {
      type: Array,
      require: true,
      default () {
        return []
      }
    },
    index: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default () {
        return VueI18n.t('picker.title')
      }
    },
    cancelTxt: {
      type: String,
      default () {
        return VueI18n.t('common.cancel')
      }
    },
    confirmTxt: {
      type: String,
      default () {
        return VueI18n.t('common.done')
      }
    },
    acceleration: {
      type: Number,
      default: -0.003
    },
    radius: {
      type: Number,
      default: 100
    },
    lineHeight: {
      type: Number,
      default: 34
    },
    cycle: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      isStill: true,
      selectedIndex: 0
    }
  },
  computed: {
    length () {
      return this.values.length
    },
    scrollValues () {
      return this.values.map((val, index) => {
        return {
          value: val,
          index
        }
      })
    },
    getWrapperStyle () {
      return {
        height: `${ 2 * this.radius }px`,
      }
    },
    getWheelStyle () {
      return {
        marginTop: `${ this.radius - Math.round(this.lineHeight / 2) }px`
      }
    },
    getCoverStyle () {
      return {
        backgroundSize: `100% ${ this.radius - Math.round(this.lineHeight / 2) }px`,
      }
    },
    getDividerStyle () {
      return {
        top: `${ this.radius - Math.round(this.lineHeight / 2) }px`,
        height: `${ this.lineHeight - 2 }px`,
      }
    }
  },
  methods: {
    initWheel () {
      if (this.length > 0) {
        this.$wheel = new BScroll(this.$refs.wrapper, {
          wheel: {
            selectedIndex: 0,
            rotate: 30,
            /**  */
            wheelWrapperClass: 'c-picker__wheel',
            wheelItemClass: 'c-picker__item'
          },
          swipeTime: 500,
          probeType: 3
        })
        this.$wheel.wheelTo(this.index)
        this.$wheel.on('scrollStart', () => {
          this.isStill = false
        })
        this.$wheel.on('scrollEnd', () => {
          this.isStill = true
          this.selectedIndex = this.$wheel.getSelectedIndex()
          this.$emit('change', this.selectedIndex)
        })
      }
    },
    cancel () {
      this.$emit('cancel')
    },
    confirm () {
      this.$emit('confirm', this.$wheel.getSelectedIndex())
    }
  },
  watch: {
    values () {
      this.$nextTick(() => {
        if (this.$wheel) {
          this.$wheel.refresh()
        } else {
          this.initWheel()
        }
      })
    },
    index () {
      this.$nextTick(() => {
        if (this.$wheel) {
          this.$wheel.refresh()
          this.$wheel.wheelTo(this.index)
        }
      })
    }
  },
  mounted () {
    this.initWheel()
  },
  beforeDestory () {
    if (this.$wheel) {
      this.$wheel.destroy()
    }
  }
}
</script>

<style lang="scss">
.c-picker {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  text-align: center;
  background-color: rgba(#000000, 0.4);
  z-index: 99;
  &__wrapper {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #ffffff;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  &__choose {
    position: relative;
    height: 44px;
    background-color: #f6f6f6;
  }
  &__title {
    text-align: center;
    line-height: 42px;
    color: #000000;
    font-size: 16px;
    font-weight: bold;
  }
  &__cancel {
    position: absolute;
    top: 0;
    left: 0;
    padding: 0 8px;
    line-height: 42px;
    color: #157efb;
    font-size: 14px;
  }
  &__confirm {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    padding: 0 8px;
    line-height: 42px;
    color: #157efb;
    font-size: 14px;
  }
  &__wheel_wrapper {
    position: relative;
    width: 100%;
    overflow: hidden;
  }
  &__wheel {
    position: absolute;
    width: 100%;
    transform-style: preserve-3d;
  }
  &__item {
    height: 34px;
    line-height: 32px;
    color: #333333;
    font-size: 18px;
  }
  &__cover {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-image: linear-gradient(to top, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.8)), linear-gradient(to bottom, rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.8));
    background-position: top,bottom;
    background-repeat: no-repeat;
  }
  &__divider {
    position: absolute;
    width: 100%;
    left: 0;
    border-top: 1px solid #cccccc;
    border-bottom: 1px solid #cccccc;
  }
  &.is-still {
    .c-picker__item.is-active {
      color: #000000;
      font-size: 20px;
    }
  }
}
</style>
