<template>
  <div class="l-form c-modify">
    <form-field class="c-form-field--select" type="number" :label="$t('mobile.newPhoneNumber')" v-model.trim="mobile">
      <area-code @change="pick"></area-code>
    </form-field>
    <form-field class="c-form-field--select" type="number" :label="$t('common.confirm')" :error="confirmErr" v-model.trim="confirmMobile">
      <area-code @change="confirmPick"></area-code>
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'

export default {
  name: 'modify-mobile',
  components: {
    formField,
    areaCode
  },
  data () {
    return {
      mobile: '',
      areaCode: '',
      confirmMobile: '',
      confirmAreaCode: '',
      confirmErr: ''
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isStaff']),
    url () {
      return this.isStaff ? 'staff/mobile/modify' : 'parent/mobile/modify'
    },
    isValid () {
      return !!(this.mobile && this.confirmMobile && this.areaCode && this.confirmAreaCode)
    }
  },
  methods: {
    ...mapActions(['updateUserInfo']),
    pick (areaCode) {
      this.areaCode = areaCode
    },
    confirmPick (areaCode) {
      this.confirmAreaCode = areaCode
    },
    save () {
      if (this.isValid) {
        if (this.confirmAreaCode !== this.areaCode || this.confirmMobile !== this.mobile) {
          this.confirmErr = this.$t('mobile.notMatch')
          return
        } else {
          this.confirmErr = ''
        }
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.url, {
            opts: {
              lock: true
            },
            success: () => {
              this.updateUserInfo({
                areaCode: this.areaCode,
                mobilePhone: this.mobile
              })
              this.$router.back()
            }
          })
        }
        this.$httpPost.request({
          token: this.user.token,
          mobile: this.areaCode + this.mobile
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

