<template>
  <safe-area-layout class="l-operation p-bluetooth">
    <template slot="topbar">
      <div class="p-bluetooth__back_bg">
        <div class="p-bluetooth__back_wrapper" @click="back">
          <div class="p-bluetooth__back"></div>
        </div>
      </div>
      <div class="l-operation__title">{{ $t('bluetooth.title') }}</div>
    </template>
    <div class="l-operation__header">
      {{ $t('bluetooth.tip') }}
      <div v-if="isIos" class="ios-bluetooth-tip">
        <div>{{ $t('bluetooth.iosUp') }}</div>
        <div>{{ $t('bluetooth.iosDown') }}<span class="ios-bluetooth-tip__button" :class="{ 'is-active': isShow }" @click="toggle">{{ $t('bluetooth.how') }}</span></div>
      </div>
    </div>
    <div class="l-operation__content u-align-c" :class="{ 'p-bluetooth__ios': isIos }">
      <div v-if="isAndroid" class="p-bluetooth__img"></div>
      <div v-if="showPic">
        <div>
          <div class="p-bluetooth__img--tip" :class="{ 'is-cn': isCN }"></div>
        </div>
        <div>
          <div class="p-bluetooth__img"></div>
        </div>
      </div>
    </div>
    <div v-if="isAndroid" slot="footer" class="l-operation__footer">
      <div class="l-operation__button o-border-button o-button--cancel" @click="back">{{ $t('common.cancel') }}</div>
      <div class="l-operation__button o-button t-adapt-button" @click="open">{{ $t('common.open') }}</div>
    </div>
  </safe-area-layout>
</template>

<script>
export default {
  name: 'wifi',
  data () {
    return {
      os: null,
      isShow: false
    }
  },
  computed: {
    isAndroid () {
      return this.os === 'android'
    },
    isIos () {
      return this.os === 'ios'
    },
    showPic () {
      return this.isIos && this.isShow
    },
    isCN () {
      return this.$i18n.locale !== 'en'
    }
  },
  methods: {
    toggle () {
      this.isShow = !this.isShow
    },
    back () {
      this.$router.back()
    },
    open () {
      this.isAndroid && this.$native.openSysSetting()
    }
  },
  created () {
    this.$native.getNativeOS().then(os => {
      this.os = os
    })
  }
}
</script>

<style lang="scss">
.p-bluetooth {
  &__back_bg {
    position: relative;
    height: 60px;
  }
  &__back_wrapper {
    position: absolute;
    top: 0;
    left: -20px;
    padding: 10px;
  }
  &__back {
    display: inline-block;
    @include bg-img-ratio('../../assets/back', 20, 36);
  }
  &__img {
    display: inline-block;
    @include bg-img-ratio('../../assets/bluetooth', 222, 408);
  }
  &__img--tip {
    display: inline-block;
    @include bg-img-ratio('../../assets/blueth_en', 514, 307);
    &.is-cn {
      @include bg-img-ratio-normal('../../assets/bluetooth_cn');
    }
  }
  &.l-operation {
    padding-top: 0;
  }
  .l-operation__header {
    padding-bottom: 20px;
  }
}
.ios-bluetooth-tip {
  margin-top: 15px;
  text-align: left;
  color: rgba(#000, 0.4);
  font-size: 14px;
  line-height: 18px;
  &__button {
    position: relative;
    color: #4979f3;
    font-size: 14px;
    text-decoration: underline;
    padding-left: 16px;
    &::after {
      content: '';
      position: absolute;
      top: 6px;
      right: -14px;
      width: 0;
      height: 0;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: 6px solid #4979f3;
      border-top: none;
    }
    &.is-active::after {
      border-top: 6px solid #4979f3;
      border-bottom: none;
    }
  }
}
</style>
