<template>
  <div class="l-form c-modify">
    <div class="l-form__title">{{ $t('setPwd.title') }}</div>
    <form-field class="c-form-field--pwd" :class="{ 'is-valid': isPwdValid }" :label="$t('common.pwd')" :tip="$t('setPwd.tip')" :error="passwordErr" @blur="checkPwdValid" v-model="password">
    </form-field>
    <form-field class="c-form-field--pwd" :class="{ 'is-valid': isConfirmPwdValid }" :label="$t('common.confirm')" :error="confirmPasswordErr" @blur="checkConfirmPwdValid" v-model="confirmPassword">
    </form-field>
    <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
  </div>
</template>

<script>
import formField from '@/components/FormField.vue'
import { checkPwd } from '@/utils/validate.js'

export default {
  name: 'reset-pwd',
  components: {
    formField
  },
  data () {
    return {
      password: '',
      confirmPassword: '',
      passwordErr: '',
      confirmPasswordErr: '',
      isPwdValid: false,
      isConfirmPwdValid: false,
      isMatch: false
    }
  },
  computed: {
    mode () {
      return this.$route.params.mode
    },
    isStaff () {
      return this.mode === 'staff'
    },
    url () {
      return this.isStaff ? 'staff/password/reset' : 'parent/password/modify'
    },
    token () {
      return this.$route.params.token
    },
    isValid () {
      return this.isPwdValid && this.isConfirmPwdValid && this.isMatch
    }
  },
  methods: {
    checkPwdValid () {
      let isPwdValid = checkPwd(this.password)
      this.isPwdValid = isPwdValid.result
      this.isMatch = this.confirmPassword === this.password
      if (this.isPwdValid) {
        this.passwordErr = ''
        if (this.confirmPassword && !this.isMatch) {
          this.confirmPasswordErr = this.$t('pwdErr.notMatch')
        } else {
          this.confirmPasswordErr = ''
        }
      } else {
        this.passwordErr = this.$t(`pwdErr.${ isPwdValid.reason }`)
        this.confirmPasswordErr = ''
      }
    },
    checkConfirmPwdValid () {
      let isConfirmPwdValid = checkPwd(this.confirmPassword)
      this.isConfirmPwdValid = isConfirmPwdValid.result
      this.isMatch = this.confirmPassword === this.password
      if (this.isPwdValid && !this.isMatch) {
        this.confirmPasswordErr = this.$t('pwdErr.notMatch')
      } else {
        this.confirmPasswordErr = ''
      }
    },
    save () {
      if (!this.isValid) {
        return
      }
      if (!this.$httpPost) {
        this.$httpPost = this.$http.createPost(this.url, {
          opts: {
            lock: true
          },
          success: () => {
            this.$router.replace({ name: 'login', params: { mode: this.mode } })
          }
        })
      }
      this.$httpPost.request({
        token: this.token,
        password: this.$md5(this.password)
      })
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
