<template>
  <div class="c-character-intro" :class="{ 'c-character-intro--smaller': smaller }">
    <div class="c-character-intro__avatar" :class="{ 'c-avatar--smaller': smaller, 'c-avatar': !smaller, 'has-avatar': !!person && !!person.headUrl }">
      <div v-if="!!person && !!person.headUrl" class="c-avatar__icon">
        <i :style="{ backgroundImage: `url(${ person.headUrl })` }"></i>
      </div>
    </div>
    <div class="c-character-intro__content">
      <div v-if="person">
        <div class="c-character-intro__row c-character-intro__wrapper"><span v-overflow class="c-character-intro__name">{{ person.name }}</span></div>
        <div v-if="person.info" class="c-character-intro__row c-character-intro__info">{{ person.info }}</div>
        <div v-if="person.tag" class="c-character-intro__row c-character-intro__tag">{{ person.tag }}</div>
      </div>
    </div>
    <div class="c-character-intro__extend">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'character-intro',
  props: {
    person: {
      type: Object
    },
    smaller: {
      type: Boolean,
      default: true
    }
  }
}
</script>

<style lang="scss">
// avatar   big 85  smaller 70
.c-character-intro {
  min-height: 100px;
  display: flex;
  &__avatar, &__extend {
    flex: none;
  }
  &__content {
    flex: 1;
    overflow: hidden;
  }
  &__row {
    & + & {
      margin-top: 12px;
    }
  }
  &__wrapper {
    padding-top: 6px;
    white-space: nowrap;
    overflow: hidden;
  }
  &__name {
    display: inline-block;
    color: #fff;
    font-size: 24px;
    &.is-overflow {
      font-size: 18px;
    }
  }
  &__info {
    color: rgba(#fff, 0.7);
    font-size: 12px;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
  }
  &__tag {
    display: inline-block;
    padding: 4px 14px;
    color: #fff;
    font-size: 14px;
    line-height: 1;
    border-radius: 9px;
    background-color: rgba(#fff, 0.3);
  }
  &--black {
    .c-character-intro__name, .c-character-intro__tag {
      color: #333;
    }
    .c-character-intro__info {
      color: rgba(#333, 0.4);
    }
    .c-character-intro__tag {
      background-color: #eaeaea;
    }
  }
}
</style>
