<template>
  <navigation-wrapper class="c-modify" :title="$t('account.bus')">
    <div class="l-navigation__content l-form">
      <form-field type="number" :placeholder="$t('account.bus')" :tip="$t('bus.tip')" v-model.trim="busId">
      </form-field>
      <div class="o-button o-button--submit t-adapt-button--active c-modify__submit" :class="{ 'is-active': isValid }" @click="save">{{ $t('common.save') }}</div>
    </div>
  </navigation-wrapper>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'
import formField from '@/components/FormField.vue'

export default {
  name: 'modify-bus',
  components: {
    formField
  },
  data () {
    return {
      busId: ''
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['getFigure']),
    id () {
      return Number(this.$route.params.id)
    },
    figure () {
      return this.getFigure(this.id)
    },
    isValid () {
      return !!this.busId
    }
  },
  methods: {
    ...mapActions(['updateFigureInfo']),
    save () {
      if (this.isValid) {
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost('parent/child/modifyBSSId', {
            opts: {
              lock: true
            },
            success: (res, data) => {
              this.updateFigureInfo({
                id: this.id,
                info: {
                  busstudentId: data.BSSId
                }
              })
              this.$router.back()
            }
          })
        }
        this.$httpPost.request({
          token: this.user.token,
          idStudent: this.id,
          BSSId: this.busId
        })
      }
    }
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
