<template>
  <attendance-statistics :title="$t('attendance.student')" :result="result" :attendances="attendances" @choose="toClassAttendance">
  </attendance-statistics>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import { formatToYYYYMMDD } from '@/utils/format.js'
import attendanceStatistics from '@/views/components/AttendanceStatistics.vue'

export default {
  name: 'attendance-classes',
  props: ['date'],
  components: {
    attendanceStatistics
  },
  data () {
    return {
      result: {
        arrived: 0,
        total: 0
      },
      attendances: []
    }
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isAdmin']),
    gradeId () {
      return Number(this.$route.params.gradeId)
    }
  },
  methods: {
    analysis (data) {
      let result = {
        arrived: data.arrivedStudentGradeTotal,
        total: data.studentGradeTotal
      }
      this.attendances = data.classStudentStat.map(val => {
        val.label = val.classId
        val.arrived = val.arrivedStudentClassTotal
        val.total = val.studentClassTotal
        val.precent = val.total > 0 ? val.arrived / val.total * 100 : 0
        val.proportion = val.total > 0 && result.total > 0 ? val.total / result.total * 100 : 0
        return val
      })
      this.result = result
    },
    toClassAttendance (attendance) {
      this.$router.push({ name: 'attendance-details', params: { mode: 'student', id: attendance.idClass, date: this.date } })
    },
    fetch () {
      if (this.isAdmin) {
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost('admin/student/class/attendance', {
            success: res => this.analysis(res.data)
          })
        }
        this.$httpPost.force({
          token: this.user.token,
          idGrade: this.gradeId,
          queryDate: this.date
        }, {
          cycle: formatToYYYYMMDD(new Date()) === this.date ? 60000 : 0
        })
      }
    },
    reset () {
      this.result = {
        arrived: 0,
        total: 0
      }
      this.attendances = []
      this.fetch()
    }
  },
  watch: {
    date () {
      this.reset()
    }
  },
  created () {
    this.fetch()
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>
